import { JobType } from "../../../../../../shared/src/job/job-types"

import { ClientJob } from "../../job"
import { MapLayer } from "src/app/geo-web/map/map-layer/map-layer"

import { testFormRootModel } from "./form-model"
import { D3ColourScheme } from "src/app/geo-web/map/colour-scheme/colour-scheme"
import { RasterMultiLayer } from "src/app/geo-web/map/map-layer/map-geotiff-layer"
import { pad0s, toFixedDrop0s } from "../../../../../../shared/src/util/string"
import { months } from "../../../../../../shared/src/util/dates"

import { mean } from "geoblaze"
import { EditableFeatureCollectionLayer } from "src/app/geo-web/map/map-layer/map-editable-feature-layer"
import { toMercator, Feature, AllGeoJSON, FeatureCollection } from "@turf/turf"
import { flattenArray } from "../../../../../../shared/src/util/array"

import { generate as c3Generate } from "c3"
import { Subscription } from "rxjs"
import { format } from "d3"
import { absSaVectorTileLayers } from "../shared/abs-sa-vector-tile-layers"

export class ClimateTempJob extends ClientJob {
  type: JobType = JobType.CLIMATE_TEMP
  public clientOnly = true

  public inputFormModel = testFormRootModel

  private previousResultsKey: string
  private resultsCache: Map<string, any[]> = new Map()

  private templayer: RasterMultiLayer
  private temperatureInputLayer: EditableFeatureCollectionLayer
  private tempInputLayerSubscription: Subscription

  async runTemperatureProcess(feature: AllGeoJSON) {
    if (typeof feature === "undefined") {
      return
    }
    const resultsKey = `${this.templayer.dimensions.rcp.selected.value}_${
      this.templayer.dimensions.temp.selected.value
    }`

    if (this.previousResultsKey === resultsKey) {
      return
    }

    this.previousResultsKey = resultsKey

    if (!this.resultsCache.has(resultsKey)) {
      this.resultsCache.set(
        resultsKey,
        await Promise.all(
          flattenArray(
            // For each year
            this.templayer.dimensions.year.options.map(year =>
              // For each month
              this.templayer.dimensions.month.options.map(async month => {
                // Get mean value for each temperature theshold layer
                const geotiff = await this.layerService.getGeotiffData(
                  `https://storage.googleapis.com/geostack-data/spark/Public/aus-temp-thresholds/${
                    this.templayer.dimensions.rcp.selected.value
                  }_${this.templayer.dimensions.temp.selected.value}_${
                    year.value
                  }${month.value}01-3857.geotiff`,
                  `${this.templayer.dimensions.rcp.selected.value} ${
                    this.templayer.dimensions.temp.selected.value
                  } ${year.value}-${month.value}`
                )
                const meanMonth = mean(geotiff, toMercator(feature))

                return meanMonth
              })
            )
          )
        )
      )
    }

    const results = this.resultsCache.get(resultsKey)

    const _self = this

    this.layerService.createNewChart({
      chart: c3Generate({
        title: {
          text: `Days above ${
            _self.templayer.dimensions.temp.selected.label
          } C`,
        },
        data: {
          // labels: { format: format(".2f") },
          x: "x",
          columns: [
            [
              "x",
              ...Array(12)
                .fill(0)
                .map((val, index) => index),
            ],
            ["2030", ...results.slice(0, 12)],
            ["2090", ...results.slice(12, 24)],
          ],
        },
        tooltip: {
          format: {
            value: (value, ratio, id) =>
              `${format(".2f")(value as number)} days above ${
                _self.templayer.dimensions.temp.selected.label
              } C`,
          },
        },
        axis: {
          x: {
            label: {
              text: "Month",
              position: "outer-center",
            },
            tick: {
              culling: false,
              format: (num: number) => months[num].substr(0, 3),
            },
            min: 0,
            max: 11,
          },
          y: {
            label: {
              text: `Days`,
              position: "outer-middle",
            },
            min: 0,
            max: 31,
          },
        },
        legend: { position: "inset" },
      }),
    })
  }

  async createInputLayers() {
    this.temperatureInputLayer = new EditableFeatureCollectionLayer(
      this.name,
      "Temperature process inputs"
    )

    this.temperatureInputLayer.hideLayer = true
    this.temperatureInputLayer.singleFeature = true

    // this.temperatureInputLayer.shapeOptions = { color: "#ff0000" }

    this.temperatureInputLayer.toolbarButtonOptions.polygon = {
      title: "Add Temperature polygon",
      primaryFaIcon: ["fas", "shapes"],
    }

    if (typeof this.tempInputLayerSubscription !== "undefined") {
      this.tempInputLayerSubscription.unsubscribe()
      delete this.tempInputLayerSubscription
    }
    this.tempInputLayerSubscription = this.temperatureInputLayer.featureCollectionObservable.subscribe(
      ((fc: FeatureCollection) => {
        this.resultsCache.clear()
        delete this.previousResultsKey
        if (fc.features.length === 0) {
          this.layerService.removeChart()
        } else if (typeof this.templayer !== "undefined") {
          this.runTemperatureProcess(fc)
        }
      }).bind(this)
    )

    const tempLayerOnClear = this.temperatureInputLayer.onObservable.subscribe(
      on => {
        if (on === "delete") {
          this.tempInputLayerSubscription.unsubscribe()
          tempLayerOnClear.unsubscribe()
        }
      }
    )

    await this.temperatureInputLayer.show()

    const jobInputPath = `https://storage.googleapis.com/geostack-data/spark/Public/aus-temp-thresholds/`

    this.templayer = new RasterMultiLayer(
      "Aus temperature thresholds",
      {
        rcp: {
          key: "rcp",
          label: "Emissions scenario",
          options: [
            {
              value: `rcp45`,
              label: `rcp45`,
            },
            {
              value: `rcp85`,
              label: `rcp85`,
            },
          ],
          selected: { value: "rcp85", label: "rcp85" },
        },
        temp: {
          key: "temp",
          label: "Temperature",
          options: Array(16)
            .fill(0)
            .map((val, index) => {
              return {
                value: `DGr${index + 30}`,
                label: `${index + 30} degrees`,
              }
            }),
          selected: { value: "DGr40", label: "40 degrees" },
        },
        month: {
          key: "month",
          label: "Month",
          options: Array(12)
            .fill(0)
            .map((val, index) => {
              return {
                value: pad0s(index + 1, 2),
                label: months[index],
              }
            }),
          selected: { value: "01", label: months[0] },
        },
        year: {
          label: "Year",
          key: "year",
          options: [
            {
              value: "2030",
              label: "2030",
            },
            {
              value: "2090",
              label: "2090",
            },
          ],
          selected: { value: "2090", label: "2090" },
        },
      },
      dimensions =>
        `${jobInputPath}${dimensions.rcp.selected.value}_${
          dimensions.temp.selected.value
        }_${dimensions.year.selected.value}${
          dimensions.month.selected.value
        }01-3857.geotiff`
    )

    this.templayer.minValue = 0
    this.templayer.maxValue = 31
    this.templayer.colourScheme = new D3ColourScheme("interpolateMagma")
    this.templayer.colourScheme.reversed = true
    this.templayer.legendCells = [0, 5, 10, 15, 20, 25, 30, 31]
    this.templayer.valueFormatFn = d => {
      return d === 1
        ? `${toFixedDrop0s(d, 2)} day`
        : `${toFixedDrop0s(d, 2)} days`
    }

    await this.templayer.show()

    this.templayer.opacity = 80

    this.templayer.onRenderObservable.subscribe(() => {
      const fc = this.temperatureInputLayer.featureCollection
      if (fc.features.length > 0) {
        this.runTemperatureProcess(fc)
      }
    })

    return [
      this.temperatureInputLayer,
      this.templayer,
      ...absSaVectorTileLayers(
        "https://storage.googleapis.com/geostack-data/spark/Public/"
      ).map(sa => {
        sa.onClick = this.temperatureInputLayer
        return sa
      }),
    ]
  }

  async createOutputLayers(): Promise<MapLayer[]> {
    return []
  }

  setBbox() {
    if (typeof this.templayer !== "undefined") {
      this.boundingBox4326 = this.templayer.data.bbox4326
    }
  }
}
