import {
  colorscales as plottyColorscales,
  addColorScale as plottyAddColorScale,
} from "plotty"

import { format as d3Format, scaleSequential, ScaleSequential } from "d3"
import * as d3ColorScheme from "d3-scale-chromatic"
import { Feature } from "@turf/turf"

export abstract class ColourScheme {
  protected _type
  protected _name
  protected _colourLegend

  protected _scale: ScaleSequential<any>
  protected _reversed = false
  get scale() {
    return this._scale
  }

  get reversed() {
    return this._reversed
  }

  set reversed(r: boolean) {
    this._reversed = r
  }

  constructor(name: string) {
    this._name = name
  }

  get type() {
    return this._type
  }

  get name() {
    return this._name
  }

  public setDomain(min: number, max: number) {}

  public static normalise(min: number, max: number) {
    return val => {
      const a = 1 / (max - min)
      return a * val + -(a * min)
    }
  }

  public toD3(features: Feature[]): (d: any) => any {
    return undefined
  }

  public toPlotty(): any {
    return undefined
  }
}

interface PredefinedScheme {
  name: string
}

export class D3ColourScheme extends ColourScheme {
  public static isInstanceOf(obj: any): obj is D3ColourScheme {
    return obj.type === "D3ColourScheme"
  }

  // d3.interpolate
  public predefinedSchemes: PredefinedScheme[] = [
    "interpolateBlues",
    "interpolateBrBG",
    "interpolateBuGn",
    "interpolateBuPu",
    "interpolateCool",
    "interpolateCubehelixDefault",
    "interpolateGnBu",
    "interpolateGreens",
    "interpolateGreys",
    "interpolateInferno",
    "interpolateMagma",
    "interpolateOrRd",
    "interpolateOranges",
    "interpolatePRGn",
    "interpolatePiYG",
    "interpolatePlasma",
    "interpolatePuBu",
    "interpolatePuBuGn",
    "interpolatePuOr",
    "interpolatePuRd",
    "interpolatePurples",
    "interpolateRainbow",
    "interpolateRdBu",
    "interpolateRdGy",
    "interpolateRdPu",
    "interpolateRdYlBu",
    "interpolateRdYlGn",
    "interpolateReds",
    "interpolateSinebow",
    "interpolateSpectral",
    "interpolateTurbo",
    "interpolateViridis",
    "interpolateWarm",
    "interpolateYlGn",
    "interpolateYlGnBu",
    "interpolateYlOrBr",
    "interpolateYlOrRd",
  ].map(key => ({
    name: key,
  }))
  public predefinedScheme: PredefinedScheme

  constructor(
    predefinedName: string = "interpolatePlasma",
    name: string = "D3 Colour Scheme"
  ) {
    super(name)

    this._type = "D3ColourScheme"
    this.predefinedScheme = { name: predefinedName }
  }

  public setDomain(min, max) {
    if (!this.reversed) {
      this._scale = scaleSequential(
        d3ColorScheme[this.predefinedScheme.name]
      ).domain([min, max])
    } else {
      this._scale = scaleSequential(
        d3ColorScheme[this.predefinedScheme.name]
      ).domain([max, min])
    }

    this._colourLegend = undefined
  }

  // Note:  setDomain must be called before toD3
  public toD3() {
    if (typeof this._scale === "undefined") {
      throw "setDomain() must be called before toD3()"
    }
    return d => this._scale(d.properties.value)
  }

  public toMapbox(property: string): mapboxgl.Expression {
    if (typeof this._scale === "undefined") {
      throw "setDomain() must be called before toMapbox()"
    }

    const scheme: mapboxgl.Expression = [
      "interpolate",
      ["linear"],
      ["get", property],
    ]
    const min = this._scale.domain()[0]
    const max = this._scale.domain()[1]
    for (let i = 0; i <= 20; i++) {
      if (this.reversed) {
        // Push value
        scheme.push(max + (min - max) * (i / 20))

        // Push colour
        scheme.push(d3ColorScheme[this.predefinedScheme.name](1 - i / 20))
      } else {
        // Push value
        scheme.push(min + (max - min) * (i / 20))

        // Push colour
        scheme.push(d3ColorScheme[this.predefinedScheme.name](i / 20))
      }
    }

    return scheme
  }

  // Note:  set Domain is NOT required for toPlotty
  public toPlotty() {
    const cols = []
    const pos = []
    for (let i = 0; i <= 20; i++) {
      pos.push(i / 20)
      cols.push(d3ColorScheme[this.predefinedScheme.name](i / 20))
    }
    if (this.reversed) {
      cols.reverse()
    }
    plottyAddColorScale(this.predefinedScheme.name, cols, pos)

    return this.predefinedScheme.name
  }
}

export class SolidColourScheme extends ColourScheme {
  public static isInstanceOf(obj: any): obj is SolidColourScheme {
    return obj.type === "SolidColourScheme"
  }

  public solidColour: string

  constructor(solidColour, name: string = "Solid colour") {
    super(name)

    this._type = "SolidColourScheme"

    this.solidColour = solidColour
  }

  toD3(features: Feature[]) {
    return (d: any) => this.solidColour
  }
}
