import { JobType } from "../../../../../../shared/src/job/job-types"

import { ClientJob } from "../../job"
import { MapLayer } from "src/app/geo-web/map/map-layer/map-layer"

import { testFormRootModel } from "./form-model"
import {
  RasterMultiLayer,
  RasterMultiLayerDimensionsMap,
  RasterExpressionLayer,
} from "src/app/geo-web/map/map-layer/map-geotiff-layer"
import { months } from "../../../../../../shared/src/util/dates"
import { format } from "d3"
import { GPU } from "gpu.js"
import { D3ColourScheme } from "src/app/geo-web/map/colour-scheme/colour-scheme"
import { FileLocation } from "../../../../../../shared/src/file-provider/interfaces"

export class ClimateProjectionsJob extends ClientJob {
  type: JobType = JobType.CLIMATE_PROJ
  public clientOnly = true

  public inputFormModel = testFormRootModel

  async createInputLayers(): Promise<MapLayer[]> {
    const dimensions: RasterMultiLayerDimensionsMap = {
      rcp: {
        label: "Emissions scenario",
        key: "rcp",
        options: [
          {
            value: `rcp45`,
            label: `rcp45`,
          },
          {
            value: `rcp85`,
            label: `rcp85`,
          },
        ],
        selected: { value: "rcp45", label: "rcp45" },
      },
      projection: {
        label: "Climate projection",
        key: "projection",
        options: [
          {
            value: `ACCESS1-0`,
            label: `ACCESS1-0`,
          },
          {
            value: `CanESM2`,
            label: `CanESM2`,
          },
          {
            value: `CESM1-CAM5`,
            label: `CESM1-CAM5`,
          },
          {
            value: `CNRM-CM5`,
            label: `CNRM-CM5`,
          },
          // {
          //   value: `GFDL-ESM2M`,
          //   label: `GFDL-ESM2M`,
          // },
          {
            value: `HadGEM2-CC`,
            label: `HadGEM2-CC`,
          },
          {
            value: `MIROC5`,
            label: `MIROC5`,
          },
          {
            value: `NorESM1-M`,
            label: `NorESM1-M`,
          },
        ],
        selected: { value: "ACCESS1-0", label: "ACCESS1-0" },
      },
      year: {
        label: "Year",
        key: "Year",
        options: Array(94)
          .fill(0)
          .map((val, index) => {
            return {
              value: `${index + 2006}`,
              label: `${index + 2006}`,
            }
          }),
        selected: { value: "2006", label: "2006" },
      },
      month: {
        label: "Month",
        key: "month",
        options: Array(12)
          .fill(0)
          .map((val, index) => {
            return {
              value: months[index].toLowerCase(),
              label: months[index],
            }
          }),
        selected: { value: months[0].toLowerCase(), label: months[0] },
      },
    }

    const climateLayerTasmax = new RasterMultiLayer(
      "Temperature max",
      dimensions,
      dimensions =>
        `https://storage.googleapis.com/geostack-data/spark/Public/climate-data/output/tasmax_Amon_${
          dimensions.projection.selected.value
        }_${
          dimensions.rcp.selected.value
        }_r1i1p1_2006-209911-seasavg_native.nc_tasmax_${
          dimensions.month.selected.value
        }_${dimensions.year.selected.value}-3857.geotiff`
    )

    climateLayerTasmax.valueFormatFn = (kelvin: number) =>
      format(".2f")(kelvin - 273.15)

    climateLayerTasmax.valueTransformation = {
      func: (kelvin: number) => kelvin - 273.15,
      inverse: (celsius: number) => celsius + 273.15,
    }

    const climateLayerTasmin = new RasterMultiLayer(
      "Temperature min",
      dimensions,
      dimensions =>
        `https://storage.googleapis.com/geostack-data/spark/Public/climate-data/output/tasmin_Amon_${
          dimensions.projection.selected.value
        }_${
          dimensions.rcp.selected.value
        }_r1i1p1_2006-209911-seasavg_native.nc_tasmin_${
          dimensions.month.selected.value
        }_${dimensions.year.selected.value}-3857.geotiff`
    )

    climateLayerTasmin.valueFormatFn = (kelvin: number) =>
      format(".2f")(kelvin - 273.15)

    climateLayerTasmin.valueTransformation = {
      func: (kelvin: number) => kelvin - 273.15,
      inverse: (celsius: number) => celsius + 273.15,
    }

    const climateLayerHum = new RasterMultiLayer(
      "Relative humidity",
      dimensions,
      dimensions =>
        `https://storage.googleapis.com/geostack-data/spark/Public/climate-data/output/hurs_Amon_${
          dimensions.projection.selected.value
        }_${
          dimensions.rcp.selected.value
        }_r1i1p1_2006-209911-seasavg_native.nc_hurs_${
          dimensions.month.selected.value
        }_${dimensions.year.selected.value}-3857.geotiff`
    )

    const climateLayerPre = new RasterMultiLayer(
      "Precipitation",
      dimensions,
      dimensions =>
        `https://storage.googleapis.com/geostack-data/spark/Public/climate-data/output/pr_Amon_${
          dimensions.projection.selected.value
        }_${
          dimensions.rcp.selected.value
        }_r1i1p1_2006-209911-seassum_native.nc_pr_${
          dimensions.month.selected.value
        }_${dimensions.year.selected.value}-3857.geotiff`
    )

    climateLayerPre.linkedLayers = [
      climateLayerTasmax,
      climateLayerTasmin,
      climateLayerHum,
    ]
    climateLayerTasmax.linkedLayers = [
      climateLayerTasmin,
      climateLayerHum,
      climateLayerPre,
    ]
    climateLayerTasmin.linkedLayers = [
      climateLayerTasmax,
      climateLayerHum,
      climateLayerPre,
    ]
    climateLayerHum.linkedLayers = [
      climateLayerTasmax,
      climateLayerTasmin,
      climateLayerPre,
    ]

    const wetBulbLayer = new RasterExpressionLayer(
      "Wet-bulb temperature max",
      [climateLayerTasmax, climateLayerHum], // From https://journals.ametsoc.org/doi/full/10.1175/JAMC-D-11-0143.1
      function(raster) {
        return (
          (raster[0][this.thread.y][this.thread.x] - 273.15) *
            Math.atan(
              0.151977 *
                Math.sqrt(raster[1][this.thread.y][this.thread.x] + 8.313659)
            ) +
          Math.atan(
            raster[0][this.thread.y][this.thread.x] -
              273.15 +
              raster[1][this.thread.y][this.thread.x]
          ) -
          Math.atan(raster[1][this.thread.y][this.thread.x] - 1.676331) +
          0.00391838 *
            Math.pow(raster[1][this.thread.y][this.thread.x], 3 / 2) *
            Math.atan(0.023101 * raster[1][this.thread.y][this.thread.x]) -
          4.686035
        )
      }
    )

    wetBulbLayer.minValue = 20
    wetBulbLayer.maxValue = 45

    wetBulbLayer.colourScheme = new D3ColourScheme("interpolateTurbo")
    wetBulbLayer.colourScheme.reversed = true

    await wetBulbLayer.show()

    return [
      wetBulbLayer,
      climateLayerTasmax,
      climateLayerTasmin,
      climateLayerPre,
      climateLayerHum,
    ]
  }

  async createOutputLayers(): Promise<MapLayer[]> {
    return []
  }

  setBbox() {}
}
