import { ClientJob } from "./job"
import { ClassMap } from "../../../../shared/src/job/job-base"
import { SparkClientJob } from "./job-types/spark/client-job"
import { JobType } from "../../../../shared/src/job/job-types"
import { Emv2ClientJob } from "./job-types/evac/client-job"
import { ClimateSlrJob } from "./job-types/climate/slr-job"
import { EES } from "./job-types/ees/client-job"
import { ClimateTempJob } from "./job-types/climate/temp-job"
import { ClimateProjectionsJob } from "./job-types/climate/projection-job"
import { SewJob } from "./job-types/risk/sew-job"
import { CoggJob } from "./job-types/risk/cogg-job"
import { NswFireJob } from "./job-types/risk/nsw-fire"
import { SparkWorkspaceClientJob } from "./job-types/spark-workspace/client-job"

// Maps between job types and job class
export const jobClassMap: ClassMap<ClientJob> = type => {
  switch (type) {
    case JobType.SPARK:
      return SparkClientJob
    case JobType.SPARKWORKSPACE:
      return SparkWorkspaceClientJob
    case JobType.EMV2:
      return Emv2ClientJob
    case JobType.CLIMATE_SLR:
      return ClimateSlrJob
    case JobType.CLIMATE_TEMP:
      return ClimateTempJob
    case JobType.CLIMATE_PROJ:
      return ClimateProjectionsJob
    case JobType.SEW:
      return SewJob
    case JobType.COGG:
      return CoggJob
    case JobType.NSWFIRE:
      return NswFireJob
    case JobType.EES:
      return EES
    default:
      throw `INVALID job type: ${type}`
  }
}
