import { JobType } from "../../../../../../shared/src/job/job-types"

import { ClientJob } from "../../job"
import { MapLayer } from "src/app/geo-web/map/map-layer/map-layer"

import { MapboxGlLayer } from "src/app/geo-web/map/map-layer/mapbox-gl-layer"
import { D3ColourScheme } from "src/app/geo-web/map/colour-scheme/colour-scheme"
import {
  RasterMultiLayerDimensionsMap,
  RasterMultiLayer,
  RasterLayer,
} from "src/app/geo-web/map/map-layer/map-geotiff-layer"
import { FileLocation } from "../../../../../../shared/src/file-provider/interfaces"
import { ResponseType } from "src/app/services/rest-api.service"
import { FeatureCollection, bbox, centroid, Feature } from "@turf/turf"
import { MousePoint } from "src/app/geo-web/map/map-layer/layer-service.service"
import {
  DynamicFormGroupModel,
  DynamicInputModel,
} from "@ng-dynamic-forms/core"

export class NswFireJob extends ClientJob {
  public mapboxStyle = "mapbox://styles/mapbox/satellite-streets-v11"

  type: JobType = JobType.NSWFIRE
  public clientOnly = true

  private landParcelsJSON: FeatureCollection

  async createInputLayers(): Promise<MapLayer[]> {
    const fireRaster = new RasterLayer(
      "Fire intensity",
      await this.jobService.resolveDirTree(
        `geostack-data/spark/Data/nsw-fire/Burn_detect_NBR.tif`,
        FileLocation.GCS
      )
    )

    fireRaster.colourScheme = new D3ColourScheme("interpolateYlOrRd")

    fireRaster.minValue = 0
    fireRaster.maxValue = 1

    this.landParcelsJSON = (await this.layerService.getFile(
      await this.jobService.resolveDirTree(
        `geostack-data/spark/Data/nsw-fire/land-parcels-fire-full.geojson`,
        FileLocation.GCS
      ),
      ResponseType.JSON
    )) as FeatureCollection

    const landParcelLayer = new MapboxGlLayer(
      "Land parcels",
      {
        id: "fire-risk",
        source: "fire-risk",
        type: "fill",
      },

      {
        id: "fire-risk",
        source: {
          type: "geojson",
          data: this.landParcelsJSON,
        },
      } as any
    )

    landParcelLayer.filterValuesArray = [
      {
        label: "Mean hazard",
        key: "fire_mean",
        minValue: 0,
        maxValue: 1,
      },
      {
        label: "Max hazard",
        key: "fire_max",
        minValue: 0,
        maxValue: 1,
      },
    ]

    landParcelLayer.colourScheme = new D3ColourScheme("interpolateYlOrRd")
    landParcelLayer.colourByProperty = {
      options: [
        {
          label: "Sum hazard",
          key: "fire_sum",
          minValue: 0,
          maxValue: 60000,
        },
        {
          label: "Mean hazard",
          key: "fire_mean",
          minValue: 0,
          maxValue: 1,
        },
        {
          label: "Max hazard",
          key: "fire_max",
          minValue: 0,
          maxValue: 1,
        },
      ],
      selected: {
        label: "Max hazard",
        key: "fire_max",
        minValue: 0,
        maxValue: 1,
      },
    }

    landParcelLayer.idByProperty = "cadid"
    landParcelLayer.onClickPolicy = "all"
    landParcelLayer.onClick = (point: MousePoint) => {
      const features = landParcelLayer.getFeaturesAtPoint(point)

      if (Array.isArray(features) && typeof features[0] !== "undefined") {
        this.layerService.showPopup({
          title: `Land parcel info`,
          model: features[0],
          coordinates: point.layerPoint,
          formSchema: [
            new DynamicFormGroupModel({
              id: "properties",
              legend: "Properties",
              group: [
                new DynamicInputModel({
                  id: "plannumber",
                  label: "Plan number",
                  readOnly: true,
                }),
                new DynamicInputModel({
                  id: "lotnumber",
                  label: "Lot number",
                  readOnly: true,
                }),
                new DynamicInputModel({
                  id: "controllin",
                  label: "Controlled by",
                  readOnly: true,
                }),

                new DynamicInputModel({
                  id: "fire_mean",
                  label: "Mean fire intensity",
                  readOnly: true,
                }),
                new DynamicInputModel({
                  id: "fire_max",
                  label: "Max fire intensity",
                  readOnly: true,
                }),
                // new DynamicInputModel({
                //   id: "fire_sum",
                //   label: "FSui",
                //   readOnly: true
                // }),
              ],
            }),
          ],
          closeOnMapMove: true,
          background: false,
          hideCancelBtn: true,
          submitBtnLabel: "Close",
        })
      }
    }

    landParcelLayer.opacity = 80

    await landParcelLayer.show()

    return [landParcelLayer, fireRaster]
  }

  async createOutputLayers(): Promise<MapLayer[]> {
    return []
  }

  setBbox() {
    if (typeof this.landParcelsJSON !== "undefined") {
      this.boundingBox4326 = bbox(this.landParcelsJSON)
    }
  }
}
