import {
  FeatureCollection,
  Feature,
  LineString,
  Polygon,
  featureCollection,
  MultiPolygon,
  coordEach,
  Point,
  clone,
} from "@turf/turf"

import * as proj4x from "proj4"

let proj4

// FIXME: this is annoying - probably something to do with angular-cli webpack stuff
if (typeof window === "undefined") {
  proj4 = proj4x
} else {
  proj4 = (proj4x as any).default
}

export const proj4defs = [
  [
    "EPSG:32718",
    "+proj=utm +zone=18 +south +ellps=WGS84 +datum=WGS84 +units=m +no_defs ",
  ],
  [
    "EPSG:32767",
    "+proj=lcc +lat_1=-36 +lat_2=-38 +lat_0=-37 +lon_0=145 +x_0=2500000 +y_0=2500000 +ellps=GRS80 +towgs84=0,0,0,0,0,0,0 +units=m +no_defs ",
  ],
  [
    "EPSG:3111",
    "+proj=lcc +lat_1=-36 +lat_2=-38 +lat_0=-37 +lon_0=145 +x_0=2500000 +y_0=2500000 +ellps=GRS80 +towgs84=0,0,0,0,0,0,0 +units=m +no_defs ",
  ],
  [
    "EPSG:28355",
    "+proj=utm +zone=55 +south +ellps=GRS80 +towgs84=0,0,0,0,0,0,0 +units=m +no_defs ",
  ],
  ["EPSG:4326", "+proj=longlat +datum=WGS84 +no_defs"],
]

proj4.defs(proj4defs)

export const EPSG4326 = "EPSG:4326"

export const EPSG3857 = "EPSG:3857"

export function projectBBox(
  bbox: number[],
  fromProjection = EPSG3857,
  toProjection = EPSG4326
): number[] {
  return [
    ...projectCoords([bbox[0], bbox[1]], fromProjection, toProjection),
    ...projectCoords([bbox[2], bbox[3]], fromProjection, toProjection),
  ]
}

export function projectCoords(
  coords: number[],
  fromProjection = EPSG3857,
  toProjection = EPSG4326
): [number, number] {
  return proj4(fromProjection, toProjection).forward(coords) as [number, number]
}

export function projectFeatureCollection(
  fc: FeatureCollection,
  fromProjection = EPSG3857,
  toProjection = EPSG4326,
  inPlace = true
): FeatureCollection {
  let projectedFc
  if (!inPlace) {
    projectedFc = clone(fc)
  } else {
    projectedFc = fc
  }

  projectedFc.features.forEach((f: any, fIdx: number) => {
    if (f.geometry.type === "LineString") {
      const lineStringFeature = f as Feature<LineString>
      lineStringFeature.geometry.coordinates.forEach((coords, cIdx) => {
        lineStringFeature.geometry.coordinates[cIdx] = projectCoords(
          coords,
          fromProjection,
          toProjection
        )
      })
    } else if (f.geometry.type === "Polygon") {
      const polygonFeature = f as Feature<Polygon>
      // Project coordinates
      polygonFeature.geometry.coordinates.forEach((linearRings, lrIdx) => {
        linearRings.forEach((coords, cIdx) => {
          polygonFeature.geometry.coordinates[lrIdx][cIdx] = projectCoords(
            coords,
            fromProjection,
            toProjection
          )
        })
      })
    } else if (f.geometry.type === "MultiPolygon") {
      const multiPolygonFeature = f as Feature<MultiPolygon>
      // Project coordinates
      multiPolygonFeature.geometry.coordinates.forEach((polygon, pIdx) => {
        polygon.forEach((linearRings, lrIdx) => {
          linearRings.forEach((coords, cIdx) => {
            multiPolygonFeature.geometry.coordinates[pIdx][lrIdx][
              cIdx
            ] = projectCoords(coords, fromProjection, toProjection)
          })
        })
      })
    } else if (f.geometry.type === "Point") {
      const pointFeature = f as Feature<Point>
      // Project coordinates
      pointFeature.geometry.coordinates = projectCoords(
        pointFeature.geometry.coordinates,
        fromProjection,
        toProjection
      )
    }
  })

  // coordEach(projectedFc, coord => {
  //   coord = project(coord)
  // })

  // Project bounding box coords
  // ;[projectedFc.bbox[0], projectedFc.bbox[1]] = project([projectedFc.bbox[0], projectedFc.bbox[1]])
  // Why
  // ;[projectedFc.bbox[2], projectedFc.bbox[3]] = project([projectedFc.bbox[2], projectedFc.bbox[3]])

  return projectedFc
}
