import {
  DynamicInputControlModelConfig,
  DynamicInputControlModel,
  serializable,
  DynamicFormControlLayout,
} from "@ng-dynamic-forms/core"
import { NgxMonacoEditorConfig } from "ngx-monaco-editor"

import * as mergeOptions from "../../../../node_modules/merge-options"

export const DYNAMIC_FORM_CONTROL_TYPE_CODE_EDITOR = "CODE_EDITOR"

export const dynamicCodeEditorConfigDefaults = {
  wordWrap: "on",
  language: "cpp",
  fontSize: 12,
  scrollBeyondLastLine: false,
}

export interface DynamicCodeEditorModelConfig
  extends DynamicInputControlModelConfig<string> {
  options?: any
}

/**
 * DynamicInputControlModel for the Monaco code editor
 *
 * @export
 * @class DynamicCodeEditorModel
 */
export class DynamicCodeEditorModel extends DynamicInputControlModel<string> {
  @serializable() options: NgxMonacoEditorConfig

  @serializable() readonly type: string = DYNAMIC_FORM_CONTROL_TYPE_CODE_EDITOR

  constructor(
    config: DynamicCodeEditorModelConfig,
    layout?: DynamicFormControlLayout
  ) {
    super(config, layout)

    this.options = mergeOptions(
      dynamicCodeEditorConfigDefaults,
      config.options || {}
    )

    // This is needed so the monaco assets are loaded from the correct location
    this.options.baseUrl = "./assets"
  }
}
