import {
  FeatureCollection,
  featureCollection as newFeatureCollection,
  Feature,
  centroid,
  distance,
  point,
  within,
  Polygon,
  MultiPolygon,
  pointToLineDistance,
  MultiLineString,
  LineString,
  booleanWithin,
  Point,
} from "@turf/turf"
import { MapLayer } from "./map-layer"
import { SolidColourScheme } from "../colour-scheme/colour-scheme"

import { DynamicFormModel } from "@ng-dynamic-forms/core"

import { downloadFile } from "src/app/util/download-file"
import { BehaviorSubject } from "rxjs"
import { MapboxGlLayer } from "./mapbox-gl-layer"
import { GeoJSONSource } from "mapbox-gl"
import { MousePoint } from "./layer-service.service"
import * as mergeOptions from "merge-options"

export enum FeatureType {
  Point = "point",
  Polygon = "polygon",
  Line = "line",
}

export class EditableFeatureToolbarButton {
  primaryFaIcon: [string, string]
  secondaryFaIcon?: [string, string]
  title: string

  /**
   * If this is left unset, the defaults from EditableFeatureCollectionLayer.createFeature will be used
   */
  createNewFn?: () => void
}

export interface EditableFeatureCollectionLayerSpec {}

export class EditableFeatureCollectionLayer extends MapLayer {
  private _mapboxLayers: MapboxGlLayer[] = []

  protected _rootElement
  public static editInProgress = false
  public static mode: "create" | "edit" = "create"
  public singleFeature = false

  public defaultFeatureProperties: {
    point: any
    line: any
    polygon: any
  } = { point: {}, line: {}, polygon: {} }

  // Form Models for editing feature properties (separate form for Point, Line and Polygon)
  public featurePropertiesFormModel?: {
    point?: DynamicFormModel
    line?: DynamicFormModel
    polygon?: DynamicFormModel
  } = {}

  public toolbarButtonOptions: {
    point?: EditableFeatureToolbarButton
    line?: EditableFeatureToolbarButton
    polygon?: EditableFeatureToolbarButton
  } = {}

  private _featureCollection: FeatureCollection = newFeatureCollection([])
  private _editFeatureCollection: FeatureCollection = newFeatureCollection([])
  get featureCollection() {
    return this._featureCollection
  }

  set featureCollection(fc: FeatureCollection) {
    this._featureCollection = fc
    this.updateFeatureCollection()
  }

  private _featureCollectionBehaviourSubject = new BehaviorSubject<
    FeatureCollection
  >(this._featureCollection)

  get featureCollectionObservable() {
    return this._featureCollectionBehaviourSubject.asObservable()
  }

  get mapboxLayers() {
    return this._mapboxLayers
  }

  private epsg: string

  private _mapboxId: string

  private _colourScheme: SolidColourScheme

  constructor(
    jobName: string,
    name: string,
    fc: FeatureCollection = newFeatureCollection([]),
    colourScheme: SolidColourScheme = new SolidColourScheme("#000000")
  ) {
    super(name)

    this._mapboxId = `fclayer-${jobName}-${name}`
      .replace(" ", "-")
      .toLowerCase()

    this._type = "EditableFeatureCollectionLayer"
    this._opacity = 0.8

    this.onClickPolicy = "all"

    this.blendModes = []

    this._featureCollection = fc

    this.updateFeatureCollection()

    this._colourScheme = colourScheme

    this.downloadData = {
      action: (() => {
        downloadFile(
          `${this.name}.json`,
          JSON.stringify(this._featureCollection),
          "text/json"
        )
      }).bind(this),
      description: `GeoJSON of ${this.name} features`,
      metadata: "",
      disabled: false,
    }

    const fillLayer = new MapboxGlLayer(`${this._mapboxId}-fill`, {
      id: `${this._mapboxId}-fill`,
      type: "fill",
      source: this._mapboxId,
    })
    fillLayer.mapboxFilter = ["==", "$type", "Polygon"]

    fillLayer.colourScheme = this.colourScheme

    const lineLayer = new MapboxGlLayer(`${this._mapboxId}-line`, {
      id: `${this._mapboxId}-line`,
      type: "line",
      source: this._mapboxId,
      paint: { "line-width": 3 },
    })
    lineLayer.mapboxFilter = ["==", "$type", "LineString"]

    lineLayer.colourScheme = this.colourScheme

    const pointLayer = new MapboxGlLayer(`${this._mapboxId}-point`, {
      id: `${this._mapboxId}-point`,
      type: "circle",
      source: this._mapboxId,
      paint: {
        "circle-radius": 10,
      },
    })
    pointLayer.mapboxFilter = ["==", "$type", "Point"]

    pointLayer.colourScheme = this.colourScheme

    this._mapboxLayers = [fillLayer, pointLayer, lineLayer]
    this.linkedLayers = this._mapboxLayers
  }

  public async updateLayer(updateLinked = true) {
    await super.updateLayer(updateLinked)
  }

  public static isInstanceOf(obj: any): obj is EditableFeatureCollectionLayer {
    return obj.type === "EditableFeatureCollectionLayer"
  }

  static enableEditMode() {
    if (!EditableFeatureCollectionLayer.editInProgress) {
      EditableFeatureCollectionLayer.mode = "edit"
      EditableFeatureCollectionLayer.editInProgress = true
    }
  }

  static disableEditMode(
    layers: EditableFeatureCollectionLayer[],
    keepChanges = true
  ) {
    if (
      EditableFeatureCollectionLayer.editInProgress &&
      EditableFeatureCollectionLayer.mode === "edit"
    ) {
      if (keepChanges) {
        layers.forEach(l => {
          l.addFeatures(
            l._editFeatureCollection.features.map(feature => {
              // Only update geometery from mapboxDraw features
              const newFeature = EditableFeatureCollectionLayer.layerService.mapboxDraw.get(
                feature.properties.mapboxDrawId
              ) as Feature

              feature.geometry = newFeature.geometry
              return feature
            })
          )
          l._editFeatureCollection.features = []
        })
      } else {
        layers.forEach(l => {
          l.addFeatures(l._editFeatureCollection.features)
          l._editFeatureCollection.features = []
        })
      }
      MapLayer.layerService.mapboxDraw.deleteAll()
      MapLayer.layerService.mapboxDraw.trash()
      EditableFeatureCollectionLayer.mode = "create"
      EditableFeatureCollectionLayer.editInProgress = false
    }
  }

  get colourScheme() {
    return this._colourScheme
  }

  set colourScheme(c: SolidColourScheme) {
    this._colourScheme = c
    this._mapboxLayers.forEach(l => {
      l.colourScheme = c
    })
  }

  async show() {
    await Promise.all(this._mapboxLayers.map(layer => layer.show()))
    await super.show()
  }

  async hide() {
    await Promise.all(this._mapboxLayers.map(layer => layer.hide()))
    await super.hide()
  }

  async clear() {
    await Promise.all(
      this._mapboxLayers.map(async layer => await layer.clear())
    )
    if (
      typeof MapLayer.layerService.mapboxGl.getSource(this._mapboxId) !==
      "undefined"
    ) {
      MapLayer.layerService.mapboxGl.removeSource(this._mapboxId)
      console.log(`removing source ${this._mapboxId}`)
    }
    await super.clear()
  }

  set opacity(o) {
    super.opacity = o
    this._mapboxLayers.map(layer => (layer.opacity = o))
  }

  get opacity() {
    return super.opacity
  }

  public resolveFeatureProperties(featureProps: any) {
    const resolved = {}
    // If any of the properties are functions -> call the function
    Object.keys(featureProps).forEach(key => {
      if (typeof featureProps[key] === "function") {
        resolved[key] = featureProps[key]()
      } else {
        resolved[key] = featureProps[key]
      }
    })

    return resolved
  }

  public createNewFeature(type: FeatureType) {
    if (EditableFeatureCollectionLayer.editInProgress) {
      return
    }
    EditableFeatureCollectionLayer.editInProgress = true
    switch (type) {
      case FeatureType.Point:
        MapLayer.layerService.mapboxDraw.changeMode("draw_point")
        this.setOnCreateHandler(this.defaultFeatureProperties.point)
        break
      case FeatureType.Line:
        MapLayer.layerService.mapboxDraw.changeMode("draw_line_string")
        this.setOnCreateHandler(this.defaultFeatureProperties.line)
        break
      case FeatureType.Polygon:
        MapLayer.layerService.mapboxDraw.changeMode("draw_polygon")
        this.setOnCreateHandler(this.defaultFeatureProperties.polygon)
        break
    }
  }

  private setOnCreateHandler(defaultFeatureProperties: any) {
    const _self = this

    const removeHandlers = event => {
      // Remove draw created event handler
      MapLayer.layerService.mapboxGl.off("draw.modechange", removeHandlers)
      MapLayer.layerService.mapboxGl.off("draw.create", onCreateHandler)
      MapLayer.layerService.mapboxDraw.trash()
      EditableFeatureCollectionLayer.editInProgress = false
    }

    const onCreateHandler = (event: { features: Array<Feature> }) => {
      // Remove draw created event handler
      removeHandlers(event)

      _self.addFeatures(event.features, defaultFeatureProperties)
    }

    MapLayer.layerService.mapboxGl.on("draw.create", onCreateHandler)
    MapLayer.layerService.mapboxGl.on("draw.modechange", removeHandlers)
  }

  public addFeatures(features: Feature[], deafultFeatureProperties = {}) {
    const _self = this

    if (this.singleFeature) {
      this._featureCollection.features = []
      if (features.length > 1) {
        features = [features.shift()]
      }
    }

    features.forEach(feature => {
      const properties = (feature.properties = feature.properties || {}) // Initialize feature.properties if needed
      Object.assign(
        properties,
        this.resolveFeatureProperties(deafultFeatureProperties)
      )
    })

    this._featureCollection.features.push(...features)

    this.updateFeatureCollection()
  }

  /**
   * Call this method after updating this._featureCollection
   *
   * @private
   */
  private updateFeatureCollection() {
    const source = MapLayer.layerService.mapboxGl.getSource(
      this._mapboxId
    ) as GeoJSONSource
    if (typeof source !== "undefined") {
      source.setData(this._featureCollection as any)
    } else {
      MapLayer.layerService.mapboxGl.addSource(this._mapboxId, {
        type: "geojson",
        data: this._featureCollection,
      } as any)
    }

    this._featureCollectionBehaviourSubject.next(this._featureCollection)
  }

  public editFeature(f: Feature, index: number) {}

  /**
   * Returns array of Toolbar buttons
   */
  public getToolbarButtons(): EditableFeatureToolbarButton[] {
    return Object.keys(this.toolbarButtonOptions).map(featureType => {
      // If createNewFn is not set -> return default createNewFeature function
      if (
        typeof this.toolbarButtonOptions[featureType].createNewFn !== "function"
      ) {
        return Object.assign({}, this.toolbarButtonOptions[featureType], {
          createNewFn: this.createNewFeature.bind(
            this,
            featureType as FeatureType
          ),
        })
      }
      // Otherwise return function set
      return this.toolbarButtonOptions[featureType]
    })
  }

  public onClick = (p: MousePoint) => {
    const mouseTreshold = 20
    const mousePoint = point([p.latlng.lng, p.latlng.lat])

    const foundFeature = this._featureCollection.features.reduce(
      (closestFeature, current, currentIndex) => {
        const withinMouseThreshold =
          MapLayer.layerService.mapboxGl
            .project(centroid(current).geometry.coordinates as [number, number])
            .dist(p.layerPoint) < mouseTreshold

        const distanceToCurrentFeature = distance(centroid(current), mousePoint)

        const distanceToClosestFeature =
          typeof closestFeature.feature === "undefined"
            ? 1000
            : distance(centroid(closestFeature.feature), mousePoint)

        // If current feature is closer to mouse than the closest feature
        if (distanceToCurrentFeature < distanceToClosestFeature) {
          if (
            // If Point and mouse point is within distance threshold
            (current.geometry.type === "Point" && withinMouseThreshold) ||
            // If Line and mouse point is within distance threshold
            ((current.geometry.type === "LineString" ||
              current.geometry.type === "MultiLineString") &&
              pointToLineDistance(mousePoint, current as Feature<
                LineString | MultiLineString
              >) < mouseTreshold &&
              withinMouseThreshold) ||
            // If Polygon and the point is within polygon
            ((current.geometry.type === "Polygon" ||
              current.geometry.type === "MultiPolygon") &&
              booleanWithin(mousePoint, current as Feature<
                Polygon | MultiPolygon
              >))
          ) {
            return { feature: current, index: currentIndex }
          }
        }

        return closestFeature
      },
      { feature: undefined, index: undefined }
    ) as { feature: Feature; index: number }

    if (typeof foundFeature.feature === "undefined") {
      return
    }

    if (EditableFeatureCollectionLayer.mode === "create") {
      this.editFeatureProperties(foundFeature.feature, foundFeature.index)
    } else {
      this.editFeatureGeometry(foundFeature.feature, foundFeature.index)
    }
  }

  private editFeatureGeometry(feature: Feature, index: number) {
    const [mapboxDrawId] = MapLayer.layerService.mapboxDraw.add(
      JSON.parse(JSON.stringify(newFeatureCollection([feature])))
    )

    const oldFeature = this._featureCollection.features.splice(index, 1)[0]
    oldFeature.properties.mapboxDrawId = mapboxDrawId

    this._editFeatureCollection.features.push(oldFeature)

    this.updateFeatureCollection()

    MapLayer.layerService.mapboxDraw.changeMode("simple_select", {
      featureIds: [mapboxDrawId],
    })
  }

  private editFeatureProperties(feature: Feature, index: number) {
    // Get formschema for layer type
    let formSchema: DynamicFormModel
    if (feature.geometry.type === "Point") {
      formSchema = this.featurePropertiesFormModel.point
    } else if (
      feature.geometry.type === "LineString" ||
      feature.geometry.type === "MultiLineString"
    ) {
      formSchema = this.featurePropertiesFormModel.line
    } else if (
      feature.geometry.type === "Polygon" ||
      feature.geometry.type === "MultiPolygon"
    ) {
      formSchema = this.featurePropertiesFormModel.polygon
    }

    if (typeof formSchema !== "undefined") {
      if (EditableFeatureCollectionLayer.editInProgress) {
        return false
      }

      MapLayer.layerService.showPopup({
        title: `Edit ${this.name} feature`,
        model: feature,
        dangerBtn: {
          label: "Delete",
          onClickFn: (() => {
            this._featureCollection.features.splice(index, 1)
            this.updateFeatureCollection()
          }).bind(this),
        },
        coordinates: MapLayer.layerService.mapboxGl.project(centroid(feature)
          .geometry.coordinates as [number, number]),
        formSchema,
        formOnSubmitFn: updatedModel => {
          // If the form has geometry editing -> update coordinates
          if ("geometry" in updatedModel) {
            // Change coordinates to array (as NgDynamicForms will change keys to strings)
            if (!Array.isArray(updatedModel.geometry.coordinates)) {
              updatedModel.geometry.coordinates = Object.values(
                updatedModel.geometry.coordinates
              ).map(coords => parseFloat(coords as string))
            }
            // Check that all coordinates are set
            if (
              typeof updatedModel.geometry.coordinates.find(
                coord => coord === ""
              ) === "undefined"
            ) {
              if (feature.geometry.type === "Point") {
                ;(feature as Feature<Point>).geometry.coordinates =
                  updatedModel.geometry.coordinates
              } else {
                // TODO: add polygon coordinate editing
              }
            }
          }
          // (deep) Merge layer feature with form Model
          this._featureCollection.features[index] = mergeOptions(
            feature,
            updatedModel
          )
          this.updateFeatureCollection()
        },
        closeOnMapMove: true,
        background: true,
      })
    }
  }
}
