import { JobType } from "../../job-types"
import { SparkJobInputs, SparkJobOutputs } from "./inputs-outputs"
import { JobTypeTraits, JobJSON } from "../../job-base"
import { Constructor, Mixin } from "../../../util/mixins"

export function sparkJob<TBase extends Constructor>(Base: TBase) {
  return class SparkJob extends Base implements JobTypeTraits {
    type: JobType.SPARK = JobType.SPARK
    inputs: SparkJobInputs = new SparkJobInputs()
    outputs: SparkJobOutputs = new SparkJobOutputs()
  }
}

export type SparkJob<Base extends JobJSON = JobJSON> = Base &
  Mixin<typeof sparkJob>
