import { Injectable } from "@angular/core"
import { JwtHelperService } from "@auth0/angular-jwt"
import { HttpClient } from "@angular/common/http"
import { BehaviorSubject } from "rxjs"
import { ConfigService } from "./config.service"
import { GeowebJWT, GeowebUserRoles } from "../../../../shared/src/user/user"

export function jwtOptionsFactory(configService: ConfigService) {
  return {
    tokenGetter: getJwt,
    whitelistedDomains: [
      new RegExp("^null$"),
      `${configService.config.HOSTNAME}:${configService.config.RESTAPI_PORT}`,
    ],
    blacklistedRoutes: ["/login"],
  }
}

export function getJwt() {
  return localStorage.getItem("access_token")
}

function setJwt(token: string) {
  return localStorage.setItem("access_token", token)
}

function clearJwt() {
  return localStorage.removeItem("access_token")
}

const jwtHelper = new JwtHelperService()

/**
 * Handles login, JWT and stores user state variables like username, isloggedin, isadmin...
 *
 * @export
 * @class AuthService
 */
@Injectable({
  providedIn: "root",
})
export class AuthService {
  private _isLoggedIn = false
  private _isAdmin = false
  private _jwt: GeowebJWT

  private _onLoginSubject: BehaviorSubject<boolean> = new BehaviorSubject(false)

  constructor(private http: HttpClient, private configService: ConfigService) {
    this.updateLoginStatus()
  }

  get isLoggedIn() {
    return this._isLoggedIn
  }

  get isAdmin() {
    return this._isAdmin
  }

  get username(): string {
    return this._jwt.username
  }

  get userRole(): GeowebUserRoles {
    return this._jwt.role
  }

  public getLoginSubjectAsObservable() {
    return this._onLoginSubject.asObservable()
  }

  private updateLoginStatus() {
    const jwt = getJwt()

    const newLoginStatus =
      typeof jwt !== "undefined" && !jwtHelper.isTokenExpired(jwt)
    if (newLoginStatus) {
      this._jwt = jwtHelper.decodeToken(jwt)
      this._isAdmin = "role" in this._jwt && this._jwt.role === "admin"
    }

    if (newLoginStatus !== this._isLoggedIn) {
      this._isLoggedIn = newLoginStatus
      this._onLoginSubject.next(newLoginStatus)
    }
  }

  public async login(username: string, password: string) {
    let response

    // TODO: use restApiSErvice

    response = await this.http
      .post(`${this.configService.config.MASTER_WEBSERVER_URL}api/login`, {
        username,
        password,
      })
      .toPromise()

    setJwt(response.token)

    this.updateLoginStatus()

    return true
  }

  public logout(): void | Boolean {
    clearJwt()

    this.updateLoginStatus()
  }
}
