import { generate } from "c3"
import { parseCsv } from "../../../../../../shared/src/util/string"
import { LayerService } from "src/app/geo-web/map/map-layer/layer-service.service"

export const createWindChart = (layerService: LayerService, metCsv: string) => {
  const parsedOutput = parseCsv(metCsv).reduce(
    (acc, col, idx) => {
      if (idx !== 0) {
        acc["date"].push(col[0])
        acc["wind_direction"].push(parseFloat(col[1]))
        acc["wind_speed"].push(parseFloat(col[2]))
      }
      return acc
    },
    { date: [], wind_direction: [], wind_speed: [] }
  )

  layerService.createNewChart({
    hide: true,
    chart: generate({
      title: { text: `Wind` },
      data: {
        // labels: { format: format(".2f") },
        xFormat: "%Y-%m-%dT%H:%M:%S%Z",
        x: "x",
        columns: [
          ["x", ...parsedOutput["date"]],
          ["Wind Direction", ...parsedOutput["wind_direction"]],
          ["Wind Speed", ...parsedOutput["wind_speed"]],
        ],
        axes: {
          "Wind Speed": "y",
          "Wind Direction": "y2",
        },
      },

      axis: {
        x: {
          type: "timeseries",
          localtime: true,
          tick: {
            format: "%d/%m/%Y %H:%M",
          },
        },
        y: {
          label: {
            text: `Wind speed`,
            position: "outer-middle",
          },
          min: 0,
        },
        y2: {
          label: {
            text: `Wind direction`,
            position: "outer-middle",
          },
          min: 0,
          max: 360,
          show: true,
          tick: {
            values: [0, 90, 180, 270],
            format: val =>
              ["S", "SW", "W", "NW", "N", "NE", "E", "SE"][
                Math.floor(val / 45)
              ],
          },
        },
      },
      legend: { position: "inset" },
      // tooltip: {
      //   format: {
      //     value: (value, ratio, id) =>
      //       `${format(".2f")(value as number)} days above ${
      //         _self.templayer.dimensions.temp.selected.label
      //       } C`,
      //   },
      // },
    }),
  })
}
