import { MapboxGlLayer } from "src/app/geo-web/map/map-layer/mapbox-gl-layer"

export const absSaVectorTileLayers = (publicFilesUrl: string) =>
  [1, 2, 3, 4].map(saNum => {
    const sa = new MapboxGlLayer(
      `ABS SA${saNum}`,
      {
        id: `abs-sa${saNum}`,
        type: "fill",
        source: `abs-sa${saNum}`,
        "source-layer": `sa${saNum}2016`,

        paint: {
          "fill-color": "rgba(255, 255, 255, 0)",
          "fill-outline-color": "#000000",
        },
      },
      {
        id: `abs-sa${saNum}`,
        source: {
          type: "vector",
          tiles: [`${publicFilesUrl}abs-sa${saNum}/{z}/{x}/{y}.pbf`],
          // url: `metadata.json`,
        },
      }
    )

    sa.idByProperty = saNum <= 2 ? `SA${saNum}_MAIN16` : `SA${saNum}_CODE16`
    sa.opacity = 30

    return sa
  })
