export class TimeSlider {
  private _value: number
  public get value(): number {
    return this._value
  }
  public set value(value: number) {
    this._value = value
    this._valueDate = new Date(this.minDate.getTime() + this._value * 1000)
    this.subscribers.forEach(sub =>
      sub.onSliderValueChange(this._value, this._valueDate)
    )
  }

  private _valueDate: Date
  public get valueDate(): Date {
    return this._valueDate
  }
  public set valueDate(value: Date) {
    this._valueDate = value
    this._value = (this._valueDate.getTime() - this.minDate.getTime()) / 1000
    this.subscribers.forEach(sub =>
      sub.onSliderValueChange(this._value, this._valueDate)
    )
  }

  min: number
  minDate: Date
  max: number
  maxDate: Date
  step: number

  subscribers: Set<ITimeSliderSubscriber> = new Set()

  public timeSliderValueChange(event) {}

  public timeSliderDateChange(event) {}
}

export interface ITimeSliderSubscriber {
  slider: TimeSlider
  sliderEnabled: boolean
  onSliderValueChange(value: number, date: Date): void
}

export function instanceOfTimeSliderSubscriber(
  object: any
): object is ITimeSliderSubscriber {
  return (
    "slider" in object &&
    "sliderEnabled" in object &&
    "onSliderValueChange" in object
  )
}
