import { Injectable } from "@angular/core"

// If you import a module but never use any of the imported values other than as TypeScript types,
// the resulting javascript file will look as if you never imported the module at all.
// import { ipcRenderer, webFrame, remote } from "electron"
// import * as childProcess from "child_process"
// import * as fs from "fs"

import { IAngularConfig } from "../../../../shared/src/config/angular-config"

export const getElectronConfig = (): Partial<IAngularConfig> => {
  return (window as any).angularConfig
}

export const isElectron = () =>
  window && typeof (window as any).angularConfig !== "undefined"

/**
 * If using in Electron context, this handles passing configuration from Electron to Angular
 *
 * @export
 * @class ElectronService
 */
@Injectable()
export class ElectronService {
  // ipcRenderer: typeof ipcRenderer
  // webFrame: typeof webFrame
  // remote: typeof remote
  // childProcess: typeof childProcess
  // fs: typeof fs

  public isElectron = false

  constructor() {
    // Conditional imports
    // if (this.isElectron()) {
    // this.ipcRenderer = (window as any).require("electron").ipcRenderer
    // this.webFrame = (window as any).require("electron").webFrame
    // this.remote = (window as any).require("electron").remote
    // }
    this.isElectron = isElectron()
  }

  getConfig = getElectronConfig
}
