import { Injectable } from "@angular/core"

import { Observable, Subject } from "rxjs"
import { DynamicFormModel } from "@ng-dynamic-forms/core"
import { ShowPopupMessage } from "./map-popup-component"

@Injectable({
  providedIn: "root",
})
export class PopupFormService {
  private _currentPopup: ShowPopupMessage = undefined
  private subject = new Subject<ShowPopupMessage>()

  constructor() {}

  get currentPopup() {
    return this._currentPopup
  }

  getPopupSubject(): Observable<ShowPopupMessage> {
    return this.subject.asObservable()
  }

  showPopup(popupMessage: ShowPopupMessage): void {
    this._currentPopup = popupMessage
    this.subject.next(popupMessage)
  }

  hidePopup() {
    if (typeof this.currentPopup.formOnCancelFn === "function") {
      this.currentPopup.formOnCancelFn()
    }
    this._currentPopup = undefined
    this.subject.next(undefined)
  }
}
