import { JobType } from "../../../../../../shared/src/job/job-types"

import { ClientJob } from "../../job"
import { MapLayer } from "src/app/geo-web/map/map-layer/map-layer"

import { testFormRootModel } from "../climate/form-model"
import { MapboxGlLayer } from "src/app/geo-web/map/map-layer/mapbox-gl-layer"
import { D3ColourScheme } from "src/app/geo-web/map/colour-scheme/colour-scheme"
import {
  RasterMultiLayerDimensionsMap,
  RasterMultiLayer,
} from "src/app/geo-web/map/map-layer/map-geotiff-layer"
import { FileLocation } from "../../../../../../shared/src/file-provider/interfaces"

export class SewJob extends ClientJob {
  type: JobType = JobType.SEW
  public clientOnly = true

  async createInputLayers(): Promise<MapLayer[]> {
    const setupLayer = (layer: MapboxGlLayer) => {
      layer.colourScheme = new D3ColourScheme("interpolateYlOrRd")
      layer.colourByProperty = {
        options: [
          {
            label: "Min hazard",
            key: "min",
            minValue: 0,
            maxValue: 10,
          },
          {
            label: "Mean hazard",
            key: "mean",
            minValue: 0,
            maxValue: 10,
          },
          {
            label: "Max hazard",
            key: "max",
            minValue: 0,
            maxValue: 10,
          },
        ],
        selected: {
          label: "Max hazard",
          key: "max",
          minValue: 0,
          maxValue: 10,
        },
      }

      layer.opacity = 100
    }

    const waterPipesLayer = new MapboxGlLayer(
      "Water pipes",
      {
        id: "water-pipes",
        type: "line",
        source: "water-pipes",
        paint: {
          "line-width": 3,
        },
      },

      {
        id: "water-pipes",
        source: {
          type: "geojson",
          data: await this.jobService.resolveDirTree(
            `geostack-data/spark/Public/sew/water_pipe.geojson`,
            FileLocation.GCS
          ),
        },
      } as any
    )

    setupLayer(waterPipesLayer)

    const manholeLayer = new MapboxGlLayer(
      "Manholes",
      {
        id: "manholes",
        source: "manholes",
        type: "circle",

        paint: {
          "circle-radius": 2,
        },
      },

      {
        id: "manholes",
        source: {
          type: "geojson",
          data: await this.jobService.resolveDirTree(
            `geostack-data/spark/Public/sew/manhole.geojson`,
            FileLocation.GCS
          ),
        },
      } as any
    )

    setupLayer(manholeLayer)

    const sewerERS = new MapboxGlLayer(
      "Sewer ERS",
      {
        id: "sewerERS",
        source: "sewerERS",
        type: "circle",

        paint: {
          "circle-radius": 2,
        },
      },

      {
        id: "sewerERS",
        source: {
          type: "geojson",
          data: await this.jobService.resolveDirTree(
            `geostack-data/spark/Public/sew/sewerERS.geojson`,
            FileLocation.GCS
          ),
        },
      } as any
    )

    setupLayer(sewerERS)

    const sewerPipesLayer = new MapboxGlLayer(
      "Sewer pipes",
      {
        id: "sewer-pipes",
        type: "line",
        source: "sewer-pipes",
        paint: {
          "line-width": 3,
        },
      },

      {
        id: "sewer-pipes",
        source: {
          type: "geojson",
          data: await this.jobService.resolveDirTree(
            `geostack-data/spark/Public/sew/sewer_pipe.geojson`,
            FileLocation.GCS
          ),
        },
      } as any
    )

    setupLayer(sewerPipesLayer)

    const sewerPumps = new MapboxGlLayer(
      "Sewer pump stations",
      {
        id: "sewer-pump",
        source: "sewer-pump",
        type: "circle",

        paint: {
          "circle-radius": 2,
        },
      },

      {
        id: "sewer-pump",
        source: {
          type: "geojson",
          data: await this.jobService.resolveDirTree(
            `geostack-data/spark/Public/sew/Sewer_Pump_Stations.geojson`,
            FileLocation.GCS
          ),
        },
      } as any
    )

    setupLayer(sewerPumps)

    const treatmentPlant = new MapboxGlLayer(
      "Treatment plants",
      {
        id: "treatment-plants",
        source: "treatment-plants",
        type: "fill",
      },

      {
        id: "treatment-plants",
        source: {
          type: "geojson",
          data: await this.jobService.resolveDirTree(
            `geostack-data/spark/Public/sew/treatment_plant.geojson`,
            FileLocation.GCS
          ),
        },
      } as any
    )

    setupLayer(treatmentPlant)

    const waterPumps = new MapboxGlLayer(
      "Water pumps",
      {
        id: "water-pumps",
        source: "water-pumps",
        type: "circle",

        paint: {
          "circle-radius": 2,
        },
      },

      {
        id: "water-pumps",
        source: {
          type: "geojson",
          data: await this.jobService.resolveDirTree(
            `geostack-data/spark/Public/sew/Water_Pump_Station.geojson`,
            FileLocation.GCS
          ),
        },
      } as any
    )

    setupLayer(waterPumps)

    const waterStorages = new MapboxGlLayer(
      "Water storages",
      {
        id: "water-storages",
        source: "water-storages",
        type: "circle",

        paint: {
          "circle-radius": 2,
        },
      },

      {
        id: "water-storages",
        source: {
          type: "geojson",
          data: await this.jobService.resolveDirTree(
            `geostack-data/spark/Public/sew/Water_Storages.geojson`,
            FileLocation.GCS
          ),
        },
      } as any
    )

    setupLayer(waterStorages)

    const dimensions: RasterMultiLayerDimensionsMap = {
      property: {
        label: "property",
        key: "property",
        options: [
          {
            value: `results_grid_speed_max.0010-3857.tif`,
            label: `Speed`,
          },
          {
            value: `results_grid_height_max.0010-3857.tif`,
            label: `Height`,
          },
          {
            value: `results_grid_time_above_0.3_metre.0010-3857.tif`,
            label: `Time above 0.3m`,
          },
        ],
        selected: {
          value: `results_grid_height_max.0010-3857.tif`,
          label: `Height`,
        },
      },
    }

    // const inundationLayer = new RasterMultiLayer(
    //   "Inundation",
    //   dimensions,
    //   dimensions =>
    //     `${this.configService.getMasterPublicFilesUrl()}sew/${
    //       dimensions["property"].selected.value
    //     }`
    // )

    // await inundationLayer.show()

    await sewerPipesLayer.show()

    await waterPipesLayer.show()

    return [
      manholeLayer,
      sewerERS,
      sewerPipesLayer,
      sewerPumps,
      treatmentPlant,
      waterPipesLayer,
      waterPumps,
      waterStorages,
      // inundationLayer,
    ]
  }

  async createOutputLayers(): Promise<MapLayer[]> {
    return []
  }

  setBbox() {}
}
