import {
  ChangeDetectionStrategy,
  Component,
  EventEmitter,
  Input,
  Output,
  ViewChild,
  Type,
  OnInit,
  ViewEncapsulation,
  AfterViewInit,
  OnDestroy,
} from "@angular/core"
import { FormGroup } from "@angular/forms"
import {
  DynamicFormControlCustomEvent,
  DynamicFormLayout,
  DynamicFormLayoutService,
  DynamicFormValidationService,
  DynamicFormControlComponent,
} from "@ng-dynamic-forms/core"

import { FileBrowserInputModel } from "./file-browser-input-model"

/**
 * FileBrowser DynamicFormControlComponent. This allows a FileBrowserInput to be used in a DynamicForm
 *
 * @export
 * @class FileBrowserInputComponent
 */
@Component({
  selector: "app-form-file-browser-input",
  templateUrl: "./form-file-browser-input.component.html",
  styleUrls: ["./form-file-browser-input.component.scss"],
  changeDetection: ChangeDetectionStrategy.OnPush,
  encapsulation: ViewEncapsulation.None,
})
export class FileBrowserInputComponent extends DynamicFormControlComponent
  implements AfterViewInit, OnInit, OnDestroy {
  @Input() group: FormGroup
  @Input() layout: DynamicFormLayout
  @Input() model: FileBrowserInputModel

  @Output() blur: EventEmitter<any> = new EventEmitter()
  @Output() change: EventEmitter<any> = new EventEmitter()
  @Output() customEvent: EventEmitter<
    DynamicFormControlCustomEvent
  > = new EventEmitter()
  @Output() focus: EventEmitter<any> = new EventEmitter()

  constructor(
    protected layoutService: DynamicFormLayoutService,
    protected validationService: DynamicFormValidationService
  ) {
    super(layoutService, validationService)
  }

  ngOnInit(): void {}

  ngAfterViewInit(): void {}

  ngOnDestroy(): void {}
}
