import {
  Component,
  forwardRef,
  OnChanges,
  Input,
  NgZone,
  ChangeDetectorRef,
  ViewEncapsulation,
  OnDestroy,
} from "@angular/core"
import {
  ControlValueAccessor,
  NG_VALUE_ACCESSOR,
  FormControl,
} from "@angular/forms"
import { FileBrowserService } from "../../file-browser/file-browser.service"
import { FileBrowserOptions } from "./file-browser-input-model"
import { DirectoryTree } from "../../../../../shared/src/file-provider/interfaces"

/**
 * FileBrowserInput control for DynamicForms. This component is embedded in FileBrowserInputComponent's view template
 *
 * @export
 * @class FileBrowserInputControlComponent
 */
@Component({
  selector: "app-file-browser-input",
  templateUrl: "./file-browser-input.component.html",
  styleUrls: ["./file-browser-input.component.scss"],
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: forwardRef(() => FileBrowserInputControlComponent),
      multi: true,
    },
  ],
  encapsulation: ViewEncapsulation.None,
})
export class FileBrowserInputControlComponent
  implements ControlValueAccessor, OnDestroy {
  propagateChange: any = () => {}
  validateFn: any = () => {}

  @Input("file") _file: DirectoryTree
  @Input("options") options?: FileBrowserOptions

  constructor(
    private fileBrowserService: FileBrowserService,
    private cdr: ChangeDetectorRef
  ) {}

  ngOnDestroy(): void {
    this.cdr.detach()
  }

  get file() {
    return this._file
  }

  set file(val) {
    this._file = val
    this.propagateChange(val)
    try {
      this.cdr.detectChanges()
    } catch {}
  }

  writeValue(value) {
    if (value) {
      this.file = value
    }
  }

  registerOnChange(fn) {
    this.propagateChange = fn
  }

  registerOnTouched() {}

  validate(c: FormControl) {
    return this.validateFn(c)
  }

  async openFileBrowser() {
    this.file = await this.fileBrowserService.show(this.options, this.file)
  }
}
