export const months = [
  "January",
  "February",
  "March",
  "April",
  "May",
  "June",
  "July",
  "August",
  "September",
  "October",
  "November",
  "December",
]

export function dateToUtcDate(d: Date) {
  return new Date(
    Date.UTC(
      d.getUTCFullYear(),
      d.getUTCMonth(),
      d.getUTCDate(),
      d.getUTCHours(),
      d.getUTCMinutes(),
      d.getUTCSeconds()
    )
  )
}

// Adapted from https://stackoverflow.com/a/37096512
export function secondsToHMSString(d: number) {
  const h = Math.floor(d / 3600)
  const m = Math.floor((d % 3600) / 60)
  const s = Math.floor((d % 3600) % 60)

  return `${h > 0 ? `${h}h ` : ""}
    ${m > 0 || h > 0 ? `${m.toString().padStart(2, "0")}m ` : ""}
    ${s.toString().padStart(2, "0")}s`
}

export function secondsToHMString(format: "hm" | ":" = "hm") {
  return (d: number) => {
    const h =
      Math.floor(d / 3600) + Math.floor(Math.round((d % 3600) / 60) / 60)
    const m = Math.round((d % 3600) / 60) % 60

    if (format === "hm") {
      return `${h > 0 ? `${h}h ` : ""}${m.toString().padStart(2, "0")}m`
    }
    return `${h.toString().padStart(2, "0")}:${m.toString().padStart(2, "0")}`
  }
}
