import { PromiseMap } from "../../../../../shared/src/util/promise"

export enum WebWorkerType {
  GeotiffLoader = "assets/scripts/geotiff-web-workerV2.js",
}

export enum WebWorkerErrorMessages {
  WorkerTerminated = "WebWorker terminated",
}

// TODO: Maybe add some queueing?

/**
 * Note: currently not used
 *
 * @export
 * @class WebWorker
 */
export class WebWorker {
  type: WebWorkerType
  worker: Worker

  workerPromises: PromiseMap = new Map()

  constructor(type: WebWorkerType) {
    this.type = type

    this.worker = new Worker(type)
    this.worker.onmessage = this.onMessageReceived.bind(this)
    this.worker.onerror = this.onError.bind(this)
  }

  onMessageReceived(message: MessageEvent) {
    if (
      typeof message.data.args !== "undefined" &&
      this.workerPromises.has(message.data.args.key)
    ) {
      if ("error" in message.data) {
        this.workerPromises
          .get(message.data.args.key)
          .reject(message.data.error)
      } else {
        this.workerPromises.get(message.data.args.key).resolve(message.data)
      }
    } else {
      console.log(`Webworker ${this.type} received message: ${message.data}`)
    }
  }

  onError(message) {
    console.log(message)
    this.workerPromises.forEach(p => p.reject(message.data))
  }

  run(args): Promise<any> {
    return new Promise((resolve, reject) => {
      this.workerPromises.set(args.key, { resolve, reject })

      this.worker.postMessage(args)
    })
  }

  terminate() {
    this.workerPromises.forEach(p =>
      p.reject(WebWorkerErrorMessages.WorkerTerminated)
    )
    this.worker.terminate()
  }
}
