// TODO: create map from types to Class
export interface IMessage {
  type:
    | ServerConfigMessageTypes
    | JobMessageTypes
    | JobListMessageTypes
    | FlashMessageTypes
    | WebSocketAuthMessageTypes
    | IGetDirTreeSignedURLType
  data:
    | IJobMessage
    | IJobListMessage
    | FlashMessage
    | IWebSocketAuthMessage
    | IGetDirTreeSignedURL
}

export enum JobMessageTypes {
  Data = "job-data",
  PartialData = "job-partial-data",
  Status = "job-status",
  Get = "job-get",
  New = "job-new",
  Log = "job-log",
  Run = "job-run",
  Stop = "job-stop",
  Delete = "job-delete",
}

export enum JobListMessageTypes {
  Get = "job-list-get",
  GetTemplate = "job-list-get-templates",
}

export enum ServerConfigMessageTypes {
  Get = "server-config-get",
  Data = "server-config-data",
}

// Start error message api interfaces
export enum FlashMessageTypes {
  General = "flash-message-general",
}

export class MessageNew {
  private _type

  constructor(type) {
    this._type = type
  }

  get type() {
    return this._type
  }

  toJSON() {
    // return all properties other than 'type'
    return {
      type: this.type,
      data: Object.keys(this)
        .filter(key => !["type", "_type"].includes(key))
        .reduce((obj, key) => {
          obj[key] = this[key]
          return obj
        }, {}),
    }
  }
}

export class FlashMessage extends MessageNew {
  title: string = "An error has occurred"
  message: string
  severity: "warn" | "error" | "info" | "success" = "error"
  duration = 6000
  sticky: boolean = false

  constructor(obj: Partial<FlashMessage> & { message: string }) {
    super(FlashMessageTypes.General)
    Object.assign(this, obj)
  }
}

export class ServerConfigGetMessage extends MessageNew {
  constructor(obj: Partial<ServerConfigGetMessage> = {}) {
    super(ServerConfigMessageTypes.Get)
    Object.assign(this, obj)
  }
}

export class ServerConfigDataMessage extends MessageNew {
  config: ServerPublicConfigJSON

  constructor(
    obj: Partial<ServerConfigDataMessage> & { config: ServerPublicConfigJSON }
  ) {
    super(ServerConfigMessageTypes.Data)
    Object.assign(this, obj)
  }
}

