// From https://stackoverflow.com/questions/42623071/maximum-call-stack-size-exceeded-with-math-min-and-math-max
export function getMax(arr) {
  let len = arr.length
  let max = 0

  while (len--) {
    max = arr[len] > max ? arr[len] : max
  }
  return max
}

export function getAbsMax(arr, noDataValue = 0) {
  let len = arr.length
  let max = 0

  while (len--) {
    max =
      arr[len] > noDataValue && Math.abs(arr[len]) > max
        ? Math.abs(arr[len])
        : max
  }
  return max
}

// Adapted https://gist.github.com/miguelmota/5f27d5cdb8462fbbb402
export function flattenArray(array): any[] {
  return Array.isArray(array)
    ? [].concat.apply([], array.map(flattenArray))
    : array
}

// Where did this come from?
export function concatenate(resultConstructor, ...arrays) {
  let totalLength = 0
  for (const arr of arrays) {
    totalLength += arr.length
  }
  const result = new resultConstructor(totalLength)
  let offset = 0
  for (const arr of arrays) {
    result.set(arr, offset)
    offset += arr.length
  }
  return result
}
