import { FeatureCollection, featureCollection, Geometry } from "@turf/turf"
import { JSONSerialiser } from "../../job-base"
import { DirectoryTree } from "../../../file-provider/interfaces"

export class SparkJobInputs implements JSONSerialiser<SparkJobInputs> {
  private _startTime: Date = new Date()

  get startTime(): Date {
    return this._startTime
  }

  set startTime(s: Date) {
    this._startTime = s
  }

  private _durationSeconds = 60 * 60

  get durationSeconds(): number {
    return this._durationSeconds
  }

  set durationSeconds(seconds: number) {
    this._durationSeconds = seconds
  }

  public resolutionMeters: number = 30
  public projection: string =
    "+proj=lcc +lat_1=-36 +lat_2=-38 +lat_0=-37 +lon_0=145 +x_0=2500000 +y_0=2500000 +ellps=GRS80 +towgs84=0,0,0,0,0,0,0 +units=m +no_defs"
  public simulationProjection: string = "EPSG:3111"

  public startConditions: FeatureCollection<
    Geometry,
    { time: number; radius?: number }
  > = featureCollection([])

  public maskConditions: FeatureCollection<
    Geometry,
    { time: number; radius?: number }
  > = featureCollection([])

  public outputProjection: string =
    "+proj=merc +a=6378137 +b=6378137 +lat_ts=0.0 +lon_0=0.0 +x_0=0.0 +y_0=0 +k=1.0 +units=m +no_defs"

  public disableFileOutputs = false

  public outputIsochroneTimeSecond = 3600
  public outputIsochroneJSONFile = "Output/out_spark_contour.geojson"

  public initialisationModel: string = ""

  public rateOfSpreadModelsCommonStart = ""
  public rateOfSpreadModelsCommonEnd = ""
  public rateOfSpreadModels: { [key: number]: string } = {}

  public updateModels: { [key: number]: string } = {}
  public updateModelsCommonStart: string = ""
  public updateModelsCommonEnd: string = ""

  get startDateISO8601() {
    return this.startTime.toISOString()
  }

  set startDateISO8601(d: string) {
    this.startTime = new Date(d)
  }

  public inputLayers: {
    name: string
    source: DirectoryTree
    projection: string
  }[] = []

  public outputLayers: {
    name: string
    destination: string
    description: string
    units: string
  }[] = []

  public classificationLayer: DirectoryTree | {} = {}

  public metCSVFile: DirectoryTree | {} = {}

  public fromJSON(inputJSON: Partial<SparkJobInputs>): SparkJobInputs {
    Object.assign(this, inputJSON)

    if ("startTime" in inputJSON) {
      this._startTime = new Date(inputJSON.startTime)
    }

    return this
  }

  toJSON(): Partial<SparkJobInputs> {
    return Object.assign({}, this, {
      startTime: this._startTime,
      _startTime: undefined,
      startDateISO8601: this.startDateISO8601,
      durationSeconds: this._durationSeconds,
      _durationSeconds: undefined,
    })
  }
}

export class SparkJobOutputs implements JSONSerialiser<SparkJobOutputs> {
  public fromJSON(outputJson: Partial<SparkJobOutputs>) {
    return Object.assign(this, outputJson)
  }

  public toJSON(): Partial<SparkJobOutputs> {
    return Object.assign({}, this)
  }
}
