  public async onJobUpdated(job: ClientJob) {
    const init =
      typeof this.currentJob === "undefined" ||
      this.currentJob.name !== job.name ||
      this.currentJob.type !== job.type

    const statusChanged =
      typeof this.currentJobStatus !== "undefined" &&
      job.status !== this.currentJobStatus

    const jobChanged =
      typeof this.currentJob !== "undefined" && job.name !== job.name

    this.currentJob = job
    this.currentJobStatus = job.status

    if (
      init ||
      (statusChanged &&
        this.currentJobStatus !== JobStatus.RUNNING &&
        this.currentJobStatus !== JobStatus.QUEUED &&
        this.currentJobStatus !== JobStatus.STARTING)
    ) {
      try {
        this.spinnerService.setSpinner(`current-job-init`, {
          name: `Initialising job view`,
          icon: SpinnerIcon.LineSpin,
        })

        if (
          this.layerService.mapboxGl.getStyle().sprite !==
          this.currentJob.mapboxStyle
        ) {
          this.layerService.mapboxGl.setStyle(this.currentJob.mapboxStyle)

          // The style must be loaded before adding layers - or the new layers about to be added might be removed
          await new Promise(resolve =>
            this.layerService.mapboxGl.once("styledata", resolve)
          )
        }

        this.layerService.clearFileCache()

        this.selectedLayers = []
        this.jobLayers = []
        this.timeSliders = []

        // Update input layers
        // if job status Changes -> then just update input layers
        if (!init && statusChanged) {
          await this.currentJob.updateInputLayers()

          await Promise.all(
            this.jobInputLayers.map(async layer => {
              await layer.updateLayer(true)
            })
          )
          // Else remove input layers and create them again
        } else {
          this.layerService.removeChart()
          await Promise.all(this.jobInputLayers.map(layer => layer.delete()))
          this.jobInputLayers = await this.currentJob.createInputLayers()

          // Get edit feature toolbar buttons from EditableFeatureCollectionLayers
          this.editableFeatureToolbarButtons = flattenArray(
            this.jobInputLayers
              .filter(EditableFeatureCollectionLayer.isInstanceOf)
              .map(layer => layer.getToolbarButtons())
          )
        }
        this.jobLayers.push(...this.jobInputLayers)

        // Delete all output layers
        this.jobOutputLayers.forEach(layer => {
          layer.delete()
        })

        // If job is finished -> draw results
        if (this.currentJob.status === JobStatus.FINISHED) {
          console.log("here finished")
          this.spinnerService.setSpinner(`current-job-init`, {
            name: `Creating output layers`,
            icon: SpinnerIcon.GridPulse,
          })

          this.jobOutputLayers = await this.currentJob.createSimulationLayer()
          this.setupInitialTime()
          this.activities = {}
          this.animationSpeed = this.currentJob.getAnimationSpeed()
          this.jobLayers.push(...this.jobOutputLayers)
        }

        this.currentJob.setBbox()

        if (init || jobChanged) {
          this.zoomToJobBbox()
        }

        this.updateTimeSliders()
        this.setLayerZOrder()
      } catch (error) {
      } finally {

        this.spinnerService.removeSpinner(`current-job-init`)
      }
    }
  }

  public async downloadUrl(url: string, type?: string) {
    this.spinnerService.setSpinner(`download-file-${url}`, {
      name: `Downloading ${url}`,
      icon: SpinnerIcon.LineSpin,
      progress: 0,
    })
    await this.restApiService.downloadUrl(url, type, progress => {
      this.spinnerService.setSpinner(`download-file-${url}`, {
        name: `Downloading ${url}`,
        icon: SpinnerIcon.LineSpin,
        progress,
      })
    })

    this.spinnerService.removeSpinner(`download-file-${url}`)
  }

  public async toggleLayerOpacity(evt, layer: MapLayer) {
    evt.stopImmediatePropagation()

    if (layer.visible) {
      layer.hide()
    } else {
      await layer.show()
    }

    this.setLayerZOrder()

    return false
  }

  public updateTimeSliders() {
    // Update timesliders
    this.timeSliders = [...this.jobLayers, ...this.jobInputLayers].reduce(
      (sliders, layer) => {
        if (instanceOfTimeSliderSubscriber(layer) && layer.sliderEnabled) {
          sliders.push(layer.slider)
        }
        return sliders
      },
      []
    )
  }

  public async renderUpdatedLayer(layer: MapLayer) {

    await layer.updateLayer()

    if (!layer.visible) {
      await layer.show()
    }

    this.setLayerZOrder()
  }

  public setLayerZOrder() {
    let prevMapboxLayerId: string
    // Place job layers below the following two mapbox layers: 'place_label_city_small_s' or 'settlement-label'
    if (
      typeof this.layerService.mapboxGl.getLayer("place_label_city_small_s") !==
      "undefined"
    ) {
      prevMapboxLayerId = "place_label_city_small_s"
    } else if (
      typeof this.layerService.mapboxGl.getLayer("settlement-label") !==
      "undefined"
    ) {
      prevMapboxLayerId = "settlement-label"
    }

    ;[...this.jobLayers, ...this.jobInputLayers].forEach((layer, idx) => {
      if (!layer.visible) {
        return
      }
      if (
        MapboxGlLayer.isInstanceOf(layer) &&
        typeof layer.mapboxGlLayer !== "undefined"
      ) {
        this.layerService.mapboxGl.moveLayer(
          layer.mapboxGlLayer.id,
          prevMapboxLayerId
        )
        prevMapboxLayerId = layer.mapboxGlLayer.id
      } else if (EditableFeatureCollectionLayer.isInstanceOf(layer)) {
        layer.mapboxLayers.forEach(mbl =>
          this.layerService.mapboxGl.moveLayer(mbl.mapboxGlLayer.id)
        )
      }
    })
  }

  public zoomToJobBbox(): Promise<void> {
    return new Promise(resolve => {
      if (typeof this.currentJob.boundingBox4326 !== "undefined") {
        this.layerService.mapboxGl.once("moveend", resolve)

        if (this.currentJob.status === JobStatus.FINISHED) {
          this.fitMapTo4326Bbox(this.currentJob.boundingBox4326, 1.5, 14)
        } else {
          this.fitMapTo4326Bbox(this.currentJob.boundingBox4326, 1.5, 10)
        }
      }
    })
  }

  public fitMapTo4326Bbox(boundingBox: number[], duration = 1.5, maxZoom?) {
    if (Array.isArray(boundingBox)) {
      this.layerService.mapboxGl.fitBounds(
        boundingBox as [number, number, number, number],
        { duration, maxZoom }
      )
    }
  }

  public toggleFeatureEdit(keepChanges: boolean) {
    if (EditableFeatureCollectionLayer.mode === "edit") {
      EditableFeatureCollectionLayer.disableEditMode(
        [...this.jobLayers, ...this.jobInputLayers].filter(l =>
          EditableFeatureCollectionLayer.isInstanceOf(l)
        ) as EditableFeatureCollectionLayer[],
        keepChanges
      )
    } else {
      const drawEditButtonBounds = this.mapboxDrawEditButton.nativeElement.getBoundingClientRect()

      this.mapPopupService.showPopup({
        title: "Edit features",
        coordinates: {
          x: drawEditButtonBounds.left + drawEditButtonBounds.width / 2,
          y: drawEditButtonBounds.top + drawEditButtonBounds.height / 2,
        },
        formOnCancelFn: () => this.toggleFeatureEdit(false),
        formOnSubmitFn: () => this.toggleFeatureEdit(true),
        background: false,
      })
      EditableFeatureCollectionLayer.enableEditMode()
    }
  }
