  /**
   * Create new layers according to selected messages
   */
  private createSingleZoneLayer(data: object) {

    let zone = data["properties"]["SA1_MAIN16"]

    let zoneLayer = new MapboxGlLayer(
      zone,
      {
        id: zone,
        type: "fill",
        source: zone,
        paint: {
          'fill-color': '#627BC1'
        }
      },
      {
        id: zone,
        source: {
          type: "geojson",
          data: data,
        },
      } as any
    )

    zoneLayer.show()

  }
  /**
   * Delete single zone layer
   */
  private deleteSingleZoneLayer(id: string) {

    if (this.layerService.mapboxGl.getLayer(id)) {

      this.layerService.mapboxGl.removeLayer(id)

    }
  }


