import { Component, OnInit } from "@angular/core"
import { FormGroup } from "@angular/forms"
import {
  DynamicFormModel,
  DynamicInputModel,
  DYNAMIC_FORM_CONTROL_INPUT_TYPE_PASSWORD,
  DynamicFormService,
} from "@ng-dynamic-forms/core"
import { alphaNumericDashesValidator } from "../../../../shared/src/util/string"
import { SpinnerService, SpinnerIcon } from "../spinner/spinner-service.service"
import { AuthService } from "../services/auth.service"
import { Router } from "@angular/router"
import { FlashMessageService } from "../services/flash-message.service"
import { FlashMessage } from "../../../../shared/src/message-api"

/**
 * Renders login form and handles login
 *
 * @export
 * @class LoginComponent
 */
@Component({
  selector: "app-login",
  templateUrl: "./login.component.html",
  styleUrls: ["./login.component.scss"],
})
export class LoginComponent implements OnInit {
  public loading = false

  public formGroup: FormGroup
  public formModel: DynamicFormModel = [
    new DynamicInputModel({
      id: "username",
      label: "Username",
      required: null,
      validators: {
        required: null,
        customValidator: alphaNumericDashesValidator,
      },
      errorMessages: {
        required: "{{ label }} is required",
        customValidator: alphaNumericDashesValidator.errorMessage,
      },
    }),
    new DynamicInputModel({
      id: "password",
      label: "Password",
      inputType: DYNAMIC_FORM_CONTROL_INPUT_TYPE_PASSWORD,
      required: null,
      validators: {
        required: null,
      },
      errorMessages: {
        required: "{{ label }} is required",
        incorrect: "Login is incorrect",
      },
    }),
  ]

  constructor(
    private formService: DynamicFormService,
    private spinnerService: SpinnerService,
    private authService: AuthService,
    private router: Router,
    private flashMessageService: FlashMessageService
  ) {
    this.formGroup = this.formService.createFormGroup(this.formModel)
  }

  ngOnInit() {
    if (this.authService.isLoggedIn) {
      this.router.navigate(["/"])
    }
  }

  async login() {
    if (this.formGroup.valid) {
      this.spinnerService.setSpinner(`login`, {
        name: `Logging in...`,
        icon: SpinnerIcon.LineSpin,
      })

      try {
        await this.authService.login(
          this.formGroup.value.username,
          this.formGroup.value.password
        )
      } catch (error) {
        this.flashMessageService.pushFlashMessage(
          new FlashMessage({
            message: error.error.message || error.message,
            title: error.statusText,
          })
        )
        if (error.status && error.status === 401) {
          this.formGroup.markAsDirty()
        }
      }

      if (this.authService.isLoggedIn) {
        this.router.navigate(["/"])
      } else {
        this.formGroup.get("password").setErrors({ incorrect: true })
      }

      this.spinnerService.removeSpinner(`login`)
    }
  }

  onEnterKeyPress = e => {
    if (e.key === "Enter") {
      this.login()
      e.srcElement.blur()
    }
  }
}
