export class GeowebUser {
  username: string
  role: GeowebUserRoles
}

export enum GeowebUserRoles {
  User = "user",
  Admin = "admin",
}

export interface ClientJWT {
  token?: string
}

export class GeowebJWT extends GeowebUser {
  // From https://scotch.io/tutorials/the-anatomy-of-a-json-web-token
  exp: string // This will probably be the registered claim most often used. This will define the expiration in NumericDate value. The expiration MUST be after the current date/time.
  iat: string // The time the JWT was issued. Can be used to determine the age of the JWT

  iss?: string // The issuer of the token
  sub?: string // The subject of the token
  aud?: string // The audience of the token

  nbf?: string // Defines the time before which the JWT MUST NOT be accepted for processing

  jti?: string // Unique identifier for the JWT. Can be used to prevent the JWT from being replayed. This is helpful for a one time use token.
}
