import {
  Component,
  OnInit,
  OnDestroy,
  ViewEncapsulation,
  ViewChild,
} from "@angular/core"
import { FileBrowserService, FileBrowserNode } from "./file-browser.service"
import { Subscription } from "rxjs"
import { Tree } from "primeng/tree"
import { FileBrowserOptions } from "../form/file-browser-input/file-browser-input-model"

/**
 * Displays a file browser - it contains a tree-like interface and communicates with fileBrowserService
 *
 * @export
 * @class FileBrowserComponent
 */
@Component({
  selector: "app-file-browser",
  templateUrl: "./file-browser.component.html",
  styleUrls: ["./file-browser.component.scss"],
  encapsulation: ViewEncapsulation.None,
})
export class FileBrowserComponent implements OnInit, OnDestroy {
  @ViewChild("fileBrowserTree") fileBrowserTree: Tree

  options: FileBrowserOptions

  selectedFile: FileBrowserNode
  files: FileBrowserNode[]
  public visible = false

  private visibleSubscription: Subscription
  private filesSubscription: Subscription
  private selectedFileSubscription: Subscription

  constructor(private fileBrowserService: FileBrowserService) {
    this.visibleSubscription = fileBrowserService
      .isVisibleObservable()
      .subscribe(visible => {
        this.visible = visible
        if (visible) {
          this.options = this.fileBrowserService.options
        }
      })

    this.filesSubscription = fileBrowserService
      .getFilesObservable()
      .subscribe(files => {
        this.files = files
      })

    this.selectedFileSubscription = fileBrowserService
      .getSelectedFileObservable()
      .subscribe(file => {
        this.selectedFile = file
      })
  }

  ngOnInit() {}

  ngOnDestroy(): void {
    this.visibleSubscription.unsubscribe()
    this.filesSubscription.unsubscribe()
    this.selectedFileSubscription.unsubscribe()
  }

  cancel() {
    this.fileBrowserService.cancel()
  }

  submit() {
    if (typeof this.selectedFile !== "undefined") {
      this.fileBrowserService.submit(this.selectedFile.data)
    } else {
      this.fileBrowserService.submit(undefined)
    }
  }

  clearSelection() {
    this.selectedFile = undefined
  }
}
