import { JobType } from "./job-types"
import { BBox } from "@turf/turf"
import { IJobLogMessage, LogMessageType } from "../message-api"
import { Constructor } from "../util/mixins"
import { FileLocation } from "../file-provider/interfaces"
export interface Type<T> extends Function {
  new (...args: any[]): T
}

export type ClassMap<Class> = (type: JobType | string) => Type<Class>

export class JobProcessParams {
  log = true
  progressIncrement = true
  makeResultsPublicRead = false
}

export class JobWorkerProcessParams {
  params: JobProcessParams
  job: JobJSON
}

export function blankJob<TBase extends Constructor>(
  Base: TBase,
  type: JobType
) {
  return class BlankJob extends Base implements JobTypeTraits {
    type: JobType = type
    inputs = undefined
    outputs = undefined
  }
}

export interface JSONSerialiser<JSONSourceClass, JSONClass = JSONSourceClass> {
  toJSON(): Partial<JSONClass>
  fromJSON(obj: Partial<JSONClass>): JSONSerialiser<JSONSourceClass, JSONClass>
}

export class JSONSerialiserT<T> {
  public fromJSON(outputJson: Partial<T>) {
    Object.assign(this, outputJson)
    return this
  }

  public toJSON(): Partial<T> {
    return Object.assign({}, this)
  }
}

export enum JobStatus {
  INACTIVE = "INACTIVE",
  STARTING = "STARTING",
  QUEUED = "QUEUED",
  RUNNING = "RUNNING",
  FINISHED = "FINISHED",
  ERROR = "ERROR",
}

/**
 *This class is used to parse/create JSON version of JobBase
 *Note: all fields must be given a value so they can be filled (only top-level fields need to be filled, nested properties don't need values)
 *
 * @export
 * @class JobJSON
 */
export class JobJSON {
  name: string = ""
  type: string = ""
  status: JobStatus = JobStatus.INACTIVE
  progress: number = 0

  // TODO: Make this relative to JOB directory not job base directory -> that is make this just ./Input and ./Output

  // Input directory relative to the job base directory (i.e. "$JOBNAME/Input")
  inputDirectory: string = ""
  // Output directory relative to the job base directory (i.e. "$JOBNAME/Output")
  outputDirectory: string = ""

  inputs: any = {}
  outputs: any = {}

  boundingBox4326: BBox = undefined

  outputStorageMode: FileLocation = FileLocation.MasterJob

  processParams: JobProcessParams = new JobProcessParams()

  startTime: Date
  finishTime: Date

  numberOfSubjobs = 0

  user: string = ""
}

export interface JobTypeTraits {
  type: JobType
  inputs: JSONSerialiser<any>
  outputs: JSONSerialiser<any>
}

export abstract class JobBase
  implements JobJSON, JSONSerialiser<JobBase, JobJSON>, JobTypeTraits {
  type: JobType = undefined
  public inputs: JSONSerialiser<any, any> = undefined
  public outputs: JSONSerialiser<any, any> = undefined
  public debugMode = false
  protected debug: (message: any) => void = console.debug

  readonly _name: string

  public user: string

  protected _logMessages: IJobLogMessage[] = []
  public onLogMessageUpdate: Set<(message: IJobLogMessage) => void> = new Set()

  protected _status: JobStatus = JobStatus.INACTIVE
  public onStatusUpdate: Set<(status: JobStatus) => void> = new Set()
  /**
   *  Prorgess between 0-100
   *
   * @protected
   */
  protected _progress: number = 0
  public onProgressUpdate: Set<
    (progress: number, dProgress?: number) => void
  > = new Set()

  // Input directory relative to the job base directory (i.e. "$JOBNAME/Input")
  public inputDirectory: string = ""
  // Output directory relative to the job base directory (i.e. "$JOBNAME/Output")
  public outputDirectory: string = ""

  public boundingBox4326: BBox

  public outputStorageMode: FileLocation = FileLocation.MasterJob

  public processParams: JobProcessParams = new JobProcessParams()

  protected _startTime: Date = new Date()
  protected _finishTime: Date = new Date()

  public numberOfSubjobs = 0

  constructor(name: string) {
    this._name = name

    this.debugMode = true
  }

  get name() {
    return this._name
  }

  get logMessages() {
    return this._logMessages
  }

  get startTime() {
    return this._startTime
  }

  get finishTime() {
    return this._finishTime
  }

  set status(status: JobStatus) {
    if (this._status !== status) {
      this._status = status
      this.onStatusUpdate.forEach(update => update(this._status))
      if (this.debugMode) {
        this.debug(`Updated status: ${status}`)
      }
      this.addLogMessage({
        name: this.name,
        type: LogMessageType.info,
        output: `updated status to ${status}`,
      })
    }
  }

  get status(): JobStatus {
    return this._status
  }

  set progress(progress: number) {
    if (this._progress !== progress) {
      const oldProgress = this._progress
      this._progress = Math.min(100, Math.max(0, progress))
      this.onProgressUpdate.forEach(update =>
        update(this._progress, this._progress - oldProgress)
      )
      if (this.debugMode) {
        this.debug(`Updated progress: ${progress}`)
      }
      this.addLogMessage({
        name: this.name,
        type: LogMessageType.info,
        output: `updated progress to ${progress}`,
      })
    }
  }

  get progress(): number {
    return this._progress
  }

  public addLogMessage(logMessage: IJobLogMessage) {
    if (typeof logMessage.date === "undefined") {
      logMessage.date = new Date()
    }
    this._logMessages.push(logMessage)
    this.onLogMessageUpdate.forEach(update => update(logMessage))
  }

  protected clearLogs() {
    this._logMessages = []
  }

  public fromJSON(jobJson: Partial<JobJSON>, keys?: (keyof JobJSON)[]) {
    let keysToUpdate = Object.keys(jobJson)

    // If a keys array is provided -> only update corresponding properties
    if (Array.isArray(keys)) {
      keysToUpdate = keys
    }

    keysToUpdate = keysToUpdate.filter(key => key !== "type" && key !== "name")

    keysToUpdate.forEach(key => {
      // If Job property has fromJSON() function -> use it to revive property
      if (
        typeof this[key] !== "undefined" &&
        this[key] !== null &&
        typeof this[key].fromJSON === "function"
      ) {
        this[key] = this[key].fromJSON(jobJson[key])
      } else {
        this[key] = jobJson[key]
      }
    })

    this.addLogMessage({
      name: this.name,
      type: LogMessageType.info,
      output: `updated job from json`,
    })

    return this
  }

  public toJSON(): JobJSON {
    const returnObj = new JobJSON()

    Object.keys(returnObj).forEach((field: string) => {
      const value = (this as any)[field]
      if (
        typeof value !== "undefined" &&
        value !== null &&
        typeof value.toJSON === "function"
      ) {
        returnObj[field] = value.toJSON()
      } else {
        returnObj[field] = value
      }
    })

    return returnObj
  }
}
