import { Injectable, OnDestroy } from "@angular/core"
import reconnectingWebsocket from "reconnecting-websocket"
import { JobService } from "./job-service.service"

import {
  IJobLogMessage,
  JobMessageTypes,
  IJobDataMessage,
  IJobGetMessage,
  IJobRunMessage,
  IMessage,
  IJobStopMessage,
  IJobNewMessage,
  FlashMessageTypes,
  FlashMessage,
  IJobStatusMessage,
  JobListMessageTypes,
  IJobListGetMessage,
  IJobListGetTemplatesMessage,
  WebSocketAuthMessageTypes,
  IWebSocketAuthMessage,
  IJobDeleteMessage,
  IJobPartialDataMessage,
  ServerConfigGetMessage,
  MessageNew,
  ServerConfigDataMessage,
  ServerConfigMessageTypes,
  IGetDirTreeSignedURLType,
  IGetDirTreeSignedURL,
} from "../../../../shared/src/message-api"

import { FlashMessageService } from "./flash-message.service"
import { Subscription, BehaviorSubject } from "rxjs"
import { AuthService, getJwt } from "./auth.service"
import { ConfigService } from "./config.service"
import { ServerPublicConfigJSON } from "../../../../shared/src/config/server-config"
import { FileLocation } from "../../../../shared/src/file-provider/interfaces"
import { PromiseMap } from "../../../../shared/src/util/promise"

/**
 * Handles communication with master server via web-socket.
 * All communication except file transfer and initial login is done through the web-socket:
 * - Fetch/set server config
 * - Fetch job/template lists
 * - Create/Load/Run/Stop/Delete job
 * - Get signed URLs for cloud storage
 * - Receive flash (toast) message from server
 *
 * @export
 * @class WebSocketService
 */
@Injectable({
  providedIn: "root",
})
export class WebSocketService implements OnDestroy {
  private rws: reconnectingWebsocket

  private _connected = false

  private _authenticated = false
  private authenticatedSubject = new BehaviorSubject(false)

  private updateAvailableJobsInterval = null

  private loginSubscription: Subscription
  private serverConfigSubject: BehaviorSubject<
    ServerPublicConfigJSON
  > = new BehaviorSubject(undefined)
  private serverConfigSubscription: Subscription

  private replyPromises: PromiseMap<string, string> = new Map()

  constructor(
    private jobService: JobService,
    private flashMessageSerice: FlashMessageService,
    private authService: AuthService,
    private configService: ConfigService
  ) {
    // Open connection on login and close connection on logout
    this.loginSubscription = this.authService
      .getLoginSubjectAsObservable()
      .subscribe(isLoggedIn =>
        isLoggedIn ? this.openConnection() : this.closeConnection()
      )

    this.serverConfigSubscription = this.serverConfigSubject.subscribe(
      serverConfig => {
        this.configService.serverConfig = serverConfig
      }
    )

    this.jobService.resolveDirTree = this.resolveDirTree.bind(this)
  }

  ngOnDestroy(): void {
    this.loginSubscription.unsubscribe()
    this.serverConfigSubscription.unsubscribe()
  }

  private openConnection() {
    console.log("opening websocket connection")
    this.rws = new reconnectingWebsocket(
      this.configService.config.MASTER_WEBSOCKET_URL,
      "",
      {
        maxReconnectionDelay: 5000,
        minReconnectionDelay: 500,
        minUptime: 5000,
        reconnectionDelayGrowFactor: 1.3,
        connectionTimeout: 4000,
      }
    )

    this.rws.addEventListener("open", this.onConnectionOpen.bind(this))
    this.rws.addEventListener("close", this.onConnectionClose.bind(this))
    this.rws.addEventListener("message", this.onMessage.bind(this))
  }

  private closeConnection() {
    if (typeof this.rws !== "undefined") {
      console.log("closing websocket connection")
      try {
        this.rws.close()
        this.rws.removeEventListener("message", this.onMessage.bind(this))
      } catch (error) {
        console.log(`FAILED to close websocket connection: ${error}`)
      }
    }
  }

  private onConnectionOpen(evt): any {
    this._connected = true
    this.authenticate()
    console.log(`opened ${this.rws.url}`)

    this.rws.removeEventListener("open", this.onConnectionOpen.bind(this))
  }

  private onConnectionClose(evt): any {
    this._connected = false
    this._authenticated = false
    this.authenticatedSubject.next(false)

    console.log(`closed ${this.rws.url}`)

    this.rws.removeEventListener("close", this.onConnectionClose.bind(this))
  }

  public authenticate() {
    this._authenticated = false
    this.authenticatedSubject.next(false)
    if (this._connected && this.authService.isLoggedIn) {
      this.rws.send(
        JSON.stringify({
          type: WebSocketAuthMessageTypes.AuthLogin,
          data: {
            message: getJwt(),
          } as IWebSocketAuthMessage,
        })
      )
    }
  }

  private onAuthenticated() {
    this._authenticated = true
    this.authenticatedSubject.next(true)

    this.getServerConfig()

    this.updateAvailableJobLists()

    // If the connection has opened and a job has been previously loaded -> reload job
    const currentJob = this.jobService.getCurrentJob()
    if (typeof currentJob !== "undefined") {
      this.getJob({ name: currentJob.name })
    }
  }

  private onMessage(evt) {
    let message: any = {}
    try {
      message = JSON.parse(evt.data)
      if (this.configService.debugMode) {
        console.log(`received message ${evt.data}`)
      }
    } catch (e) {
      console.log(`${this.rws.url} failed to parse message - ${evt.data}`)
    }

    if ("type" in message) {
      switch (message.type) {
        // START WEBSOCKET AUTHENTICATION MESSAGES
        case WebSocketAuthMessageTypes.AuthSuccess:
          this.onAuthenticated()
          break
        case WebSocketAuthMessageTypes.AuthFailed:
        case WebSocketAuthMessageTypes.AuthInvalid:
          this._authenticated = false
          this.authenticatedSubject.next(false)
          const authFailedMesage: IWebSocketAuthMessage = message.data

          this.flashMessageSerice.pushFlashMessage(
            new FlashMessage({
              title: authFailedMesage.message,
              sticky: true,
              message:
                "Your authentication token is invalid. Please log in again.",
            })
          )
          break

        // START JOB MESSAGES
        case JobMessageTypes.Data:
          const jobMessage: IJobDataMessage = message.data
          this.jobService.setCurrentJob(jobMessage.job)
          break
        case JobMessageTypes.PartialData:
          const jobPartialDataMessage: IJobPartialDataMessage = message.data
          this.jobService.updateCurrentJob(jobPartialDataMessage.jobData)
          break
        case JobMessageTypes.Status:
          const statusMessage: IJobStatusMessage = message.data
          const currentJob = this.jobService.getCurrentJob()
          if (typeof currentJob !== "undefined") {
            currentJob.status = statusMessage.status
            if ("progress" in statusMessage) {
              currentJob.progress = statusMessage.progress
            }
            this.jobService.setCurrentJob(currentJob)
          }
          break
        case JobMessageTypes.Log:
          const logMessage: IJobLogMessage = message.data
          this.jobService.pushToLogs(logMessage)
          break
        case JobListMessageTypes.Get:
          const jobListMessage: IJobListGetMessage = message.data
          this.jobService.setAvailableJob(jobListMessage.list)
          break
        case JobListMessageTypes.GetTemplate:
          const jobTemplatesListMessage: IJobListGetTemplatesMessage =
            message.data
          this.jobService.setAvailableJobTemplates(jobTemplatesListMessage.list)
          break

        // START FLASH MESSAGE MESSAGES
        case FlashMessageTypes.General:
          this.flashMessageSerice.pushFlashMessage(message.data as FlashMessage)
          break

        // START CONFIG MESSAGES
        case ServerConfigMessageTypes.Data:
          const serverConfigMessage = new ServerConfigDataMessage(message.data)
          console.log(serverConfigMessage)
          this.serverConfigSubject.next(serverConfigMessage.config)
          break

        // START MISC MESSAGES
        case IGetDirTreeSignedURLType:
          const getDirTreeSignedUrlMessage = message.data as IGetDirTreeSignedURL
          const promiseId = `${IGetDirTreeSignedURLType}-${
            getDirTreeSignedUrlMessage.location
          }-${getDirTreeSignedUrlMessage.path}`
          if (this.replyPromises.has(promiseId)) {
            this.replyPromises
              .get(promiseId)
              .resolve(getDirTreeSignedUrlMessage.url)
          }
          break

        // UNKOWN MESSAGES
        default:
          console.log(`Received unknown message ${JSON.stringify(message)}`)
          break
      }
    }
  }

  private sendMessage(message: IMessage | MessageNew) {
    if (this._authenticated) {
      this.rws.send(JSON.stringify(message))

      if (this.configService.debugMode) {
        console.log(`sent message ${JSON.stringify(message)}`)
      }
    }
  }

  public getJob(message: IJobGetMessage) {
    this.sendMessage({ type: JobMessageTypes.Get, data: message })
  }

  public runJob(message: IJobRunMessage) {
    this.sendMessage({ type: JobMessageTypes.Run, data: message })
  }

  public saveJob(message: IJobDataMessage) {
    this.sendMessage({ type: JobMessageTypes.Data, data: message })
  }

  public stopJob(message: IJobStopMessage) {
    this.sendMessage({ type: JobMessageTypes.Stop, data: message })
  }

  public newJob(message: IJobNewMessage) {
    this.sendMessage({ type: JobMessageTypes.New, data: message })
  }

  public deleteJob(message: IJobDeleteMessage) {
    if (this.authService.isAdmin) {
      this.sendMessage({ type: JobMessageTypes.Delete, data: message })
    }
  }

  public updateAvailableJobLists(interval = -1) {
    if (interval > 0 && this.updateAvailableJobsInterval === null) {
      this.updateAvailableJobsInterval = setInterval(() => {
        this.updateAvailableJobLists(-1)
      }, interval)
    }

    this.sendMessage({ type: JobListMessageTypes.Get, data: {} })
    this.sendMessage({ type: JobListMessageTypes.GetTemplate, data: {} })
  }

  public clearUpdateAvailableJobListInterval() {
    if (this.updateAvailableJobsInterval !== null) {
      clearInterval(this.updateAvailableJobsInterval)
      this.updateAvailableJobsInterval = null
    }
  }

  private getServerConfig() {
    this.sendMessage(new ServerConfigGetMessage())
  }

  public updateServerConfig() {
    if (this.authService.isAdmin) {
      this.sendMessage(
        new ServerConfigDataMessage({ config: this.configService.serverConfig })
      )
    }
  }

  public getFileSignedUrl(
    path: string,
    location: FileLocation
  ): Promise<string> {
    return new Promise((resolve, reject) => {
      this.replyPromises.set(
        `${IGetDirTreeSignedURLType}-${location}-${path}`,
        { resolve, reject }
      )
      this.sendMessage({
        type: IGetDirTreeSignedURLType,
        data: { path, location },
      })
    })
  }

  public async resolveDirTree(
    path: string,
    location: FileLocation,
    publicCloud = false
  ) {
    switch (location) {
      case FileLocation.MasterPublic:
        return `${this.configService.config.MASTER_WEBSERVER_URL}${
          this.configService.serverConfig.API_PUBLIC_FILES_URL
        }${path}`
      case FileLocation.MasterPrivate:
        return `${this.configService.config.MASTER_WEBSERVER_URL}${
          this.configService.serverConfig.API_PRIVATE_FILES_URL
        }${path}`
      case FileLocation.MasterJob:
        return `${this.configService.config.MASTER_WEBSERVER_URL}${
          this.configService.serverConfig.API_JOB_FILES_URL
        }${path}`
      case FileLocation.MasterTemplate:
        return `${process.env.MASTER_RESTAPI_URL}${
          this.configService.serverConfig.API_TEMPLATE_FILES_URL
        }${path}`
      case FileLocation.S3:
        return publicCloud
          ? `${this.configService.serverConfig.S3_JOB_FILES_BUCKET_URL}${path}`
          : ""
      case FileLocation.GCS:
        return await this.getFileSignedUrl(path, location)
      case FileLocation.URL:
        return `${path}`
    }
  }

  public getServerConfigSubject() {
    return this.serverConfigSubject
  }

  public getAuthenticatedAsObservable() {
    return this.authenticatedSubject.asObservable()
  }
}
