import { plot as plottyPlot } from "plotty"
import { format as d3Format, color, min as d3Min, max as d3Max, max } from "d3"
import { MapLayer, ILayerSpec } from "./map-layer"
import { D3ColourScheme, ColourScheme } from "../colour-scheme/colour-scheme"
import { WebWorkerErrorMessages } from "../../../util/web-workers/web-worker"
import { CanvasOverlayLayer } from "./canvas-layer"
import { toMercator } from "@turf/turf"
import { MousePoint } from "./layer-service.service"
import {
  projectCoords,
  EPSG4326,
  EPSG3857,
} from "../../../../../../shared/src/util/geospatial-projections"

// Created for georaster library
export interface Georaster {
  rasterType: "geotiff" | "object"
  sourceType: "url" | "Buffer" | "ArrayBuffer"
  values: number[][][]
  height: number
  width: number
  pixelHeight: number
  pixelWidth: number
  projection: number
  noDataValue: number
  numberOfRasters: number
  xmin: number
  ymax: number
  xmax: number
  ymin: number
  maxs: number[]
  mins: number[]
  ranges: number[]
}

export interface RasterLayerData extends Georaster {
  geotiffArrayBuffer: ArrayBuffer
  valuesFlat: number[][]
  bbox4326: [number, number, number, number]
}

export interface IRasterLayerSpec extends ILayerSpec {
  noDataValue?: number
  projectionOverride?: number
}

export type RasterLayerDataInput =
  | RasterLayerData
  | (() => Promise<RasterLayerData>)
  | string

export class RasterLayer extends CanvasOverlayLayer
  implements IRasterLayerSpec {
  private _data: RasterLayerData
  private _getDataFn: () => Promise<RasterLayerData>

  public colourScheme: D3ColourScheme
  public valueFormatFn: (d: number) => string = d3Format(".2f")

  public projectionOverride?: number

  constructor(
    name: string,
    data?: RasterLayerDataInput,
    private _band: number = 0
  ) {
    super(name)

    this._type = "RasterLayer"

    this.setData(data)

    this.colourScheme = new D3ColourScheme("interpolateInferno")
  }

  public async setData(data: RasterLayerDataInput) {
    delete this._data
    if (typeof data === "function") {
      this._getDataFn = data
    } else if (typeof data === "string") {
      this._getDataFn = () =>
        MapLayer.layerService.getGeotiffData(
          data,
          this.name,
          this.projectionOverride
        )
    } else {
      this._data = data
    }

    if (this.visible) {
      await this.render(true)
    }
  }

  get data() {
    return this._data
  }

  getBbox(): [number, number, number, number] {
    if (typeof this.data === "undefined") {
      return undefined
    }
    return this.data.bbox4326
  }

  public async init() {
    if (typeof this._data === "undefined") {
      try {
        this._data = await this._getDataFn()

        // If minValue/maxValue wasn't set manually -> use min/max pixel values
        if (
          typeof this.maxValue === "undefined" ||
          typeof this.minValue === "undefined"
        ) {
          // Unfortunately - georaster's min/max values are sometimes wrong (that is this._data.mins/maxs
          let maxValue = d3Max(this._data.valuesFlat[this._band])
          let minValue = d3Min(this._data.valuesFlat[this._band])

          // Check if nodatavalue is min or max -> and then set min/max pixel values accordingly
          if (this.data.noDataValue === minValue) {
            minValue = d3Min(this._data.valuesFlat[this._band], d =>
              d <= this.data.noDataValue ? maxValue : d
            )
          }

          if (this.data.noDataValue === maxValue) {
            maxValue = d3Max(this._data.valuesFlat[this._band], d =>
              d >= this.data.noDataValue ? minValue : d
            )
          }

          if (typeof this.maxValue === "undefined") {
            this.maxValue = maxValue
          }

          if (typeof this.minValue === "undefined") {
            this.minValue = minValue
          }
        }
      } catch (error) {
        if (error !== WebWorkerErrorMessages.WorkerTerminated) {
          console.log(
            `Error occurred while initialising layer ${this.name} (${error})`
          )
          console.log(error)
        }
        throw error
      }
    }
  }

  public async render(force = false) {
    if ((this._rendered && !force) || this._rendering) {
      return
    }
    this._rendering = true

    try {
      await this.init()
    } catch (error) {
      this._rendering = false
      return
    }

    const plot = new plottyPlot({
      domain: [this.minValue, this.maxValue],
      canvas: this._rootElement.node(),
      data: this._data.valuesFlat[this._band],
      width: this._data.width,
      height: this._data.height,

      colorScale: this.colourScheme.toPlotty(),
    })

    plot.setNoDataValue(this.data.noDataValue)
    plot.setClamp(false, true)
    plot.render()

    await super.render(force)

    this._rendering = false
    this._rendered = true
  }

  public clear(): Promise<void> {
    super.clear()
    return new Promise(async (resolve, reject) => {
      if (this._rendered) {
        await this.hide()
        setTimeout(() => {
          this._canvasContext.clearRect(
            0,
            0,
            this._data.width,
            this._data.height
          )
          // this._data.pixels = [] ?????

          // how to clear georaster

          this._initialised = false
          resolve()
        }, 500)
      } else {
        resolve()
      }
    })
  }

  public hide(): Promise<void> {
    super.hide()
    return new Promise(async (resolve, reject) => {
      if (this._rendered) {
        this._rootElement
          .transition()
          .duration(400)
          .style("opacity", 0)
          .on("end", () => {
            resolve()
          })
      }
    })
  }

  public async show() {
    if (!this._rendered) {
      await this.render()
    }
    await super.show()
  }

  public getValueAtPoint(point: MousePoint) {
    if (typeof this._data !== "undefined") {
      // Adapted from https://github.com/stuartmatthews/leaflet-geotiff/blob/master/leaflet-geotiff.js

      const EPSG3857coords = projectCoords(
        [point.latlng.lng, point.latlng.lat],
        EPSG4326,
        EPSG3857
      )

      const x = Math.floor(
        (EPSG3857coords[0] - this.data.xmin) / this.data.pixelWidth
      )
      const y = Math.floor(
        (this.data.ymax - EPSG3857coords[1]) / this.data.pixelHeight
      )

      // If x and y are outside range return undefined
      if (x < 0 || x > this._data.width || y < 0 || y > this._data.height) {
        return undefined
      }

      // const i = y * this._data.width + x
      const value = this._data.values[this._band][y][x]

      if (value !== this.data.noDataValue) {
        return this.valueFormatFn(value)
      }
    }

    return undefined
  }
}

export interface RasterMultiLayerDimension {
  label: string
  key: string
  options: { label: string; value: string }[]
  selected: { label: string; value: string }
}

export type RasterMultiLayerDimensionsMap = {
  [key: string]: RasterMultiLayerDimension
}

export class RasterMultiLayer extends RasterLayer {
  public static isInstanceOf(obj: any): obj is RasterMultiLayer {
    return obj.type === "RasterMultiLayer"
  }

  constructor(
    name: string,
    private _dimensions: RasterMultiLayerDimensionsMap,
    private _layerMap: (
      dimensions: RasterMultiLayerDimensionsMap
    ) => RasterLayerDataInput
  ) {
    super(name, _layerMap(_dimensions))

    this._type = "RasterMultiLayer"

    this.disableFilter = true
  }

  get layerMap() {
    return this._layerMap
  }

  get dimensions() {
    return this._dimensions
  }

  get dimensionsArray() {
    return Object.values(this._dimensions)
  }

  async updateLayer(updateLinked = true) {
    const newData = this._layerMap(this._dimensions)
    await this.setData(newData)
    if (updateLinked) {
      await Promise.all(
        this.linkedLayers.map(layer => layer.updateLayer(false))
      )
    }
  }
}

import { GPU, IKernelRunShortcut, KernelFunction } from "gpu.js"
import {
  concatenate,
  flattenArray,
} from "../../../../../../shared/src/util/array"

export class RasterExpressionLayer extends RasterLayer {
  private _layerUpdating = false

  constructor(
    name: string,
    private _layers: RasterLayer[],
    private _kernalFn: KernelFunction
  ) {
    super(name)

    this.setData(this.calc)

    this._type = "RasterExpressionLayer"

    if (RasterMultiLayer.isInstanceOf(_layers[0])) {
      this.disableFilter = true
    }
  }

  async calc() {
    await Promise.all(this._layers.map(layer => layer.init()))

    const gpu = new GPU()

    const kernal = gpu
      .createKernel(this._kernalFn)
      .setOutput([this._layers[0].data.width, this._layers[0].data.height])

    const results = kernal(
      this._layers.map(layer => layer.data.values[0])
    ) as number[][]

    gpu.destroy()

    const newRasterLayerData: RasterLayerData = Object.assign(
      {},
      this._layers[0].data
    )

    newRasterLayerData.geotiffArrayBuffer = undefined
    newRasterLayerData.values = [results]
    newRasterLayerData.valuesFlat = newRasterLayerData.values.map(values =>
      concatenate(Float32Array, ...values)
    )

    return newRasterLayerData
  }

  get dimensions() {
    if (RasterMultiLayer.isInstanceOf(this._layers[0])) {
      return (this._layers[0] as RasterMultiLayer).dimensions
    }
    return undefined
  }

  get dimensionsArray() {
    if (RasterMultiLayer.isInstanceOf(this._layers[0])) {
      return Object.values((this._layers[0] as RasterMultiLayer).dimensions)
    }
    return undefined
  }

  async updateLayer(updateLinked = true) {
    if (this._layerUpdating || this._rendering) {
      return
    }
    this._layerUpdating = true

    await Promise.all(
      this._layers.map(async layer => {
        if (RasterMultiLayer.isInstanceOf(layer)) {
          await layer.updateLayer(false)
        }
      })
    )

    await this.setData(this.calc)

    this._layerUpdating = false
  }
}
