import { Injectable } from "@angular/core"

import { Observable, Subject } from "rxjs"
import { FlashMessage } from "../../../../shared/src/message-api"

/**
 * Provides subject to push flash messages (toast messages)
 *
 * @export
 * @class FlashMessageService
 */
@Injectable({
  providedIn: "root",
})
export class FlashMessageService {
  private flashMessageSubject = new Subject<FlashMessage>()

  constructor() {}

  getFlashMessageObservable(): Observable<FlashMessage> {
    return this.flashMessageSubject.asObservable()
  }
  pushFlashMessage(flashMessage: FlashMessage): void {
    this.flashMessageSubject.next(flashMessage)
  }
}
