import { Injectable, OnDestroy, Injector } from "@angular/core"

import { Observable, Subject, Subscription, BehaviorSubject } from "rxjs"
import {
  IJobLogMessage,
  JobListItem,
  JobTemplateListItem,
} from "../../../../shared/src/message-api"
import { AuthService } from "./auth.service"
import { JobJSON } from "../../../../shared/src/job/job-base"
import { ConfigService } from "./config.service"

import { jobClassMap } from "../jobs/job-class-map"
import { ClientJob } from "../jobs/job"
import { LayerService } from "../geo-web/map/map-layer/layer-service.service"
import {
  FileLocation,
  DirectoryTree,
} from "../../../../shared/src/file-provider/interfaces"

/**
 * Contains all job related variables, job/tempalte lists and subjects to observe changes to jobs.
 * These variables are set by WebSocketService
 *
 * @export
 * @class JobService
 */
@Injectable({
  providedIn: "root",
})
export class JobService implements OnDestroy {
  private behaviorSubject = new BehaviorSubject<ClientJob>(undefined)
  private jobSubscription: Subscription
  private currentJob: ClientJob

  private _logs: IJobLogMessage[] = []
  private logsSubject = new Subject<IJobLogMessage[]>()

  private _availableJobs: JobListItem[] = []
  private availableJobsSubject = new Subject<JobListItem[]>()

  private _availableJobTemplates: JobTemplateListItem[] = []
  private availableJobTemplatesSubject = new Subject<JobTemplateListItem[]>()

  private _availableExtraFiles: DirectoryTree
  private availableExtraFilesSubject = new Subject<DirectoryTree>()

  private loginSubscription: Subscription

  /**
   *
   * This is set by websocketservice :(
   */
  public resolveDirTree: (
    path: string,
    location: FileLocation,
    publicCloud?: boolean
  ) => Promise<string>

  constructor(
    private authService: AuthService,
    private configService: ConfigService,
    private injector: Injector
  ) {
    this.jobSubscription = this.behaviorSubject.subscribe(job => {
      this.currentJob = job
    })
    this.loginSubscription = this.authService
      .getLoginSubjectAsObservable()
      .subscribe(loggedIn => {
        if (!loggedIn && typeof this.currentJob !== "undefined") {
          this.setCurrentJob(undefined)
        }
      })
  }

  ngOnDestroy(): void {
    this.jobSubscription.unsubscribe()
    this.loginSubscription.unsubscribe()
  }

  getCurrentJob(): ClientJob {
    return this.currentJob
  }

  getCurrentJobBehaviourSubject(): BehaviorSubject<ClientJob> {
    return this.behaviorSubject
  }

  getCurrentJobObservable(): Observable<ClientJob> {
    return this.behaviorSubject.asObservable()
  }

  updateCurrentJob(jobData: Partial<JobJSON>) {
    // Object.assign(this.currentJob, jobData)
    this.currentJob.fromJSON(jobData)

    if (this.configService.debugMode) {
      console.log("updateCurrentJob")
      console.log(jobData)
    }

    this.behaviorSubject.next(this.currentJob)
  }

  setCurrentJob(job: ClientJob | JobJSON): void {
    if (typeof job === "undefined") {
      this.behaviorSubject.next(undefined)
    } else if (
      typeof this.currentJob !== "undefined" &&
      this.currentJob.name === job.name
    ) {
      this.updateCurrentJob(job)
    } else if (job instanceof ClientJob) {
      this.behaviorSubject.next(job)
    } else {
      const newJob = new (jobClassMap(job.type))(
        job.name,
        this,
        this.configService,
        this.injector.get(LayerService)
      )
      newJob.fromJSON(job)
      this.behaviorSubject.next(newJob)
    }
    // TODO: if job is being updated (and not changed) only update properties (and detect changes?)
    if (this.configService.debugMode) {
      console.log(this.currentJob)
    }
  }

  public getLogs(): IJobLogMessage[] {
    return this._logs
  }

  public getLogsObservable(): Observable<IJobLogMessage[]> {
    return this.logsSubject.asObservable()
  }

  public pushToLogs(log: IJobLogMessage) {
    if (this._logs.length > 1000) {
      this._logs.shift()
    }
    this._logs.push(log)
    this.logsSubject.next(this._logs)
  }

  public clearLogs() {
    this._logs = []
    this.logsSubject.next(this._logs)
  }

  public getAvailableJobs(): JobListItem[] {
    return this._availableJobs
  }

  public getAvailableJobsObservable(): Observable<JobListItem[]> {
    return this.availableJobsSubject.asObservable()
  }

  public setAvailableJob(jobs: JobListItem[]) {
    this._availableJobs = jobs
    this.availableJobsSubject.next(this._availableJobs)
  }

  public getAvailableJobTemplates(): JobTemplateListItem[] {
    return this._availableJobTemplates
  }

  public getAvailableJobTemplatesObservable(): Observable<
    JobTemplateListItem[]
  > {
    return this.availableJobTemplatesSubject.asObservable()
  }

  public setAvailableJobTemplates(jobTemplates: JobTemplateListItem[]) {
    this._availableJobTemplates = jobTemplates
    this.availableJobTemplatesSubject.next(this._availableJobTemplates)
  }

  public getAvailableExtraFiles(): DirectoryTree {
    return this._availableExtraFiles
  }

  public getAvailableExtraFilesObservable(): Observable<DirectoryTree> {
    return this.availableExtraFilesSubject.asObservable()
  }

  public setAvailableExtraFiles(extraFiles: DirectoryTree): any {
    this._availableExtraFiles = extraFiles
    this.availableExtraFilesSubject.next(this._availableExtraFiles)
  }
}
