class SparkMeteorologicalInputLayerFormConfig {
  key: string = ""
  label: string = ""

  constructor(obj: SparkMeteorologicalInputLayerFormConfig) {
    Object.assign(this, obj)
  }
}

class SparkInputLayerFormConfig {
  key: string = ""
  label: string = ""
  defaultValueDisabled: boolean = false
  interpolationDisabled: boolean = false
  modelNameDisabled: boolean = false

  constructor(obj: Partial<SparkInputLayerFormConfig>) {
    Object.assign(this, obj)
  }
}

function generateInputLayerFormModels(
  layer: SparkInputLayerFormConfig,
  defaultValues: Partial<InputLayer> = {}
): DynamicFormControlModel[] {
  return [
    // new FileBrowserInputModel({
    //   id: `file`,
    //   label: `File`,
    //   options: { selectableType: "file" },
    // }),
    new DynamicInputModel({
      id: `projection`,
      label: "Projection (WKT)",
      value: defaultValues.projection,
    }),
    new DynamicInputModel({
      id: `defaultValue`,
      label: "Default value",
      validators: {
        required: null,
        customValidator: floatValidator,
      },
      errorMessages: {
        required: "{{ label }} is required",
        customValidator: floatValidator.errorMessage,
      },
      disabled: layer.defaultValueDisabled,
      value: defaultValues.defaultValue,
    }),
    new DynamicSelectModel({
      id: `interpolation`,
      label: "Interpolation",
      options: [
        {
          label: "Nearest",
          value: InputLayerInterpolation.Nearest,
        },
        {
          label: "Bilinear",
          value: InputLayerInterpolation.Bilinear,
        },
        {
          label: "Bicubic",
          value: InputLayerInterpolation.Bicubic,
        },
      ],
      validators: {
        required: null,
      },
      errorMessages: {
        required: "{{ label }} is required",
      },
      disabled: layer.interpolationDisabled,
      value: defaultValues.interpolation,
    }),
    new DynamicInputModel({
      id: `modelName`,
      label: "Model name",
      disabled: layer.modelNameDisabled,
      value: defaultValues.modelName,
      validators: {
        required: null,
      },
      errorMessages: {
        required: "{{ label }} is required",
      },
    }),
  ]
}

