import { MapLayer } from "./map-layer"

import { format as d3Format } from "d3"

import { contours as d3Contours } from "d3-contour"
import {
  MultiPolygon,
  Polygon,
  feature,
  Feature,
  featureCollection,
  coordEach,
  FeatureCollection,
} from "@turf/turf"
import { SolidColourScheme, ColourScheme } from "../colour-scheme/colour-scheme"

import {
  projectBBox,
  EPSG4326,
  EPSG3857,
  projectCoords,
} from "../../../../../../shared/src/util/geospatial-projections"
import { RasterLayer } from "./map-geotiff-layer"
import { ITimeSliderSubscriber, TimeSlider } from "./time-slider"
import { downloadFile } from "../../../util/download-file"
import { MapboxGlLayer } from "./mapbox-gl-layer"
import { GeoJSONSourceRaw, GeoJSONSource } from "mapbox-gl"
export class MapContourLayer extends MapboxGlLayer
  implements ITimeSliderSubscriber {
  private _pixels: number[]
  private _pixelsReversed: number[]
  private _geotiffWidth: number
  private _geotiffHeight: number
  private _bbox4326: [number, number, number, number]
  private _width3857: number
  private _height3857: number
  private _bbox3857: [number, number, number, number]

  private _data: Feature<MultiPolygon | Polygon>[]
  private _fc: FeatureCollection = featureCollection([])

  private _geotiffLayer: RasterLayer

  public contourCacheSteps = 500
  private _contourCache: Feature<MultiPolygon | Polygon>[]

  public transitionDelayMultiplier = 0

  public thresholds = [0]
  public colourScheme: ColourScheme
  public valueFormatFn: (d: number) => string = d3Format(".2f")

  private _slider: TimeSlider
  private _rendering = false
  private _missedRenderUpdate = false

  private _mapboxId: string

  protected _source: { id: string; source: GeoJSONSourceRaw }

  public get slider(): TimeSlider {
    return this._slider
  }

  public set slider(value: TimeSlider) {
    if (typeof this._slider !== "undefined") {
      this._slider.subscribers.delete(this)
    }
    this._slider = value
    this._slider.subscribers.add(this)
  }

  public sliderEnabled = false

  onSliderValueChange(value: number, date: Date): void {
    if (this.sliderEnabled) {
      this.thresholds = [value]
      MapLayer.layerService.zone.runOutsideAngular(
        (() => {
          this.render()
        }).bind(this)
      )
    }
  }

  constructor(
    name: string,
    geotiffLayer: RasterLayer,
    private _layerStyleType: "fill" | "line" = "line"
  ) {
    super(name)

    this._type = "GeowebMapContourLayer"

    this._geotiffLayer = geotiffLayer

    this.colourScheme = new SolidColourScheme("#000000")

    this.colourByProperty = { key: "value", label: "Value" }

    this.showValueInPopup = false

    this.downloadData = {
      action: this.downloadGeoJSON.bind(this),
      description: `GeoJSON of ${this.name} polygons`,
      metadata: "",
      disabled: true,
    }

    this._mapboxId = `${this._type}-${name}`.replace(" ", "-").toLowerCase()

    this._mapboxGlLayer = {
      id: this._mapboxId,
      type: this._layerStyleType,
      source: this._mapboxId,
      paint:
        this._layerStyleType === "line"
          ? {
              "line-width": 2,
            }
          : {},
    }

    this._source = {
      id: this._mapboxId,

      source: {
        type: "geojson",
        data: this._fc,
        tolerance: 1,
      },
    } as any
  }

  private downloadGeoJSON() {
    downloadFile(`${this.name}.json`, JSON.stringify(this._fc), "text/json")
  }

  private getContourPolygons(thresholds: number[]) {
    const contours: d3.ContourMultiPolygon[] = d3Contours()
      .size([this._geotiffWidth, this._geotiffHeight])
      .thresholds(thresholds)(this._pixelsReversed)

    const features: Feature<MultiPolygon>[] = []

    // The contour feature coordinates represent the pixel locations of the input GeoTIFF (which is EPSG3857)
    // So to get ESPG4326 we must go from pixel value -> EPSG3857 -> ESPG4326
    for (let i = 0; i < contours.length; i++) {
      const f = feature(contours[i])
      f.properties.value = contours[i].value
      coordEach(f, coord => {
        ;[coord[0], coord[1]] = projectCoords([
          this._width3857 * (coord[0] / this._geotiffWidth) + this._bbox3857[0],
          this._bbox3857[3] -
            this._height3857 * (coord[1] / this._geotiffHeight),
        ])
      })

      features.push(f)
    }

    return features
  }

  private getContours(
    thresholds = this.thresholds
  ): Feature<MultiPolygon | Polygon>[] {
    const reversedThresholds = thresholds.map(
      threshold => this.maxValue - threshold
    )

    if (reversedThresholds.length === 1) {
      const cacheIdx = Math.round(
        (reversedThresholds[0] / this.maxValue) * this.contourCacheSteps
      )
      if (typeof this._contourCache[cacheIdx] === "undefined") {
        const contours = this.getContourPolygons(reversedThresholds)
        this._contourCache[cacheIdx] = contours[0]
      }

      return [this._contourCache[cacheIdx]]
    }

    return this.getContourPolygons(reversedThresholds)
  }

  public async render(force: boolean = false) {
    if (this._rendering) {
      return
    }
    this._rendering = true

    if (!this._initialised) {
      await this._geotiffLayer.init()

      this._pixels = this._geotiffLayer.data.valuesFlat[0]

      // reverse pixel values
      this._pixelsReversed = this._pixels.map(pixel => {
        if (pixel >= 0) {
          return this._geotiffLayer.maxValue - pixel
        }
        return pixel
      })

      // Because we reverse pixel values -> we must reverse legend labels
      this.legendCells = this.thresholds
      this.legendLabels = this.legendCells
        .map(cell => `${this.valueFormatFn(cell)}`)
        .reverse()

      this.reverseLegendLabels = true

      this._geotiffWidth = this._geotiffLayer.data.width
      this._geotiffHeight = this._geotiffLayer.data.height

      this._bbox4326 = this._geotiffLayer.data.bbox4326
      this._bbox3857 = projectBBox(this._bbox4326, EPSG4326, EPSG3857) as [
        number,
        number,
        number,
        number
      ]

      this._width3857 = this._bbox3857[2] - this._bbox3857[0]
      this._height3857 = this._bbox3857[3] - this._bbox3857[1]

      this.minValue = this._geotiffLayer.minValue
      this.maxValue = this._geotiffLayer.maxValue
      this._contourCache = new Array<Feature<MultiPolygon | Polygon>>(
        this.contourCacheSteps
      )
    }

    this._data = this.getContours()
    this._fc.features = this._data

    if (!this._initialised) {
      this._source.source.data = this._fc as any
      await super.render(force)
    } else {
      await super.render(force)
      await new Promise(resolve => {
        const animate = (() => {
          if (!MapLayer.layerService.mapboxGl.isSourceLoaded(this._source.id)) {
            return requestAnimationFrame(animate) // wait until the next frame
          }
          ;(MapLayer.layerService.mapboxGl.getSource(
            this._source.id
          ) as GeoJSONSource).setData(this._fc as any)
          resolve()
        }).bind(this)
        animate()
      })
    }

    this._rendering = false

    this.downloadData.disabled = false
  }
}
