import { FeatureCollection, featureCollection, Geometry } from "@turf/turf"
import { JSONSerialiser } from "../../job-base"
import {
  DirectoryTreeFile,
  DirectoryTreeDirectory,
} from "../../../file-provider/interfaces"

export enum InputLayerInterpolation {
  Nearest = 0,
  Bilinear = 1,
  Bicubic = 2,
}

export class InputLayer<ModelName = string> {
  file?: DirectoryTreeFile
  projection?: string
  defaultValue: number = 0
  interpolation: InputLayerInterpolation = 0
  modelName: ModelName

  constructor(modelName, obj: Partial<InputLayer> = {}) {
    Object.assign(this, obj)
    this.modelName = modelName
  }
}

export class MeteorologicalInputTransform {
  scaleFactor: number = 1
  valueOffset: number = 0
}

export class MeteorologicalInputLayer {
  file?: DirectoryTreeFile
  directoryFilter?: string
  transform: MeteorologicalInputTransform

  constructor() {
    this.transform = new MeteorologicalInputTransform()
  }
}

export enum GeotiffType {
  Unknown,
  uInt8,
  uInt16,
  Int16,
  uInt32,
  Int32,
  Float32,
  Float64,
  ComplexInt16,
  ComplexInt32,
  ComplexFloat32,
  ComplexFloat64,
}

export enum OutputUnitType {
  Undefined,
  Time,
  Distance,
  Temperature,
  HeatFluxDensity,
  Percent,
  Degress,
  SpeedInMs,
  SpeedInKmh,
}

export enum ReductionType {
  None,
  Max,
  Min,
  Integral,
  Mean,
  Stdev,
  RMS,
  Sum,
  HeavisideIntegral,
  PerimiterLengthIntegral,
  Var,
  Covar,
  CountUnique,
  MaxIndexed,
}

export class SparkWorkspaceJobInputs
  implements JSONSerialiser<SparkWorkspaceJobInputs> {
  /**
   * If this is set, then the SparkWorkspace inputs will be overriden by values inside the XML file when the job object is created
   *
   */
  public loadSparkWorkspaceXML: DirectoryTreeFile | {} = {}

  private _startTime: Date = new Date()
  private _endTime: Date = new Date()

  get startTime(): Date {
    return this._startTime
  }

  set startTime(s: Date) {
    this._startTime = s
    // If the new starttime is after the current endtime -> set endtime to start time
    if (
      typeof this._endTime === "undefined" ||
      (typeof this._startTime.getTime === "function" &&
        typeof this._endTime.getTime === "function" &&
        this._endTime.getTime() < this._startTime.getTime())
    ) {
      this._endTime = this._startTime
    }
  }

  get endTime(): Date {
    return this._endTime
  }

  set endTime(e: Date) {
    this._endTime = e
    // If the new endtime is before the current starttime -> set starttime to endtime
    if (
      typeof this._startTime === "undefined" ||
      (typeof this._startTime.getTime === "function" &&
        typeof this._endTime.getTime === "function" &&
        this._startTime.getTime() > this._endTime.getTime())
    ) {
      this._startTime = this._endTime
    }
  }

  get simulationDuration(): number {
    if (
      typeof this._startTime !== "undefined" &&
      this._startTime !== null &&
      typeof this._startTime.getTime === "function" &&
      typeof this._endTime !== "undefined" &&
      this._endTime !== null &&
      typeof this._endTime.getTime === "function"
    ) {
      return (
        (this.endTime.getTime() - this.startTime.getTime()) / (60 * 60 * 1000)
      )
    }
    return -1
  }

  set simulationDuration(hours: number) {
    if (hours > 0) {
      this.endTime = new Date(this.startTime.getTime() + hours * 60 * 60 * 1000)
    }
  }

  // FIXME: remove simulation duration getter/setter
  public simulationDurationHours = ""

  public simulationResolution: number = 30
  public simulationProjection: string = "EPSG:28355"

  public featureCollections: {
    startSources: FeatureCollection<Geometry, { time: Date; radius?: number }>
    masks: FeatureCollection<Geometry, { time: Date; radius?: number }>
  } = {
    startSources: featureCollection([]),
    masks: featureCollection([]),
  }

  public startSourcesProjection = "EPSG:4326"
  public shapeMaskProjection = "EPSG:4326"
  public shapeMaskShapefile = ""
  public outputProjection: string = "EPSG:3857"

  public disableFileOutputs = false

  public outputDataRasterFilename?: string = ""
  public outputEnsembleRasterFilename?: string = ""

  public outputDataRasterType = ""
  public outputRasterFilename = ""
  public outputShapefileFilename = ""
  public outputSpotFireShapefileFilename = ""
  public outputHeader = ""
  public outputIsochroneTime = ""

  public outputPostgis = {
    appendScript: "",
    createScript: "",
    db: "",
    hostname: "",
    schema: "",
    table: "",
    user: "",
  }

  public initialisationScripts: {
    startC?: string
    startPython?: { script: string; files: { file: DirectoryTreeFile }[] }
    endC?: string
  } = {}
  public rateOfSpreadModels: { [key: number]: string } = {}

  public postProcessingScripts: { header?: string; [key: number]: string } = {}

  public geostackEnsemble: {
    seedStart: number
    seedEnd: number
    seedCsv?: DirectoryTreeFile
  } = {
    seedStart: 0,
    seedEnd: 0,
  }

  public bbox: {
    bbox: string[]
    res: string[]
    crs: string
    autoToggle: string
    buffer: string
    toggle: string
  } = {
    bbox: [],
    res: [],
    crs: "EPSG:4326",
    autoToggle: "",
    buffer: "",
    toggle: "",
  }

  public kmlOutput = { dir: "", timeStep: "", write: "" }

  public inputs: {
    meteorological: {
      layers: {
        windDirection: MeteorologicalInputLayer
        windSpeed: MeteorologicalInputLayer
        temperature: MeteorologicalInputLayer
        relativeHumidity: MeteorologicalInputLayer
        dewPointTemperature: MeteorologicalInputLayer
        droughtFactor: MeteorologicalInputLayer
      }
      baseDirectory?: DirectoryTreeDirectory
      startTimeOverride?: string
      timeConversionCoefficient: number
      projection: string
      windUseDirectionAndSpeed: boolean
    }

    terrain: {
      landClassification: InputLayer<"class">
      fireHistory: InputLayer<"fire_history">
      elevation: InputLayer<"elevation">
      fuelLoad: InputLayer<"fuel_load">
      curing: InputLayer<"curing">
    }
    custom: InputLayer[]
    series: {
      file?: DirectoryTreeFile
      timeZone?: string
      windRandomVariation: { speed: number; direction: number }
      pythonScript?: string
    }
    ensemble: {
      numSimulations?: number
      outputLayers?: number
      cScript?: string
      pythonScript?: string
      outputRasterType?: GeotiffType
      unitType?: OutputUnitType
      randomSeed?: number
      reduction?: {
        layers: { number?: number; type?: ReductionType }[]

        pythonScript?: string
      }
      startPointScript?: string
    }
    firebrand: {
      enabled?: boolean
      creationCScript?: string
      updateCScript?: string
      surfaceGrowthCScript?: string
      elevationLayerName?: string
      airbourneGrowth?: string
    }
    disruption: {
      shapeFile?: DirectoryTreeFile
      shapeFileProjection?: string
      clearState?: boolean
      overwriteClassification?: boolean
      overwriteClassificationValue?: number
      overwriteUserLayer?: boolean
      overwriteUserLayerName?: string
      overwriteNetworkStateName?: string
    }
    nearField: {
      enabled?: boolean
      functionParam?: number
      magnitudeParam?: number
      elevationParam?: number
      elevationMaxHeight?: number
      windConversion?: number
      elevationLayerName?: string
    }
    windCorrection: {
      active: string
      maxHeight: string
      parameter: string
      verticalLayers: string
    }
    verticalLevels: { levels: string; spacing: string }
  } = {
    meteorological: {
      layers: {
        windDirection: new MeteorologicalInputLayer(),
        windSpeed: new MeteorologicalInputLayer(),
        temperature: new MeteorologicalInputLayer(),
        relativeHumidity: new MeteorologicalInputLayer(),
        dewPointTemperature: new MeteorologicalInputLayer(),
        droughtFactor: new MeteorologicalInputLayer(),
      },
      timeConversionCoefficient: 1,
      projection: "EPSG:4326",
      windUseDirectionAndSpeed: true,
    },
    terrain: {
      landClassification: new InputLayer("class", { defaultValue: 1 }),
      fireHistory: new InputLayer("fire_history"),
      elevation: new InputLayer("elevation"),
      fuelLoad: new InputLayer("fuel_load"),
      curing: new InputLayer("curing"),
    },
    custom: [],
    series: { windRandomVariation: { speed: 0, direction: 0 } },
    ensemble: {
      numSimulations: 0,
      outputLayers: 1,
      randomSeed: 0,
      outputRasterType: GeotiffType.Float32,
      unitType: OutputUnitType.Undefined,
      reduction: {
        layers: [{ number: 1, type: ReductionType.None }],
        pythonScript: "",
      },
      startPointScript: "",
    },
    firebrand: { elevationLayerName: "elevation" },
    disruption: {},
    nearField: { elevationLayerName: "elevation" },
    windCorrection: {
      active: "",
      maxHeight: "",
      parameter: "",
      verticalLayers: "",
    },
    verticalLevels: { levels: "", spacing: "" },
  }

  public fromJSON(
    inputJSON: Partial<SparkWorkspaceJobInputs>
  ): SparkWorkspaceJobInputs {
    Object.assign(this, inputJSON)

    if ("startTime" in inputJSON) {
      this._startTime = new Date(inputJSON.startTime)
    }
    if ("endTime" in inputJSON) {
      this._endTime = new Date(inputJSON.endTime)
    }

    if (
      "featureCollections" in inputJSON &&
      "masks" in this.featureCollections
    ) {
      this.featureCollections.masks.features.forEach(
        f => (f.properties.time = new Date(f.properties.time))
      )
    }

    if (
      "featureCollections" in inputJSON &&
      "startSources" in this.featureCollections
    ) {
      this.featureCollections.startSources.features.forEach(
        f => (f.properties.time = new Date(f.properties.time))
      )
    }

    return this
  }

  toJSON(): Partial<SparkWorkspaceJobInputs> {
    return Object.assign({}, this, {
      startTime: this._startTime,
      _startTime: undefined,
      endTime: this._endTime,
      _endTime: undefined,
    })
  }
}

export class SparkWorkspaceJobOutputs
  implements JSONSerialiser<SparkWorkspaceJobOutputs> {
  public fromJSON(outputJson: Partial<SparkWorkspaceJobOutputs>) {
    return Object.assign(this, outputJson)
  }

  public toJSON(): Partial<SparkWorkspaceJobOutputs> {
    return Object.assign({}, this)
  }
}
