import {
  serializable,
  DynamicFormControlLayout,
  DynamicFormValueControlModel,
  DynamicFormValueControlModelConfig,
} from "@ng-dynamic-forms/core"
import { DirectoryTree } from "../../../../../shared/src/file-provider/interfaces"

export const DYNAMIC_FORM_CONTROL_TYPE_FILE_BROWSER_INPUT = "FILE_BROWSER_INPUT"

/**
 * Options for FileBroserInputModel
 *
 * @export
 * @class FileBrowserOptions
 */
export class FileBrowserOptions {
  selectableType?: "directory" | "file"

  constructor(obj?: Partial<FileBrowserOptions>) {
    if (obj) {
      Object.assign(this, obj)
    }
  }
}

/**
 * DynamicFormValueControlModelConfig for FileBrowserInput
 *
 * @export
 * @interface FileBrowserInputModelConfig
 */
export interface FileBrowserInputModelConfig
  extends DynamicFormValueControlModelConfig<DirectoryTree> {
  options?: FileBrowserOptions
}

/**
 * DynamicFormValueControlModel for FileBrowserInput
 *
 * @export
 * @class FileBrowserInputModel
 */
export class FileBrowserInputModel extends DynamicFormValueControlModel<
  DirectoryTree
> {
  @serializable() options?: FileBrowserOptions

  @serializable()
  readonly type: string = DYNAMIC_FORM_CONTROL_TYPE_FILE_BROWSER_INPUT

  constructor(
    config: FileBrowserInputModelConfig,
    layout?: DynamicFormControlLayout
  ) {
    super(config, layout)

    this.options = config.options
  }
}
