import { JSONSerialiser } from "../../job-base"
import { featureCollection, FeatureCollection, Geometry } from "@turf/turf"

export class ClimateSlrJobInputs
  implements JSONSerialiser<ClimateSlrJobInputs> {
  public inputFc: FeatureCollection<Geometry> = featureCollection([])

  constructor() {}
  public fromJSON(
    inputJSON: Partial<ClimateSlrJobInputs>
  ): ClimateSlrJobInputs {
    Object.assign(this, inputJSON)
    return this
  }

  public toJSON(): Partial<ClimateSlrJobInputs> {
    return Object.assign({}, this)
  }
}

export class ClimateSlrJobOutputs
  implements JSONSerialiser<ClimateSlrJobOutputs> {
  public output: any = {}

  constructor() {}
  public fromJSON(outputJson: Partial<ClimateSlrJobOutputs>) {
    Object.assign(this, outputJson)
    return this
  }

  public toJSON(): Partial<ClimateSlrJobOutputs> {
    return Object.assign({}, this)
  }
}
