// PrimeNg
import { SliderModule } from "primeng/slider"
import { TooltipModule } from "primeng/tooltip"
import { InputTextModule } from "primeng/inputtext"
import { SidebarModule } from "primeng/sidebar"
import { ButtonModule } from "primeng/button"
import { OrderListModule } from "primeng/orderlist"
import { ColorPickerModule } from "primeng/colorpicker"
import { DropdownModule } from "primeng/dropdown"
import { CalendarModule } from "primeng/calendar"
import { ToastModule } from "primeng/toast"
import { OverlayPanelModule } from "primeng/overlaypanel"
import { CardModule } from "primeng/card"
import { ListboxModule } from "primeng/listbox"
import { ProgressBarModule, TabViewModule } from "primeng/primeng"
import { ToggleButtonModule } from "primeng/togglebutton"
import { SlideMenuModule } from "primeng/slidemenu"
import { DialogModule } from "primeng/dialog"
import { PanelModule } from "primeng/panel"
import { TreeModule } from "primeng/tree"
import { TreeNode } from "primeng/api"

// Module Imports
import { AppRoutingModule } from "./app-routing.module"
import { AppComponent } from "./app.component"
import { GeowebComponent } from "./geo-web/geoweb.component"
import { HomeComponent } from "./home/home.component"
import { GeowebMapComponent } from "./geo-web/map/map.component"
import { PopupFormComponent } from "./geo-web/map/map-popup/map-popup-component"
import { CallbackPipe } from "./util/callback.pipe"
import { DisclaimerComponent } from "./disclaimer/disclaimer.component"
import {
  validateCustomValidator,
  ForbiddenValidatorDirective,
} from "./util/form-validators"
import { SpinnerComponent } from "./spinner/spinner.component"
import { JobConfigComponent } from "./form/form.component"
import { LoginComponent } from "./login/login.component"
import { jwtOptionsFactory } from "./services/auth.service"
import { JobDynamicFormComponent } from "./form/form-instance.component"

import { FileDropUploadComponent } from "./file-drop-upload/file-drop-upload.component"

import { DynamicCodeEditorComponent } from "./form/monaco-editor/form-monaco-editor.component"
import { DYNAMIC_FORM_CONTROL_TYPE_CODE_EDITOR } from "./form/monaco-editor/code-editor-model"

import { MonacoEditorModule, NGX_MONACO_EDITOR_CONFIG } from "ngx-monaco-editor"
import { FileBrowserComponent } from "./file-browser/file-browser.component"
import { FileBrowserInputComponent } from "./form/file-browser-input/form-file-browser-input.component"
import { DYNAMIC_FORM_CONTROL_TYPE_FILE_BROWSER_INPUT } from "./form/file-browser-input/file-browser-input-model"
import { FileBrowserInputControlComponent } from "./form/file-browser-input/file-browser-input.component"
import { FormMutableArrayComponent } from "./form/form-mutable-array/form-mutable-array.component"
import {
  ElectronService,
  getElectronConfig,
  isElectron,
} from "./services/electron.service"
import { ConfigService } from "./services/config.service"

export function initialiseApp(configService: ConfigService) {
  return (): Promise<any> => {
    return configService.init()
  }
}

@NgModule({
  declarations: [
    AppComponent,
    GeowebComponent,
    HomeComponent,
    GeowebMapComponent,
    JobConfigComponent,
    PopupFormComponent,
    DisclaimerComponent,
    SpinnerComponent,
    ForbiddenValidatorDirective,
    CallbackPipe,
    LoginComponent,
    JobDynamicFormComponent,
    DynamicCodeEditorComponent,
    FormMutableArrayComponent,
    FileDropUploadComponent,
    FileBrowserComponent,
    FileBrowserInputComponent,
    FileBrowserInputControlComponent,
  ],
  entryComponents: [
    DynamicCodeEditorComponent,
    FormMutableArrayComponent,
    FileBrowserInputComponent,
  ],
  imports: [
    // Angular
    BrowserModule,
    BrowserAnimationsModule,
    HttpClientModule,
    JwtModule.forRoot({
      jwtOptionsProvider: {
        provide: JWT_OPTIONS,
        useFactory: jwtOptionsFactory,
        deps: [ConfigService],
      },
    }),
    AppRoutingModule,
    FormsModule,

    ReactiveFormsModule,
    DynamicFormsCoreModule,
    DynamicFormsPrimeNGUIModule,

    FileDropModule,

    FontAwesomeModule,

    // PrimeNG
    SliderModule,
    TooltipModule,
    InputTextModule,
    SidebarModule,
    ButtonModule,
    OrderListModule,
    ColorPickerModule,
    DropdownModule,
    CalendarModule,
    ToastModule,
    OverlayPanelModule,
    CardModule,
    ListboxModule,
    ProgressBarModule,
    ToggleButtonModule,
    SlideMenuModule,
    DialogModule,
    PanelModule,
    TabViewModule,
    TreeModule,

    MonacoEditorModule.forRoot(), // use forRoot() in main app module only.
  ],
  providers: [
    ElectronService,
    ConfigService,
    {
      provide: APP_INITIALIZER,
      useFactory: initialiseApp,
      multi: true,
      deps: [ConfigService],
    },
    // This fixes Monaco Editor loading with Electron (https://github.com/atularen/ngx-monaco-editor/issues/49)
    {
      provide: NGX_MONACO_EDITOR_CONFIG,
      useValue: {
        baseUrl: isElectron()
          ? `${getElectronConfig().APP_PATH}assets`
          : "./assets",
      },
    },
    // Add custom form validators
    {
      provide: NG_VALIDATORS,
      useValue: validateCustomValidator,
      multi: true,
    },
    {
      provide: DYNAMIC_VALIDATORS,
      useValue: new Map<string, Validator | ValidatorFactory>([
        ["customValidator", validateCustomValidator],
      ]),
    },
    // Add custom form controls
    {
      provide: DYNAMIC_FORM_CONTROL_MAP_FN,
      useValue: (
        model: DynamicFormControlModel
      ): Type<DynamicFormControl> | null => {
        switch (model.type) {
          case DYNAMIC_FORM_CONTROL_TYPE_CODE_EDITOR:
            return DynamicCodeEditorComponent
          case DYNAMIC_FORM_CONTROL_TYPE_ARRAY:
            return FormMutableArrayComponent
          case DYNAMIC_FORM_CONTROL_TYPE_FILE_BROWSER_INPUT:
            return FileBrowserInputComponent
        }
      },
    },
  ],
  bootstrap: [AppComponent],
})
export class AppModule {
  constructor(private injector: Injector) {
    InjectorInstance = this.injector
  }
}
