import {
  Component,
  OnInit,
  Input,
  ViewEncapsulation,
  AfterViewInit,
  ViewChild,
  ElementRef,
} from "@angular/core"

import { IFormSchema } from "./form-interfaces"

/**
 * This component is used to render a tree of DynamicForms recursively. The parent component is JobConfigComponent
 *
 * @export
 * @class JobDynamicFormComponent
 */
@Component({
  selector: "app-form-instance",
  templateUrl: "./form-instance.component.html",
  styleUrls: ["./form-instance.component.scss"],
  encapsulation: ViewEncapsulation.None,
})
export class JobDynamicFormComponent implements OnInit, AfterViewInit {
  @Input() formSchema: IFormSchema[]
  @ViewChild("rootElement")
  public rootElement: ElementRef

  constructor() {}

  ngOnInit() {}

  ngAfterViewInit(): void {}

  isArray(array) {
    return Array.isArray(array)
  }

  isInArray(key, array: any[]) {
    return array.includes(key)
  }
}
