import { Injectable, OnDestroy } from "@angular/core"
import { TreeNode } from "primeng/api"
import { Subject, Observable, Subscription } from "rxjs"
import { flattenArray } from "../../../../shared/src/util/array"
import { ConfigService } from "../services/config.service"
import { FileBrowserOptions } from "../form/file-browser-input/file-browser-input-model"
import { DirectoryTree } from "../../../../shared/src/file-provider/interfaces"

/**
 * Sets data type of TreeNode (primeng's tree object) to DirectoryTree
 *
 * @export
 * @interface FileBrowserNode
 */
export interface FileBrowserNode extends TreeNode {
  data?: DirectoryTree
  children?: FileBrowserNode[]
  parent?: FileBrowserNode
  selectable: boolean
}

/**
 * This service is used to trigger/display the FileBrowser component it also contains functions for interacting with FileBrowserNode
 *
 * @export
 * @class FileBrowserService
 */
@Injectable({
  providedIn: "root",
})
export class FileBrowserService implements OnDestroy {
  private _options: FileBrowserOptions

  private visible = false
  private visibleSubject = new Subject<boolean>()

  private files: FileBrowserNode[]
  private filesSubject = new Subject<FileBrowserNode[]>()

  private selectedFileSubject = new Subject<FileBrowserNode>()

  private submittedFileSubject = new Subject<DirectoryTree | "cancel">() // Subject fired on file submit and cancel

  private needToUpdateFiles = false

  constructor(private configService: ConfigService) {}

  get options() {
    return this._options
  }

  ngOnDestroy(): void {}

  isVisibleObservable(): Observable<boolean> {
    return this.visibleSubject.asObservable()
  }

  getFilesObservable(): Observable<FileBrowserNode[]> {
    return this.filesSubject.asObservable()
  }

  getSelectedFileObservable(): Observable<FileBrowserNode> {
    return this.selectedFileSubject.asObservable()
  }

  // TODO: preserve previously expanded items
  updateFiles(publishFilesSubject = true): any {
    this.files = []

    // NOT CURRENTLY IMPLEMENTED...

    if (publishFilesSubject) {
      this.filesSubject.next(this.files)
    }

    if (this.configService.debugMode) {
      console.log(this.files)
    }

    this.needToUpdateFiles = false
  }

  private directoryTreeToTreeNodeArray(
    dirTree: DirectoryTree,
    parent?: FileBrowserNode
  ) {
    const treeNode: FileBrowserNode = {
      label: dirTree.name,
      data: dirTree,
      selectable: true,
      parent,
    }

    treeNode.children = Array.isArray(dirTree.children)
      ? dirTree.children.map(child =>
          this.directoryTreeToTreeNodeArray.bind(this)(child, treeNode)
        )
      : []

    return treeNode
  }

  updateOptions(options: FileBrowserOptions): any {
    this._options = new FileBrowserOptions(options)

    // Set selectableType ('file' or 'directory')
    // First set all files to selectable if they have a data field
    this.setPropertyRecursive(this.files, node => {
      node.selectable = "data" in node
    })
    if (this._options.selectableType) {
      this.setPropertyRecursive(this.files, node => {
        node.selectable =
          node.selectable &&
          node.data &&
          node.data.type === this._options.selectableType
      })
    }
  }

  findSelectedFileInTreeNodes(targetFile, treeNodes = this.files) {
    if (
      typeof treeNodes === "undefined" ||
      treeNodes.length === 0 ||
      typeof targetFile === "undefined"
    ) {
      return []
    }

    return flattenArray(
      treeNodes.map(file => {
        if (
          file.label === targetFile.label &&
          file.data.path === targetFile.data.path
        ) {
          return [file]
        }
        if (Array.isArray(file.children) && file.children.length > 0) {
          return this.findSelectedFileInTreeNodes(targetFile, file.children)
        }
      })
    ).filter(match => typeof match !== "undefined")
  }

  private setPropertyFromChild(
    node: FileBrowserNode,
    toSet: Partial<FileBrowserNode> | ((currentNode: FileBrowserNode) => void)
  ) {
    if (typeof toSet === "function") {
      toSet(node)
    } else {
      Object.assign(node, toSet)
    }

    if (node.parent) {
      this.setPropertyFromChild(node.parent, toSet)
    }
  }

  private setPropertyRecursive(
    nodes: FileBrowserNode[],
    toSet: Partial<FileBrowserNode> | ((currentNode: FileBrowserNode) => void)
  ) {
    nodes.forEach(node => {
      if (typeof toSet === "function") {
        toSet(node)
      } else {
        Object.assign(node, toSet)
      }

      if (node.children) {
        this.setPropertyRecursive(node.children, toSet)
      }
    })
  }

  expandToFile(file: FileBrowserNode) {
    if (typeof file !== "undefined") {
      this.expandRecursiveFromChild(file)
    }
  }

  private expandRecursive(nodes: FileBrowserNode[], isExpand: boolean) {
    this.setPropertyRecursive(nodes, { expanded: isExpand })
  }

  private expandRecursiveFromChild(
    node: FileBrowserNode,
    isExpand: boolean = true
  ) {
    this.setPropertyFromChild(node, { expanded: isExpand })
  }

  public show(
    options?: FileBrowserOptions,
    selected?: DirectoryTree
  ): Promise<DirectoryTree> {
    // If already visible - cancel previous instance
    if (this.visible) {
      this.cancel()
    }

    if (this.needToUpdateFiles) {
      this.updateFiles(false)
    }

    // Update options
    this.updateOptions(options)

    // Convert selected file to a FileBrowserNode - and try to locate it in the File Tree (and expand all files to it)
    let selectedFileNode: FileBrowserNode

    if (typeof selected !== "undefined") {
      selectedFileNode = this.findSelectedFileInTreeNodes(
        this.directoryTreeToTreeNodeArray(selected)
      )[0]
      this.expandToFile(selectedFileNode)
    }

    this.filesSubject.next(this.files)
    this.selectedFileSubject.next(selectedFileNode)

    this.visible = true
    this.visibleSubject.next(this.visible)

    // Return a promise which is resolved when a a file is pushed to the submittedFileSubject by the FileBrowserComponent
    return new Promise((resolve, reject) => {
      const sub = this.submittedFileSubject.subscribe(dirTree => {
        if (dirTree === "cancel") {
          reject("cancel")
        } else {
          resolve(dirTree)
        }
        sub.unsubscribe()
      })
    })
  }

  public hide() {
    if (this.visible) {
      this.visible = false
      this.visibleSubject.next(this.visible)
    }
  }

  public submit(dirTree: DirectoryTree) {
    this.visible = false
    this.visibleSubject.next(this.visible)
    this.submittedFileSubject.next(dirTree)
  }

  public cancel() {
    this.visible = false
    this.visibleSubject.next(this.visible)
    this.submittedFileSubject.next("cancel")
  }
}
