// From directory-tree npm package
export class DirectoryTree {
  path: string
  name: string
  size?: number
  type: "directory" | "file"
  children?: DirectoryTree[]
  extension?: string
  location: FileLocation = FileLocation.MasterJob
}

export class DirectoryTreeFile extends DirectoryTree {
  type: "file" = "file"
}
export class DirectoryTreeDirectory extends DirectoryTree {
  type: "directory" = "directory"
}

export const isValidDirectoryTree = (dirTree: any): dirTree is DirectoryTree =>
  typeof dirTree !== "undefined" &&
  dirTree !== null &&
  typeof dirTree.path !== "undefined"

export enum FileLocation {
  MasterJob = "master-job",
  MasterTemplate = "master-template",
  MasterPrivate = "master-private",
  MasterPublic = "master-public",
  S3 = "s3",
  GCS = "gcs",
  URL = "url",
  None = "none",
}

export type ResolveDirTree = (
  dt: DirectoryTree,
  debug: (message: string) => void
) => Promise<string>

export type StorageConfig = RemoteStorageConfig | LocalStorageConfig

export interface RemoteStorageConfig {
  saveDir: string
  masterBaseUrl: string
  masterPublicFilesApiUrl: string
  masterPrivateFilesApiUrl: string
  masterJobFilesApiUrl: string
  masterTemplateFilesApiUrl: string
}

export interface LocalStorageConfig {
  saveDir: string
  publicDir: string
  privateDir: string
  jobDir: string
  templateDir: string
}

export const instanceOfLocalStorageConfig = (
  obj: any
): obj is LocalStorageConfig =>
  typeof obj.publicDir === "string" &&
  typeof obj.privateDir === "string" &&
  typeof obj.jobDir === "string" &&
  typeof obj.templateDir === "string"
