export interface CustomValidator {
  isValid: (s: string) => boolean
  /**
   * Please don't include full stops
   *
   */
  errorMessage: String
}

export function pad0s(number: number, zeros: number = 2) {
  return number.toString().padStart(zeros, "0")
}

export function toFixedDrop0s(number: number, toFixed: number = 2) {
  return number.toFixed(toFixed).replace(/\.00$/, "")
}

// From https://stackoverflow.com/a/14428340
export function formatCurrency(number: number) {
  const money = `$${number.toFixed(2).replace(/\d(?=(\d{3})+\.)/g, "$&,")}`
  return money.length < 10 ? money : money.split(".")[0]
}

export const alphaNumericDashesValidator: CustomValidator = {
  isValid: (str: string) =>
    str === "" || isValidForRegexpFn(/^[a-zA-Z0-9-_]+$/)(str),
  errorMessage:
    "Must only contain alphanumerics, dashes or underscores (a-Z, 0-9, -, _)",
}

export const positiveIntegerValidator: CustomValidator = {
  isValid: (str: string) =>
    str === "" || isValidForRegexpFn(/^\+?(0|[1-9]\d*)$/)(str),
  errorMessage: "Positive integer value required",
}

// Adapted from https://stackoverflow.com/a/10834843
export const integerValidator: CustomValidator = {
  isValid: (str: string) =>
    str === "" || isValidForRegexpFn(/^[-+]?(0|[1-9]\d*)$/)(str),
  errorMessage: "Integer value required",
}

// From http://regexlib.com/Search.aspx?k=float&AspxAutoDetectCookieSupport=1
export const floatValidator: CustomValidator = {
  isValid: isValidForRegexpFn(/^[-+]?\d*\.?\d*$/),
  errorMessage: "Number required",
}

export const latitudeValidator: CustomValidator = {
  isValid: str => floatValidator.isValid(str) && isLatitude(parseFloat(str)),
  errorMessage: "Invalid latitude",
}

export const longitudeValidator: CustomValidator = {
  isValid: str => floatValidator.isValid(str) && isLongitude(parseFloat(str)),
  errorMessage: "Invalid longitude",
}

export function isLatitude(lat) {
  return isFinite(lat) && Math.abs(lat) <= 90
}

export function isLongitude(lng) {
  return isFinite(lng) && Math.abs(lng) <= 180
}

export function isValidForRegexpFn(regexp) {
  return (str: string) => str.toString().search(regexp) !== -1
}

export function isValidForRegexp(str, regexp) {
  return str.toString().search(regexp) !== -1
}

// Adapted from https://stackoverflow.com/a/4253415
export function escapeString(str: string) {
  return str
    .replace(/\\n/g, "\\n")
    .replace(/\\'/g, "\\'")
    .replace(/\\"/g, '\\"')
    .replace(/\\&/g, "\\&")
    .replace(/\\r/g, "\\r")
    .replace(/\\t/g, "\\t")
    .replace(/\\b/g, "\\b")
    .replace(/\\f/g, "\\f")
}

export function parseCsv(str: string) {
  return str
    .replace(/[\r]+/g, "")
    .split("\n")
    .map(row => (row !== "" ? row.split(",") : undefined))
    .filter(row => row !== undefined) as string[][]
}
