import {
  ValidatorFn,
  AbstractControl,
  ValidationErrors,
  NG_VALIDATORS,
  Validator,
} from "@angular/forms"
import {
  isValidForRegexp,
  CustomValidator,
} from "../../../../shared/src/util/string"
import { Directive, Input } from "@angular/core"

export function validateCustomValidator(
  customValidator: CustomValidator
): ValidatorFn {
  return (control: AbstractControl): ValidationErrors | null => {
    if (control.value === null || customValidator.isValid(control.value)) {
      return null // don't validate empty values to allow optional controls
    }

    return { customValidator: true }
  }
}

@Directive({
  selector: "[customValidator]",
  providers: [
    {
      provide: NG_VALIDATORS,
      useExisting: ForbiddenValidatorDirective,
      multi: true,
    },
  ],
})
export class ForbiddenValidatorDirective implements Validator {
  @Input() customValidator: CustomValidator

  validate(control: AbstractControl): { [key: string]: any } | null {
    if (control.value === null || this.customValidator.isValid(control.value)) {
      return null // don't validate empty values to allow optional controls
    }

    return { customValidator: true }
  }
}
