import { Injectable } from "@angular/core"

import { Subject, Observable } from "rxjs"
import { spinnerDivCounts } from "./spinner-consts"

// The div count is the number of Div elements required for the load-awesome spinner (see load-awesome package)

export interface SpinnerItem {
  target?: string
  name: string
  icon: SpinnerIcon
  progress?: number
  divCount?: number
  size?: SpinnerSize
}

// Note: if you add icons to this enum, make sure you also add the corresponding load-awesome css file to client/angular.json
export enum SpinnerIcon {
  Fire = "la-fire",
  GridPulse = "la-ball-grid-pulse",
  LineSpin = "la-line-spin-fade-rotating",
  RotatingCircle = "la-ball-clip-rotate",
  Ball8Bit = "la-ball-8bits",
  Pacman = "la-pacman",
  BallScale = "la-ball-scale",
  Square = "la-square-loader",
}

export enum SpinnerSize {
  sm = "la-sm",
  md = "",
  lg = "la-2x",
  xl = "la-3x",
}

/**
 * Provides subjects to create/change/remove spinners. See also SpinnerComponent
 *
 * @export
 * @class SpinnerService
 */
@Injectable({
  providedIn: "root",
})
export class SpinnerService {
  private activeSpinners: Map<string, SpinnerItem> = new Map()

  private activeSpinnersSubject = new Subject<SpinnerItem[]>()

  constructor() {}

  getActiveSpinnersObservable(): Observable<SpinnerItem[]> {
    return this.activeSpinnersSubject.asObservable()
  }

  getActiveSpinners() {
    return Array.from(this.activeSpinners).map(entry => entry[1])
  }

  setSpinner(id: string, spinner: SpinnerItem) {
    if (typeof spinner.divCount === "undefined") {
      spinner.divCount = spinnerDivCounts[spinner.icon.toString()]
    }

    this.activeSpinners.set(id, spinner)

    this.activeSpinnersSubject.next(this.getActiveSpinners())
  }

  updateSpinner(id: string, spinner: Partial<SpinnerItem>): boolean {
    const spinnerToUpdate = this.activeSpinners.get(id)
    if (typeof spinnerToUpdate === "undefined") {
      return false
    }

    this.activeSpinners.set(id, Object.assign(spinnerToUpdate, spinner))
    return true
  }

  removeSpinner(id: string) {
    this.activeSpinners.delete(id)
    this.activeSpinnersSubject.next(this.getActiveSpinners())
  }

  removeSpinnersStartWith(match: string) {
    const toDelete = new Set<string>()
    this.activeSpinners.forEach((spinner, key) => {
      if (key.startsWith(match)) {
        toDelete.add(key)
      }
    })

    toDelete.forEach(key => this.activeSpinners.delete(key))
  }
}
