    this.inputShape.opacity = 50
    this.inputShape.singleFeature = true

    this.inputShape.featureCollectionObservable.subscribe(
      ((inputFeatures: FeatureCollection) => {
        if (
          typeof this.ffdiSummaryFc !== "undefined" &&
          inputFeatures.features.length > 0
        ) {
          const allPointsMatch = pointsWithinPolygon(
            this.ffdiSummaryFc as FeatureCollection<Point>,
            inputFeatures as FeatureCollection<Polygon>
          )

          this.inputs.fireRasterSelection = []

          if (this.inputs.fireRasterFilter.length === 0) {
            this.inputs.fireRasterSelection = allPointsMatch.features.map(
              feature => `cell_ffdi30_${feature.properties.simulationid}.tif`
            )
          } else {
            this.inputs.fireRasterSelection = allPointsMatch.features
              .filter(feature => {
                let match = true
                for (
                  let filterIndex = 0;
                  filterIndex < this.inputs.fireRasterFilter.length;
                  filterIndex++
                ) {
                  const filter = this.inputs.fireRasterFilter[filterIndex]
                  match =
                    match &&
                    typeof feature.properties[filter.key] !== "undefined" &&
                    (filter.min === null ||
                      feature.properties[filter.key] > filter.min) &&
                    (filter.max === null ||
                      feature.properties[filter.key] < filter.max)
                }
                return match
              })
              .map(
                feature => `cell_ffdi30_${feature.properties.simulationid}.tif`
              )
          }
        }
      }).bind(this)
    )

