import { MapLayer } from "./map-layer"
import {
  ColourScheme,
  D3ColourScheme,
  SolidColourScheme,
} from "../colour-scheme/colour-scheme"
import { AnySourceData, GeoJSONSource, PointLike, Expression } from "mapbox-gl"
import { MousePoint } from "./layer-service.service"
import { Subject } from "rxjs"
import { layer } from "@fortawesome/fontawesome-svg-core"
import { union, Feature, Polygon, bbox, feature } from "@turf/turf"

export interface MapboxLayerFilterDimension {
  label: string
  key: string
  options: { label: string; value: string }[]
  selected: { label: string; value: string }
}

export interface MapboxLayerFilterValues {
  label: string
  key: string
  minValue?: number
  maxValue?: number
}

export interface MapboxColourByPropertyDataJoin {
  featureValue: string | number
  dataValue: number
}

export interface MapboxColourByProperty {
  label: string
  key: string
  minValue?: number
  maxValue?: number
  dataToJoin?: MapboxColourByPropertyDataJoin[]
}

export interface MapboxColourByProperties {
  options: MapboxColourByProperty[]
  selected: MapboxColourByProperty
}
//Create population layer
export interface SelectedLayerPopulation {
  name: string
}

export interface SelectedLayerPopulations {
  options: SelectedLayerPopulation[]
  selected: SelectedLayerPopulation
}
//Create fire layers for selected region
export interface SelectedFire {
  name: string
}

export interface SelectedLayerFires {
  options: SelectedFire[]
  selected: SelectedFire
}
//Create evacuation message for selected layer
export interface SelectedMessage {
  message: string
}

export interface SelectedLayerMessages {
  options: SelectedMessage[]
  selected: SelectedMessage
}

export class MapboxGlLayer extends MapLayer {
  public static isInstanceOf(obj: any): obj is MapboxGlLayer {
    return "mapboxGlLayer" in obj
  }



  public dimensionsArray: MapboxLayerFilterDimension[]
  public filterValuesArray: MapboxLayerFilterValues[] = []

  /**
   * This will disable setting the mapbox filter with the dimensionsArray
   *
   */
  public disableFilter = false

  private _colourByProperty: MapboxColourByProperties

  private _population: SelectedLayerPopulations

  private _fire: SelectedLayerFires

  private _speed: number

  private _animationSpeed: number

  private _time: number

  private _evacMessage: SelectedLayerMessages

  get colourByProperty() {
    return this._colourByProperty
  }

  set colourByProperty(
    colorByProperty: MapboxColourByProperties | MapboxColourByProperty
  ) {
    if (
      "options" in colorByProperty &&
      Array.isArray(colorByProperty.options)
    ) {
      this._colourByProperty = colorByProperty
    } else {
      this._colourByProperty = {
        options: [colorByProperty as MapboxColourByProperty],
        selected: colorByProperty as MapboxColourByProperty,
      }
    }
  }
  //Setup getter for population
  get population() {
    return this._population
  }

  //Set population values
  set population(population: SelectedLayerPopulations | SelectedLayerPopulation) {
    if (
      "options" in population &&
      Array.isArray(population.options)
    ) {
      this._population = population
    } else {
      this._population = {
        options: [population as SelectedLayerPopulation],
        selected: population as SelectedLayerPopulation,
      }
    }
  }
  //Setup getter for fires
  get fire() {
    return this._fire
  }

  //Set population values
  set fire(fire: SelectedLayerFires | SelectedFire) {
    if (
      "options" in fire &&
      Array.isArray(fire.options)
    ) {
      this._fire = fire
    } else {
      this._fire = {
        options: [fire as SelectedFire],
        selected: fire as SelectedFire,
      }
    }
  }

  //Setup getter for evac messages
  get evacMessage() {
    return this._evacMessage
  }

  //Set evacuation message values
  set evacMessage(message: SelectedLayerMessages | SelectedMessage) {
    if (
      "options" in message &&
      Array.isArray(message.options)
    ) {
      this._evacMessage = message
    } else {
      this._evacMessage = {
        options: [message as SelectedMessage],
        selected: message as SelectedMessage,
      }
    }
  }

  // Return maximum speed on road network
  get speed() {
    return this._speed
  }

  //setup maximum speed on road network
  set speed(speed: number) {
    this._speed = speed
  }

  // Return animationspeed for simulation
  get animationSpeed() {
    return this._animationSpeed
  }

  //setup animationspeed for simulation
  set animationSpeed(speed: number) {
    this._animationSpeed = speed
  }

  // Return value in time slider
  get time() {
    return this._time
  }

  //setup value in time slider
  set time(time: number) {
    this._time = time
  }

  public mapboxFilter: any[]
  public idByProperty: string

  public blendMode = undefined
  public blendModes = []

  private _maxValue: number
  private _minValue: number

  private _opacityPreHide = 1

  private _onRenderSubject = new Subject<void>()
  private _onRenderObservable = this._onRenderSubject.asObservable()

  get onRenderObservable() {
    return this._onRenderObservable
  }

  public colourScheme: ColourScheme

  constructor(
    name: string,
    protected _mapboxGlLayer?: mapboxgl.Layer,
    protected _source?: { id: string; source: AnySourceData }
  ) {
    super(name)

    if (
      typeof this._mapboxGlLayer !== "undefined" &&
      typeof this._mapboxGlLayer.id === "undefined"
    ) {
      throw `New MapboxGlLayer ${name}: _mapboxGlLayer.id is undefined`
    }

    if (
      typeof this._mapboxGlLayer !== "undefined" &&
      typeof _mapboxGlLayer.filter !== "undefined"
    ) {
      this.mapboxFilter = _mapboxGlLayer.filter
    }
  }

  get minValue() {
    return this._minValue
  }

  set minValue(v) {
    this._minValue = v
    if (
      typeof this._colourByProperty !== "undefined" &&
      typeof this._colourByProperty.selected.minValue !== "undefined"
    ) {
      this._colourByProperty.selected.minValue = v
    }
  }

  get maxValue() {
    return this._maxValue
  }

  set maxValue(v) {
    this._maxValue = v
    if (
      typeof this._colourByProperty !== "undefined" &&
      typeof this._colourByProperty.selected.maxValue !== "undefined"
    ) {
      this._colourByProperty.selected.maxValue = v
    }
  }

  get mapboxGlLayer() {
    return this._mapboxGlLayer
  }

  get sourceId() {
    return this._source.id
  }

  async render(force = false) {
    if (typeof this._mapboxGlLayer === "undefined") {
      return
    }
    if (typeof this._mapboxGlLayer.paint === "undefined") {
      this._mapboxGlLayer.paint = {}
    }

    const addedToMap =
      typeof MapLayer.layerService.mapboxGl.getLayer(this.mapboxGlLayer.id) !==
      "undefined"

    if (!this._initialised || force) {
      // Initialise filter
      this._mapboxGlLayer.filter =
        typeof this.mapboxFilter === "undefined"
          ? ["all"]
          : ["all", this.mapboxFilter]

      // Set Filter from dimensionsArray
      if (typeof this.dimensionsArray !== "undefined" && !this.disableFilter) {
        this._mapboxGlLayer.filter.push(
          ...this.dimensionsArray.map(dim => [
            "==",
            ["get", dim.key],
            dim.selected.value,
          ])
        )
      }

      // Set Filter from valu
      this._mapboxGlLayer.filter.push(
        ...this.filterValuesArray.reduce((filter, current) => {
          if (typeof current.minValue !== "undefined") {
            filter.push([">=", ["get", current.key], current.minValue])
          }
          if (typeof current.maxValue !== "undefined") {
            filter.push(["<=", ["get", current.key], current.maxValue])
          }

          return filter
        }, [])
      )

      // Set colour scale
      if (
        typeof this.colourScheme !== "undefined" &&
        D3ColourScheme.isInstanceOf(this.colourScheme) &&
        typeof this._colourByProperty !== "undefined"
      ) {
        const selectedColByProp = this._colourByProperty.selected
        // Update min/max values if defined in colorByProperty
        if (typeof selectedColByProp.minValue !== "undefined") {
          this.minValue = selectedColByProp.minValue
        }
        if (typeof selectedColByProp.maxValue !== "undefined") {
          this.maxValue = selectedColByProp.maxValue
        }
        this.colourScheme.setDomain(this.minValue, this.maxValue)

        let colourExpression: Expression

        // If data has been provided to colour by -> see https://docs.mapbox.com/mapbox-gl-js/example/data-join/
        if (typeof selectedColByProp.dataToJoin !== "undefined") {
          colourExpression = ["match", ["get", selectedColByProp.key]]

          // Calculate color for each data value
          selectedColByProp.dataToJoin.forEach(value => {
            colourExpression.push(
              value.featureValue,
              this.colourScheme.scale(value.dataValue)
            )
          })

          // Last value is the default, used where there is no data
          colourExpression.push("rgba(0,0,0,0)")

          // Otherwise, we are colouring by a property contained in each feature
        } else {
          colourExpression = this.colourScheme.toMapbox(selectedColByProp.key)

          // Set filter to remove null values
          this._mapboxGlLayer.filter.push([
            "!=",
            ["get", selectedColByProp.key],
            null,
          ])
        }

        // Update map layer
        if (addedToMap) {
          MapLayer.layerService.mapboxGl.setPaintProperty(
            this.mapboxGlLayer.id,
            `${this.mapboxGlLayer.type}-color`,
            colourExpression
          )
        } else {
          this._mapboxGlLayer.paint[
            `${this.mapboxGlLayer.type}-color`
          ] = colourExpression
        }
      } else if (
        typeof this.colourScheme !== "undefined" &&
        SolidColourScheme.isInstanceOf(this.colourScheme)
      ) {
        if (addedToMap) {
          MapLayer.layerService.mapboxGl.setPaintProperty(
            this.mapboxGlLayer.id,
            `${this.mapboxGlLayer.type}-color`,
            this.colourScheme.solidColour
          )
        } else {
          this._mapboxGlLayer.paint[
            `${this.mapboxGlLayer.type}-color`
          ] = this.colourScheme.solidColour
        }
      }

      // Delete filter if === ["all"]
      if (
        this._mapboxGlLayer.filter.length === 1 &&
        this._mapboxGlLayer.filter[0] === "all"
      ) {
        delete this._mapboxGlLayer.filter
      }

      if (addedToMap && typeof this._mapboxGlLayer.filter !== "undefined") {
        MapLayer.layerService.mapboxGl.setFilter(
          this.mapboxGlLayer.id,
          this._mapboxGlLayer.filter
        )
      }
    }

    if (!addedToMap) {
      this._mapboxGlLayer.paint[
        `${this.mapboxGlLayer.type}-opacity`
      ] = this._opacity
      if (typeof this._source !== "undefined") {
        if (
          typeof MapLayer.layerService.mapboxGl.getSource(this._source.id) !==
          "undefined"
        ) {
          MapLayer.layerService.mapboxGl.removeSource(this._source.id)
        }
        MapLayer.layerService.mapboxGl.addSource(
          this._source.id,
          this._source.source
        )
      }

      MapLayer.layerService.mapboxGl.addLayer(this.mapboxGlLayer)

      this._initialised = true
    }

    await super.render(force)

    this._onRenderSubject.next()
  }

  updateLayerFilter() {
    MapLayer.layerService.mapboxGl.setFilter(
      this.mapboxGlLayer.id,
      this.mapboxGlLayer.filter
    )
  }

  async show() {
    await this.render()
    if (this.opacity === 0) {
      this.opacity = this._opacityPreHide * 100
    }

    await super.show()
  }

  async hide() {
    this._opacityPreHide = this._opacity
    this.opacity = 0
    await super.hide()
  }

  async clear() {
    if (
      typeof this.mapboxGlLayer !== "undefined" &&
      typeof MapLayer.layerService.mapboxGl.getLayer(this.mapboxGlLayer.id) !==
      "undefined"
    ) {
      MapLayer.layerService.mapboxGl.removeLayer(this.mapboxGlLayer.id)
    }

    if (
      typeof this._source !== "undefined" &&
      typeof MapLayer.layerService.mapboxGl.getSource(this._source.id) !==
      "undefined"
    ) {
      MapLayer.layerService.mapboxGl.removeSource(this._source.id)
    }

    await super.clear()
  }

  /**
   * FIXME: sometimes this hangs before a layer has been added.
   *
   */
  set opacity(o) {
    super.opacity = o
    if (
      typeof MapLayer.layerService.mapboxGl.getLayer(this.mapboxGlLayer.id) !==
      "undefined"
    ) {
      MapLayer.layerService.mapboxGl.setPaintProperty(
        this.mapboxGlLayer.id,
        `${this.mapboxGlLayer.type}-opacity`,
        o / 100
      )
    }
  }

  get opacity() {
    return this._opacity * 100
  }

  private getPolygonAtPoint(pointOrBox: PointLike | [PointLike, PointLike]) {
    if (typeof this.idByProperty === "undefined") {
      throw "Layer idByProperty must be defined to call getPolygonAtPoint()"
    }

    let filter = this._mapboxGlLayer.filter

    // Get one feature under mouse
    const renderedFeatures = MapLayer.layerService.mapboxGl.queryRenderedFeatures(
      pointOrBox,
      { layers: [this.mapboxGlLayer.id] }
    )[0]

    if (typeof renderedFeatures === "undefined") {
      return undefined
    }

    // Create new filter which merges current filter, with idByProperty (to find parts of the feature under mouse)
    if (typeof filter === "undefined") {
      filter = ["all"]
    }

    const found = MapLayer.layerService.mapboxGl.queryRenderedFeatures(
      undefined,
      {
        layers: [this.mapboxGlLayer.id],
        filter: [
          ...filter,
          [
            "==",
            ["get", this.idByProperty],
            renderedFeatures.properties[this.idByProperty],
          ],
        ],
      }
    ) as Feature<Polygon>[]

    if (found.length === 0) {
      return undefined
    }

    // Return merged polygon
    return [union(...found)]
  }

  private getPointFeatureAtPoint(
    pointOrBox: PointLike | [PointLike, PointLike]
  ) {
    const f = MapLayer.layerService.mapboxGl.queryRenderedFeatures(pointOrBox, {
      layers: [this.mapboxGlLayer.id],
    })
    return f
  }

  public getFeaturesAtPoint(point: MousePoint, buffer = 0) {
    let pointOrBox:
      | PointLike
      | [PointLike, PointLike] = MapLayer.layerService.mapboxGl.project(
        point.latlng
      )

    if (buffer !== 0) {
      pointOrBox = [
        [pointOrBox.x - buffer, pointOrBox.y - buffer],
        [pointOrBox.x + buffer, pointOrBox.y + buffer],
      ]
    }

    if (
      this.mapboxGlLayer.type === "fill" ||
      this.mapboxGlLayer.type === "line"
    ) {
      return this.getPolygonAtPoint(pointOrBox)
    }
    if (
      this.mapboxGlLayer.type === "circle" ||
      this.mapboxGlLayer.type === "symbol"
    ) {
      return this.getPointFeatureAtPoint(pointOrBox)
    }

    return undefined
  }

  public getValueAtPoint(point: MousePoint) {
    if (typeof this._colourByProperty === "undefined") {
      return undefined
    }

    const layer = MapLayer.layerService.mapboxGl.queryRenderedFeatures(
      MapLayer.layerService.mapboxGl.project(point.latlng),
      { layers: [this.mapboxGlLayer.id] }
    )[0]

    if (typeof layer === "undefined") {
      return undefined
    }

    if (typeof this._colourByProperty.selected.dataToJoin === "undefined") {
      return layer.properties[this._colourByProperty.selected.key]
    }
    const value = this._colourByProperty.selected.dataToJoin.find(
      data =>
        data.featureValue ===
        layer.properties[this._colourByProperty.selected.key]
    )
    return typeof value === "undefined" ? undefined : value.dataValue
  }
}
