export enum SparkFormModeKey {
  Basic = "basic",
  Advanced = "advanced",
}

export const sparkFormRootModel = new FormRootModel()

sparkFormRootModel.subjectPropertyKey = "inputs"

sparkFormRootModel.modes = [
  {
    key: SparkFormModeKey.Basic,
    name: "Basic",
    userRoles: [GeowebUserRoles.User, GeowebUserRoles.Admin],
  },
  {
    key: SparkFormModeKey.Advanced,
    name: "Advanced",
    userRoles: [GeowebUserRoles.Admin],
  },
]

sparkFormRootModel.defaultMode = sparkFormRootModel.modes[0]

sparkFormRootModel.schemaFactory = (
  currentFormMode: IFormMode,
  configService: ConfigService
): IFormSchema[] => {
  return [
    {
      name: "Basic",
      modeKeys: [SparkFormModeKey.Basic, SparkFormModeKey.Advanced],
      form: {
        model: [
          new DynamicDatePickerModel({
            id: "startTime",
            label: "Start time",
            additional: {
              showTime: true,
              inline: false,
            },
            validators: {
              required: null,
            },
            errorMessages: {
              required: "{{ label }} is required",
            },
          }),
          new DynamicInputModel({
            id: "durationSeconds",
            label: "Simulation duration (seconds)",
            validators: {
              required: null,
              customValidator: floatValidator,
            },
            errorMessages: {
              required: "{{ label }} is required",
              customValidator: floatValidator.errorMessage,
            },
          }),
          new DynamicInputModel({
            id: "resolutionMeters",
            label: "Simulation resolution (m)",
            validators: {
              required: null,
              customValidator: floatValidator,
            },
            errorMessages: {
              required: "{{ label }} is required",
              customValidator: floatValidator.errorMessage,
            },
          }),
          new DynamicSwitchModel({
            id: `disableFileOutputs`,
            label: `Disable file outputs`,
            offLabel: "File outputs are ENABLED",
            onLabel: "File outputs are DISABLED",
            validators: {
              required: null,
            },
            errorMessages: {
              required: "{{ label }} is required",
            },
            hidden: currentFormMode.key !== SparkFormModeKey.Advanced,
          }),
          //
        ],
      },
    },
    {
      name: "Initialisation Scripts",
      modeKeys: [SparkFormModeKey.Advanced],
      form: [
        {
          name: `Initialisation model`,
          modeKeys: [SparkFormModeKey.Advanced],
          form: {
            layout: {
              startC: {
                grid: {
                  host: "ui-g-12",
                },
              },
            },
            model: [
              new DynamicCodeEditorModel({
                id: `initialisationModel`,
                label: `Initialisation model`,
                options: { language: "cpp" },
              }),
            ],
          },
        },
      ],
    },
    {
      name: "Rate of Spread Models",
      modeKeys: [SparkFormModeKey.Advanced],
      form: [
        {
          name: `Rate-of-spread model start`,
          modeKeys: [SparkFormModeKey.Advanced],
          form: {
            layout: {
              startC: {
                grid: {
                  host: "ui-g-12",
                },
              },
            },
            model: [
              new DynamicCodeEditorModel({
                id: `rateOfSpreadModelsCommonStart`,
                label: `Rate-of-spread model start`,
                options: { language: "cpp" },
              }),
            ],
          },
        },
        {
          name: `Rate-of-spread model end`,
          modeKeys: [SparkFormModeKey.Advanced],
          form: {
            layout: {
              startC: {
                grid: {
                  host: "ui-g-12",
                },
              },
            },
            model: [
              new DynamicCodeEditorModel({
                id: `rateOfSpreadModelsCommonEnd`,
                label: `Rate-of-spread model end`,
                options: { language: "cpp" },
              }),
            ],
          },
        },
        ...[...Array(12).keys()].map(idx => {
          return {
            name: `RoS ${idx + 1}`,
            modeKeys: [SparkFormModeKey.Advanced],
            form: {
              model: [
                new DynamicFormGroupModel({
                  id: "rateOfSpreadModels",
                  group: [
                    new DynamicCodeEditorModel({
                      id: `${idx + 1}`,
                      label: `RoS ${idx + 1}`,
                      options: { language: "cpp" },
                    }),
                  ],
                }),
              ],
            },
          }
        }),
      ],
    },
    {
      name: "Update Models",
      modeKeys: [SparkFormModeKey.Advanced],
      form: [
        {
          name: `Update model start`,
          modeKeys: [SparkFormModeKey.Advanced],
          form: {
            layout: {
              startC: {
                grid: {
                  host: "ui-g-12",
                },
              },
            },
            model: [
              new DynamicCodeEditorModel({
                id: `updateModelsCommonStart`,
                label: `Update model start`,
                options: { language: "cpp" },
              }),
            ],
          },
        },
        {
          name: `Update model end`,
          modeKeys: [SparkFormModeKey.Advanced],
          form: {
            layout: {
              startC: {
                grid: {
                  host: "ui-g-12",
                },
              },
            },
            model: [
              new DynamicCodeEditorModel({
                id: `updateModelsCommonEnd`,
                label: `Update model end`,
                options: { language: "cpp" },
              }),
            ],
          },
        },
        ...[...Array(12).keys()].map(idx => {
          return {
            name: `Update model ${idx + 1}`,
            modeKeys: [SparkFormModeKey.Advanced],
            form: {
              model: [
                new DynamicFormGroupModel({
                  id: "updateModels",
                  group: [
                    new DynamicCodeEditorModel({
                      id: `${idx + 1}`,
                      label: `Update model ${idx + 1}`,
                      options: { language: "cpp" },
                    }),
                  ],
                }),
              ],
            },
          }
        }),
      ],
    },
  ]
}

