import { JSONSerialiserT } from "../../job-base"
import { FeatureCollection, Point, featureCollection } from "@turf/turf"
import {
  FileLocation,
  DirectoryTree,
  DirectoryTreeFile,
} from "../../../file-provider/interfaces"

export class Emv2JobInputs extends JSONSerialiserT<Emv2JobInputs> {
  public inputProjection = "EPSG:4326"

  public fireRasterSelectionGeometry: FeatureCollection = featureCollection([])

  public fireRasterSelection: string[] = []

  public fireRasterFilter: { key: string; min?: number; max?: number }[] = []

  public fireRasterBaseDirectory: DirectoryTree | {} = {}

  public fireSummaryGeojson: DirectoryTree | {} = {}

  public outputGeojson: DirectoryTree = {
    name: "output.json",
    path: "Output/output.json",
    type: "file",
    location: FileLocation.MasterJob,
  }

  public networkConfig = {
    constant: 100.0,
    defaultLinkType: 3,
  }

  public files: {
    networkXML?: DirectoryTreeFile
    populationRaster?: DirectoryTreeFile
    fireRaster?: DirectoryTreeFile
  } = {}
}

export interface Emv2Stats {
  geotiffFilename: string
  flow: number
  head: number
}

export class Emv2JobOutputs extends JSONSerialiserT<Emv2JobOutputs> {
  stats: Emv2Stats[]
}

export const emv2SubjobName = (name: string, fire: string) =>
  `!${name} subjob ${fire}`
