import { JobType } from "../../job-types"
import {
  SparkWorkspaceJobInputs,
  SparkWorkspaceJobOutputs,
} from "./inputs-outputs"
import { JobTypeTraits, JobJSON } from "../../job-base"
import { Constructor, Mixin } from "../../../util/mixins"

export function sparkWorkspaceJob<TBase extends Constructor>(Base: TBase) {
  return class SparkWorkspaceJob extends Base implements JobTypeTraits {
    type: JobType.SPARKWORKSPACE = JobType.SPARKWORKSPACE
    inputs: SparkWorkspaceJobInputs = new SparkWorkspaceJobInputs()
    outputs: SparkWorkspaceJobOutputs = new SparkWorkspaceJobOutputs()
  }
}

export type SparkWorkspaceJob<Base extends JobJSON = JobJSON> = Base &
  Mixin<typeof sparkWorkspaceJob>
