import { Component, OnInit, AfterViewInit, NgZone } from "@angular/core"
import { DisclaimerSerivce } from "../disclaimer/disclaimer-service"
import { Router } from "@angular/router"
import { AuthService } from "../services/auth.service"

/**
 * Home component, redirects to login or job (GeoWebComponent)
 *
 * @export
 * @class HomeComponent
 */
@Component({
  selector: "app-home",
  templateUrl: "./home.component.html",
  styleUrls: ["./home.component.scss"],
})
export class HomeComponent implements OnInit, AfterViewInit {
  afterViewInit = false

  constructor(
    private disclaimerService: DisclaimerSerivce,
    private router: Router,
    private zone: NgZone,
    private authService: AuthService
  ) {}

  ngOnInit() {
    if (!this.authService.isLoggedIn) {
      this.router.navigate(["/login"])
    } else if (this.disclaimerService.disclaimerAgreedTo) {
      this.router.navigate(["/job"])
    }
  }

  ngAfterViewInit(): void {
    setTimeout(() => {
      this.zone.run(() => {
        this.afterViewInit = true
      })
    }, 300)
  }
}
