import { sparkWorkspaceJob } from "../../../../../../shared/src/job/job-types/spark-workspace/job"
import { ClientJob } from "../../job"
import {
  sparkFormRootModel,
  startSourceFormGroups,
  maskSourceFormGroups,
} from "./form-model"
import { EditableFeatureCollectionLayer } from "src/app/geo-web/map/map-layer/map-editable-feature-layer"
import bbox from "@turf/bbox"
import {
  featureCollection,
  area,
  bboxPolygon,
  FeatureCollection,
} from "@turf/turf"
import { MapLayer } from "src/app/geo-web/map/map-layer/map-layer"
import { RasterLayer } from "src/app/geo-web/map/map-layer/map-geotiff-layer"
import {
  D3ColourScheme,
  SolidColourScheme,
} from "src/app/geo-web/map/colour-scheme/colour-scheme"
import { secondsToHMString } from "../../../../../../shared/src/util/dates"
import { MapContourLayer } from "src/app/geo-web/map/map-layer/map-contour-layer"
import { TimeSlider } from "src/app/geo-web/map/map-layer/time-slider"
import { projectFeatureCollection } from "../../../../../../shared/src/util/geospatial-projections"
import { ResponseType } from "src/app/services/rest-api.service"
import { createWindChart } from "./wind-chart"
import { FlashMessage } from "../../../../../../shared/src/message-api"
import { isValidDirectoryTree } from "../../../../../../shared/src/file-provider/interfaces"

export class SparkWorkspaceClientJob extends sparkWorkspaceJob(ClientJob) {
  public inputFormModel = sparkFormRootModel

  public mapboxStyle = "mapbox://styles/mapbox/streets-v11"

  private arrivalTimeLayer: RasterLayer
  private startSourcesLayer: EditableFeatureCollectionLayer
  private maskSourcesLayer: EditableFeatureCollectionLayer

  async createInputLayers() {
    Object.keys(this.inputs.featureCollections)
      .filter(fc => typeof fc === "undefined")
      .forEach(key => {
        this.inputs.featureCollections[key] = featureCollection([])
      })
    this.startSourcesLayer = new EditableFeatureCollectionLayer(
      this.name,
      "Input fire sources",
      this.inputs.featureCollections.startSources,
      new SolidColourScheme("#ff0000")
    )

    // this.startSourcesLayer.shapeOptions = { color: "#ff0000" }

    // this.startSourcesLayer.pointIconProperties = {
    //   iconSize: [30, 30],
    //   iconAnchor: [15, 15],
    //   iconUrl: "assets/img/aahs-symbols/fire/AAHS_FIRE_OP_SPOT_FIRE_CONF.png",
    // }

    this.startSourcesLayer.toolbarButtonOptions.point = {
      title: "Add fire source",
      primaryFaIcon: ["fas", "fire"],
      secondaryFaIcon: ["fas", "map-pin"],
    }

    this.startSourcesLayer.toolbarButtonOptions.polygon = {
      title: "Add fire polygon",
      primaryFaIcon: ["fas", "fire"],
      secondaryFaIcon: ["fas", "shapes"],
    }

    // This function is called when a new feature is created
    const getCurrentJobStartTime = (() => {
      return this.inputs.startTime
    }).bind(this)

    this.startSourcesLayer.defaultFeatureProperties = {
      point: {
        time: getCurrentJobStartTime,
        radius: 0,
      },
      line: { time: getCurrentJobStartTime },
      polygon: { time: getCurrentJobStartTime },
    }

    this.startSourcesLayer.featurePropertiesFormModel = startSourceFormGroups

    this.startSourcesLayer.featureCollectionObservable.subscribe(
      this.checkFeatures.bind(this)
    )

    this.maskSourcesLayer = new EditableFeatureCollectionLayer(
      this.name,
      "Input mask polygons",
      this.inputs.featureCollections.masks,
      new SolidColourScheme("#000000")
    )

    this.maskSourcesLayer.toolbarButtonOptions.polygon = {
      title: "Add mask polygon",
      primaryFaIcon: ["fas", "crop"],
    }

    this.maskSourcesLayer.defaultFeatureProperties.polygon = {
      time: getCurrentJobStartTime,
    }

    this.maskSourcesLayer.featurePropertiesFormModel = maskSourceFormGroups

    await this.startSourcesLayer.show()
    await this.maskSourcesLayer.show()

    if (isValidDirectoryTree(this.inputs.inputs.series.file)) {
      try {
        const metCsv = await this.layerService.getFile(
          await this.jobService.resolveDirTree(
            this.inputs.inputs.series.file.path,
            this.inputs.inputs.series.file.location
          ),
          ResponseType.Text
        )

        createWindChart(this.layerService, metCsv)
      } catch (error) {
        console.log(`error generated met CSV chart:`)
        console.log(error)
      }
    }

    return [this.startSourcesLayer, this.maskSourcesLayer]
  }

  async updateInputLayers() {
    this.startSourcesLayer.featureCollection = this.inputs.featureCollections.startSources
    this.maskSourcesLayer.featureCollection = this.inputs.featureCollections.masks
  }

  async createAdditionalLayers(): Promise<MapLayer[]> {
    return []
  }

  async createOutputLayers(): Promise<MapLayer[]> {
    if (this.numberOfSubjobs === 0) {
      const geotiffPath = await this.jobService.resolveDirTree(
        `${this.outputDirectory}${this.inputs.outputDataRasterFilename}`,
        this.outputStorageMode
      )

      const layers = await this.layerService.geotiffLayerFactory([
        {
          geotiffPath,
          layerSpecs: {
            0: {
              name: "Arrival time (raster)",
              valueFormat: 'secondsToHMString("hm")',
              hideLayer: true,
            },
            1: {
              name: "Speed",
              units: "m/s",
              minValue: 0,
            },
            2: {
              name: "Maximum intensity",
              units: "kW/m",
              minValue: 0,
            },
            3: {
              name: "Maximum flame height",
              units: "m",
              minValue: 0,
            },
            4: {
              name: "Elevation",
              units: "m",
            },
            5: {
              name: "Fuel age",
              units: "years",
              minValue: 0,
            },
            6: {
              name: "Land classification",
              valueFormat: {
                0: "Un-burnable",
                1: "Dry eucalypt - Vesta",
                2: "Grasslands",
                3: "Heathland",
                4: "Semi-arid mallee heath",
                5: "Spinifex grassland",
                6: "Pine",
                7: "Dry eucalypt - Leaflet 80",
                8: "Urban",
              },
              legendCells: [0, 1, 2, 3, 4, 5, 6, 7, 8],
              minValue: 0,
              maxValue: 8,
            },
            8: {
              name: "Wind x-velocity",
              units: "m/s",
            },
            9: {
              name: "Wind y-velocity",
              units: "m/s",
            },
          },
        },
      ])

      this.arrivalTimeLayer = layers[0] as RasterLayer
      await this.arrivalTimeLayer.init()

      const arrivalTimeContourLayer = new MapContourLayer(
        "Arrival time contour",
        this.arrivalTimeLayer,
        "line"
      )

      arrivalTimeContourLayer.opacity = 100

      const arrivalTimeSlider = new TimeSlider()
      arrivalTimeSlider.min = 1
      arrivalTimeSlider.minDate = this.inputs.startTime
      arrivalTimeSlider.max = this.arrivalTimeLayer.maxValue
      arrivalTimeSlider.maxDate = this.inputs.endTime
      arrivalTimeSlider.value = 1
      arrivalTimeSlider.step = 60

      arrivalTimeContourLayer.slider = arrivalTimeSlider
      arrivalTimeContourLayer.sliderEnabled = true

      const arrivalTimeIsochronesLayer = new MapContourLayer(
        "Arrival time",
        this.arrivalTimeLayer,
        "fill"
      )

      arrivalTimeIsochronesLayer.valueFormatFn = secondsToHMString(":")

      arrivalTimeIsochronesLayer.transitionDelayMultiplier = 1
      arrivalTimeIsochronesLayer.colourScheme = new D3ColourScheme()
      arrivalTimeIsochronesLayer.opacity = 100

      // Generate thresholds for isochrones
      arrivalTimeIsochronesLayer.thresholds = []

      // Choose delta_time (seconds) based to get approximately 10 isochrones
      // 1 minute, 5m, 10m, 30m, 1hour, 2h, 3h, 4h, 5h
      const deltaTimes = [
        60,
        5 * 60,
        10 * 60,
        30 * 60,
        60 * 60,
        2 * 60 * 60,
        3 * 60 * 60,
        4 * 60 * 60,
        5 * 60 * 60,
      ]

      // Find the dt which results in closest to 10 isochrones
      const diff = deltaTimes.map(dt =>
        Math.abs(10 - this.arrivalTimeLayer.maxValue / dt)
      )
      const dt = deltaTimes[diff.indexOf(Math.min(...diff))]

      for (let i = 0; i < this.arrivalTimeLayer.maxValue; i += dt) {
        arrivalTimeIsochronesLayer.thresholds.push(i)
      }
      arrivalTimeIsochronesLayer.thresholds.push(this.arrivalTimeLayer.maxValue)

      arrivalTimeIsochronesLayer.downloadData.metadata = `Arrival time isochrones.
  Each polyon represents the fire boundary at a given time - where the value property is the time in seconds.
  Polygons are generated for the following times (after the simulation start time: ${this.inputs.startTime.toString()}):
  ${arrivalTimeIsochronesLayer.thresholds
    .map(secondsToHMString("hm"))
    .join("\n")}`

      await Promise.all([
        arrivalTimeContourLayer.show(),
        arrivalTimeIsochronesLayer.show(),
      ])

      return [
        arrivalTimeContourLayer,
        arrivalTimeIsochronesLayer,
        // windVectorFieldLayer,
        ...layers,
      ]
    }

    return []
  }

  setBbox() {
    // Add all input features so we can set map extent
    const featuresToFitMapTo = []

    if (this.inputs.startSourcesProjection !== "EPSG:4326") {
      featuresToFitMapTo.push(
        ...projectFeatureCollection(
          JSON.parse(
            JSON.stringify(this.inputs.featureCollections.startSources)
          ),
          this.inputs.startSourcesProjection,
          "EPSG:4326"
        ).features
      )
    } else {
      featuresToFitMapTo.push(
        ...this.inputs.featureCollections.startSources.features
      )
    }

    featuresToFitMapTo.push(...this.inputs.featureCollections.masks.features)

    // Create bounding box based on input features
    if (featuresToFitMapTo.length > 0) {
      this.boundingBox4326 = bbox(featureCollection(featuresToFitMapTo))
    }

    if (
      typeof this.arrivalTimeLayer !== "undefined" &&
      typeof this.arrivalTimeLayer.getBbox() !== "undefined"
    ) {
      const geotiffBbox = this.arrivalTimeLayer.getBbox()
      this.boundingBox4326.forEach((coord, index) => {
        if (geotiffBbox[index] < coord * (-1 ^ Math.floor(index / 2))) {
          this.boundingBox4326[index] = geotiffBbox[index]
        }
      })
    }
  }

  public canJobRun() {
    return this.checkFeatures(this.startSourcesLayer.featureCollection)
  }

  private checkFeatures(fc: FeatureCollection) {
    if (fc.features.length > 10) {
      this.layerService.flashMessageService.pushFlashMessage(
        new FlashMessage({
          title: `Too many input features`,
          message: `There are too many input features, please delete some before running the job`,
          severity: "error",
        })
      )

      return false
    }

    const fcBboxArea = area(bboxPolygon(bbox(fc)))
    const fcArea = area(fc)

    if (fcArea > 10000000) {
      this.layerService.flashMessageService.pushFlashMessage(
        new FlashMessage({
          title: `Features are too large`,
          message: `The input features are too large, please delete or reduce the size before running the job`,
          severity: "error",
        })
      )

      return false
    }

    if (fcBboxArea > 25000000) {
      this.layerService.flashMessageService.pushFlashMessage(
        new FlashMessage({
          title: `Features are too sparse`,
          message: `The input features cover too large an area, please reduce before running the job`,
          severity: "error",
        })
      )

      return false
    }

    return true
  }
}
