import { MapboxGlLayer } from "./mapbox-gl-layer"
import { CanvasSourceRaw, CanvasSource, RasterPaint } from "mapbox-gl"
import { MapLayer } from "./map-layer"
import { Selection, select } from "d3"

export abstract class CanvasOverlayLayer extends MapboxGlLayer {
  public static isInstanceOf(obj: any): obj is CanvasOverlayLayer {
    return "rootElement" in obj && "render" in obj
  }

  protected _rendered = false
  protected _rendering = false

  protected _rootElement: Selection<any, any, any, any>
  protected _canvasContext: CanvasRenderingContext2D

  protected _mapboxId: string

  protected _source: { id: string; source: CanvasSourceRaw }

  get rootElement() {
    return this._rootElement
  }

  constructor(name: string) {
    super(name)

    this._rootElement = select("body").append("canvas")
    this._mapboxId = `${this.type}-${name}`.replace(" ", "-").toLowerCase()
    this._rootElement.attr("id", this._mapboxId)
    this._rootElement.style("display", "none")

    this._canvasContext = this._rootElement.node().getContext("2d")

    // this._canvasContext.globalCompositeOperation = "lighten"
    this._canvasContext.imageSmoothingEnabled = false
  }

  abstract getBbox(): [number, number, number, number]

  public async render(force = false) {
    const bbox = this.getBbox()

    if (typeof bbox === "undefined") {
      return
    }

    if (
      typeof this._mapboxGlLayer === "undefined" ||
      typeof this._source === "undefined"
    ) {
      this._mapboxGlLayer = {
        id: this._mapboxId,
        type: "raster",
        source: this._mapboxId,
        paint: {
          "raster-fade-duration": 0,
          "raster-resampling": "nearest",
        } as RasterPaint,
      }

      this._source = {
        id: this._mapboxId,
        source: {
          type: "canvas",
          canvas: this._mapboxId,
          coordinates: [
            [bbox[0], bbox[3]],
            [bbox[2], bbox[3]],
            [bbox[2], bbox[1]],
            [bbox[0], bbox[1]],
          ],
        },
      }
    } else if (
      this._source.source.coordinates[0][0] !== bbox[0] ||
      this._source.source.coordinates[0][1] !== bbox[3] ||
      this._source.source.coordinates[1][0] !== bbox[2] ||
      this._source.source.coordinates[3][1] !== bbox[1]
    ) {
      this._source.source.coordinates = [
        [bbox[0], bbox[3]],
        [bbox[2], bbox[3]],
        [bbox[2], bbox[1]],
        [bbox[0], bbox[1]],
      ]
      ;(MapLayer.layerService.mapboxGl.getSource(
        this._mapboxId
      ) as CanvasSource).setCoordinates(this._source.source.coordinates)
      console.log("set coords")
    }

    await super.render(force)
  }

  public async clear() {
    if (!this._rendering) {
      super.clear()
      this._rendered = false
    }
    // TODO: if rendering -> clear after render
  }

  public async delete() {
    super.delete()
    if (
      typeof this._rootElement !== "undefined" &&
      typeof this._rootElement.remove === "function"
    ) {
      this._rootElement.remove()
    }
    // TODO: if rendering -> delete after render
  }
}
