import { Component, OnInit, Input } from "@angular/core"
import {
  UploadFile,
  UploadEvent,
  FileSystemFileEntry,
  FileSystemDirectoryEntry,
} from "ngx-file-drop"
import {
  SpinnerService,
  SpinnerIcon,
  SpinnerSize,
} from "../spinner/spinner-service.service"
import { RestApiService } from "../services/rest-api.service"
import { JobService } from "../services/job-service.service"
import { FlashMessage } from "../../../../shared/src/message-api"
import { FlashMessageService } from "../services/flash-message.service"

/**
 * This component handles uploading files when they are dropped on the screen
 *
 * @export
 * @class FileDropUploadComponent
 */
@Component({
  selector: "app-file-drop-upload",
  templateUrl: "./file-drop-upload.component.html",
  styleUrls: ["./file-drop-upload.component.scss"],
})
export class FileDropUploadComponent implements OnInit {
  @Input() dropZoneClassName: string

  public state: "over" | "none" | "uploading" = "none"

  constructor(
    private spinnerService: SpinnerService,
    private restApiService: RestApiService,
    private jobService: JobService,
    private flashMessageService: FlashMessageService
  ) {}

  ngOnInit() {}

  public files: UploadFile[] = []

  public dropped(event: UploadEvent) {
    this.spinnerService.removeSpinner(`file-drop-upload-over`)
    this.state = "uploading"
    this.files = event.files

    const currentJob = this.jobService.getCurrentJob()

    if (
      typeof currentJob === "undefined" ||
      typeof currentJob.inputDirectory !== "string"
    ) {
      this.flashMessageService.pushFlashMessage(
        new FlashMessage({
          title: `Failed to upload files: no job is loaded`,
          message: "Please load a job before trying to upload files",
          sticky: true,
        })
      )
      return
    }

    for (const droppedFile of event.files) {
      // Is it a file?
      if (droppedFile.fileEntry.isFile) {
        this.spinnerService.setSpinner(
          `file-drop-upload-uploading-${droppedFile.fileEntry.name}`,
          {
            name: `Uploading file ${droppedFile.fileEntry.name}`,
            icon: SpinnerIcon.RotatingCircle,
            progress: 0,
            target: "file-drop-upload",
          }
        )
        const fileEntry = droppedFile.fileEntry as FileSystemFileEntry
        fileEntry.file(async (file: File) => {
          try {
            await this.restApiService.uploadJobFile(
              file,
              `${currentJob.inputDirectory}${droppedFile.relativePath}`,
              currentJob.name,
              progress => {
                this.spinnerService.updateSpinner(
                  `file-drop-upload-uploading-${droppedFile.fileEntry.name}`,
                  {
                    progress,
                  }
                )
              }
            )

            this.flashMessageService.pushFlashMessage(
              new FlashMessage({
                title: `Sucessfull upload`,
                message: `Sucessfully uploaded file ${file.name}`,
                severity: "success",
              })
            )
          } catch (error) {
            this.flashMessageService.pushFlashMessage(
              new FlashMessage({
                title: `Failed to upload file ${file.name}`,
                message: "message" in error ? error.message : error,
                sticky: true,
              })
            )
          } finally {
            this.spinnerService.removeSpinner(
              `file-drop-upload-uploading-${droppedFile.fileEntry.name}`
            )
          }
        })
      } else {
        // It was a directory (empty directories are added, otherwise only files)
        const fileEntry = droppedFile.fileEntry as FileSystemDirectoryEntry
      }
    }
  }

  public fileOver(event) {
    this.state = "over"

    this.spinnerService.setSpinner(`file-drop-upload-over`, {
      name: `Drop to load files...`,
      icon: SpinnerIcon.BallScale,
      target: "file-drop-upload",
    })
  }

  public fileLeave(event) {
    this.spinnerService.removeSpinner(`file-drop-upload-over`)
    this.state = "none"
  }
}
