import {
  ChangeDetectionStrategy,
  Component,
  EventEmitter,
  Input,
  Output,
  ViewChild,
  OnInit,
  ViewEncapsulation,
  AfterViewInit,
  OnDestroy,
} from "@angular/core"
import { FormGroup } from "@angular/forms"
import {
  DynamicFormControlCustomEvent,
  DynamicFormLayout,
  DynamicFormLayoutService,
  DynamicFormValidationService,
  DynamicFormControlComponent,
} from "@ng-dynamic-forms/core"
import { EditorComponent } from "ngx-monaco-editor"
import { DynamicCodeEditorModel } from "./code-editor-model"

import { Subscription } from "rxjs"
import {
  SpinnerService,
  SpinnerIcon,
} from "src/app/spinner/spinner-service.service"

/**
 * DynamicFormControlComponent for the Monaco code editor.
 *
 * @export
 * @class DynamicCodeEditorComponent
 */
@Component({
  selector: "app-form-monaco-editor",
  templateUrl: "./form-monaco-editor.component.html",
  styleUrls: ["./form-monaco-editor.component.scss"],
  changeDetection: ChangeDetectionStrategy.OnPush,
  encapsulation: ViewEncapsulation.None,
})
export class DynamicCodeEditorComponent extends DynamicFormControlComponent
  implements AfterViewInit, OnInit, OnDestroy {
  @Input() group: FormGroup
  @Input() layout: DynamicFormLayout
  @Input() model: DynamicCodeEditorModel

  @Output() blur: EventEmitter<any> = new EventEmitter()
  @Output() change: EventEmitter<any> = new EventEmitter()
  @Output() customEvent: EventEmitter<
    DynamicFormControlCustomEvent
  > = new EventEmitter()
  @Output() focus: EventEmitter<any> = new EventEmitter()

  @ViewChild("codeEditor") codeEditor: EditorComponent

  private codeEditorInitSubscription: Subscription

  constructor(
    protected layoutService: DynamicFormLayoutService,
    protected validationService: DynamicFormValidationService,
    private spinnerService: SpinnerService
  ) {
    super(layoutService, validationService)
  }

  ngOnInit(): void {
    this.spinnerService.setSpinner("dynamic-code-editor-component-loading", {
      target: "dynamic-code-editor-component",
      icon: SpinnerIcon.Ball8Bit,
      name: "Loading editor..",
    })
  }

  ngAfterViewInit(): void {
    this.codeEditorInitSubscription = this.codeEditor.onInit.subscribe(() => {
      this.spinnerService.removeSpinner("dynamic-code-editor-component-loading")
    })
  }

  ngOnDestroy(): void {
    this.codeEditorInitSubscription.unsubscribe()
  }
}
