import {
  Component,
  OnDestroy,
  ViewChild,
  ElementRef,
  Input,
  Output,
  EventEmitter,
  ChangeDetectorRef,
  OnInit,
  NgZone,
  ViewEncapsulation,
} from "@angular/core"
import { PopupFormService } from "./map-popup.service"
import { Subject, Subscription } from "rxjs"
import { DynamicFormModel, DynamicFormService } from "@ng-dynamic-forms/core"
import { FormGroup } from "@angular/forms"

export interface ShowPopupMessage {
  title: String
  coordinates: { x: number; y: number }
  model?: any
  formSchema?: DynamicFormModel
  dangerBtn?: { label: string; onClickFn: () => void }
  formOnSubmitFn?: (value: any) => void
  formOnCancelFn?: () => void
  background: boolean
  closeOnMapMove?: boolean
  hideCancelBtn?: boolean
  cancelBtnLabel?: string
  submitBtnLabel?: string
}

@Component({
  selector: "app-map-popup",
  templateUrl: "./map-popup.component.html",
  styleUrls: ["./map-popup.component.scss"],
  encapsulation: ViewEncapsulation.None,
})
export class PopupFormComponent implements OnDestroy {
  @ViewChild("popupContainer") mainMenu: ElementRef

  public currentPopup: ShowPopupMessage

  private _visible: boolean

  public formModel: DynamicFormModel
  public formGroup: FormGroup

  // The position of the popup relative to the cooridnates
  public position: "top" | "bottom"
  // Amount of X pixels to translate the caret (so it aligns with the original coordinates)
  public caretTranslateX = 0

  private _popupSubject: Subscription

  set visible(v: boolean) {
    this._visible = v
  }

  get visible() {
    return this._visible
  }

  constructor(
    private popupService: PopupFormService,
    private formService: DynamicFormService
  ) {
    this._popupSubject = popupService
      .getPopupSubject()
      .subscribe(popupMessage => this.updatePopup(popupMessage))
  }

  ngOnDestroy(): void {
    this._popupSubject.unsubscribe()
  }

  updatePopup(popupMessage: ShowPopupMessage) {
    if (typeof popupMessage === "undefined") {
      this._visible = false
    } else {
      this.currentPopup = popupMessage

      // Position the popup above or below the cformOnCancelFnoordinates if y-value is above/below middle of screen
      this.position =
        this.currentPopup.coordinates.y >
        Math.max(
          document.documentElement.clientHeight,
          window.innerHeight || 0
        ) /
          2
          ? "top"
          : "bottom"

      // Update coordinates and caret (triangle) position if they are outidse the bounds (160px, 100%-160px)
      const minX = 160
      const maxX =
        Math.max(document.documentElement.clientWidth, window.innerWidth || 0) -
        minX

      this.caretTranslateX = 0
      if (this.currentPopup.coordinates.x < minX) {
        this.caretTranslateX = Math.min(
          minX - this.currentPopup.coordinates.x,
          135
        )
        this.currentPopup.coordinates.x = minX
      } else if (this.currentPopup.coordinates.x > maxX) {
        this.caretTranslateX = Math.max(
          maxX - this.currentPopup.coordinates.x,
          -135
        )
        this.currentPopup.coordinates.x = maxX
      }

      // If the form model/schema has changed -> create new form
      if (this.formModel !== popupMessage.formSchema) {
        this.formModel = popupMessage.formSchema

        if (typeof this.formModel !== "undefined") {
          this.formGroup = this.formService.createFormGroup(this.formModel)
        }
      }

      if (
        typeof this.formGroup !== "undefined" &&
        typeof this.currentPopup.model !== "undefined"
      ) {
        // Must be patchValue and not setValue - as setValue will throw an error if the model contains keys which don't exist in the form
        this.formGroup.patchValue(this.currentPopup.model)
      }

      this._visible = true
    }
  }

  save() {
    if (typeof this.currentPopup.formOnSubmitFn === "function") {
      this.currentPopup.formOnSubmitFn(
        typeof this.formGroup !== "undefined" ? this.formGroup.value : undefined
      )
    }

    this._visible = false
    this.currentPopup = undefined
  }

  cancel() {
    if (typeof this.currentPopup.formOnCancelFn === "function") {
      this.currentPopup.formOnCancelFn()
    }

    this._visible = false
    this.currentPopup = undefined
  }

  dangerBtnClick() {
    if (
      typeof this.currentPopup.dangerBtn !== "undefined" &&
      typeof this.currentPopup.dangerBtn.onClickFn === "function"
    ) {
      this.currentPopup.dangerBtn.onClickFn()
      this.cancel()
    }
  }

  onEnterKeyPress = e => {
    if (e.key === "Enter") {
      this.save()
      e.srcElement.blur()
    }
  }
}
