import { DynamicInputModel } from "@ng-dynamic-forms/core"
import { FormRootModel } from "../form/form-interfaces"
import { GeowebUserRoles } from "../../../../shared/src/user/user"

export enum ServerConfigFormModeKey {
  Basic = "basic",
}

export const serverConfigForm = new FormRootModel()

serverConfigForm.modes = [
  {
    key: ServerConfigFormModeKey.Basic,
    name: "Basic",
    userRoles: [GeowebUserRoles.Admin],
  },
]

serverConfigForm.defaultMode = serverConfigForm.modes[0]

// This model matches ServerPublicConfigJSON
serverConfigForm.schemaFactory = [
  {
    name: "Server Config",
    modeKeys: [ServerConfigFormModeKey.Basic],
    form: {
      model: [
        new DynamicInputModel({
          id: "JOB_DIR",
          label: "Job directory",
          validators: {
            required: null,
          },
          errorMessages: {
            required: "{{ label }} is required",
          },
        }),
        new DynamicInputModel({
          id: "JOB_TEMPLATE_DIR",
          label: "Job template directory",
        }),
        new DynamicInputModel({
          id: "PRIVATE_FILES_DIR",
          label:
            "Job template library directories (comma-delimited with no spaces)",
        }),
        new DynamicInputModel({
          id: "SPARK_PATH",
          label: "Geoweb path",
          validators: {
            required: null,
          },
          errorMessages: {
            required: "{{ label }} is required",
          },
        }),
        new DynamicInputModel({
          id: "EMV2_PATH",
          label: "EMV2 path",
          validators: {
            required: null,
          },
          errorMessages: {
            required: "{{ label }} is required",
          },
        }),
        new DynamicInputModel({
          id: "OPENCL_PLATFORM_ID",
          label: "OpenCL Platform ID",
        }),
      ],
    },
  },
]
