import { FormGroup } from "@angular/forms"
import { DynamicFormLayout, DynamicFormModel } from "@ng-dynamic-forms/core"
import { Subscription } from "rxjs"
import { GeowebUserRoles } from "../../../../shared/src/user/user"
import { ConfigService } from "../services/config.service"

/**
 *The following class serves as the root object for a Form
It contains a schemaFactory which generates an array of FormSchemas (IFormSchema),
it contains the Form's possible modes (which can be used to filter IFormSchema and FormControls within the IFormSchema)
and the current form mode.

An instance of this class is passed to the <app-form> component
 *
 * @export
 * @class FormRootModel
 */
export class FormRootModel {
  /**
   * This generates an array of IFormSchema based on the current form mode and configService (which contains environment vars)
   *
   */
  schemaFactory:
    | ((formMode: IFormMode, configService: ConfigService) => IFormSchema[])
    | IFormSchema[]
  modes: IFormMode[]
  defaultMode: IFormMode

  /**
   * If this is set, then the subject used for getting/setting values starts at the given key (that is subject[subjectPropertyKey] instead of subject)
   *
   */
  subjectPropertyKey?: string

  constructor(obj: Partial<FormRootModel> = {}) {
    Object.assign(this, obj)
  }
}

/**
 * Form modes allow the same form model to change for a given mode - This is rendered as a dropdown list
 *
 * @export
 * @interface IFormMode
 */
export interface IFormMode {
  key: string
  /**
   *Label of form mode
   *
   */
  name: string
  /**
   *Which user roles have permission to use the given FormMode
   *
   */
  userRoles: GeowebUserRoles[]
}

/**
 * Each IFormSchema represents a Form Node in the tree of Forms
 * The form field is either an array of more form children (IFormSchema)
 * or it is an instance of a form (IFormSchemaInstance)
 *
 * @export
 * @interface IFormSchema
 */
export interface IFormSchema {
  name: string
  modeKeys: string[]
  form: IFormSchemaInstance | IFormSchema[]
}

/**
 * This contains needed DynamicForm variables - it is wrapped up in IFormSchema
 *
 * @export
 * @interface IFormSchemaInstance
 */
export interface IFormSchemaInstance {
  model: DynamicFormModel

  layout?: DynamicFormLayout

  formGroup?: FormGroup

  /**
   * subscription of {@link FormGroup}.valueChanges
   */
  valueChangeSub?: Subscription
  /**
   * Indicates whether currently saving form values (used to prevent recursion between saving form values -> and updating form values -> saving form values...).
   * This is due to some form components immediately broadcasing valueChanges after the values have been set programatically
   */
  savingValuesChanges?: boolean

  styleClass?: string
}
